import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TasksComponent } from './tasks/tasks.component';
import { TaskSummaryComponent } from './task-summary/task-summary.component';
import { KycTaskSummaryComponent } from './kyc-task-summary/kyc-task-summary.component';
import { CustOnBoardingTaskSummaryComponent } from './cust-on-boarding-task-summary/cust-on-boarding-task-summary.component';
import { CustOnBoardingApproverComponent } from './cust-on-boarding-approver/cust-on-boarding-approver.component';
import { CorporateTaskSummaryComponent } from './corporate-task-summary/corporate-task-summary.component';
import { CorporateApproverComponent } from './corporate-approver/corporate-approver.component';
import { CardOriginationTaskSummaryComponent } from './card-origination-task-summary/card-origination-task-summary.component';
import { CardAccountApproverComponent } from './card-account-approver/card-account-approver.component';
import { LoanAccountTaskSummaryComponent } from './loan-account-task-summary/loan-account-task-summary.component';
import { LoanAccountDetailsUpdateComponent } from './loan-account-approver/loan-account-details-update/loan-account-details-update.component';
export const TaskRoutes: Routes = [
  {
      path: '',
    // component: TasksComponent,
  },
  {
    path: 'task-summary',
    component: TaskSummaryComponent,
  },

  {
    path: 'cust-on-boarding-task-summary',
    component: CustOnBoardingTaskSummaryComponent,
  },
  {
    path: 'kyc-task-summary',
    component: KycTaskSummaryComponent,
  },
  {
    path: 'cust-on-boarding-approver',
    component: CustOnBoardingApproverComponent,
  },
  {
    path: 'corporate-task-summary',
    component: CorporateTaskSummaryComponent,
  },
  {
    path: 'loan-account-task-summary',
    component: LoanAccountTaskSummaryComponent,
  },
   {
    path: 'corporate-approver',
    component: CorporateApproverComponent,
  },
  {
    path:'card-account-task-summary',
    component: CardOriginationTaskSummaryComponent,
  },
  {
    path:'card-account-approver',
    component: CardAccountApproverComponent,
  },
  {
    path: 'loan-account-task-summary/loan-account-approver',
    component: LoanAccountDetailsUpdateComponent,
  }

]
