import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-update-contact-info',
  templateUrl: './update-contact-info.component.html',
  styleUrls: ['./update-contact-info.component.scss']
})
export class UpdateContactInfoComponent implements OnInit {
  contactInfoUpdateForm: FormGroup;
  customerId: any;
  userAddress_array = new FormArray([]);
  arrayCity: string[] = [];
  arrayState: any[] = [];
  entityName: string;
  arrayCityPerm: string[] = [];
  arrayStatePerm: any[] = [];
  arrayCountryPerm: any[] = [];
  arrayCountry: any[] = [];
  zipCodeIndex: any;
  isAllSameDigitVal: boolean;
  arrayResidence: string[];
  constructor(
    private dialogRef: MatDialogRef<UpdateContactInfoComponent>,
    @Inject(MAT_DIALOG_DATA) public contactInfo,
    private formBuilder: FormBuilder,
    private ls: LocalStoreService,
    private apiService: ApiService,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    console.log(this.contactInfo);
    this.customerId = this.ls.getItem('customerId')
    this.getResidenceDetails("residence")
    this.getCountryName("country");
    if (this.contactInfo) {
      this.buildContactInfoForm()
      this.userAddress_array = this.contactInfoUpdateForm.get('userAddress_array') as FormArray;
      for (let i = 0; i < this.contactInfo.length; i++) {
        this.getStateName(this.contactInfo[i].country, this.contactInfo[i].addressType)
        this.getCityName()
        this.userAddress_array.push(this.createUserAddress(this.contactInfo[i]))
      }
    }
    else {
      this.buildContactInfoForm()
      this.userAddress_array.push(this.createUserAddress())
    }

  }
  buildContactInfoForm() {
    this.contactInfoUpdateForm = this.formBuilder.group({
      userAddress_array: this.formBuilder.array([])
    });
  }
  createUserAddress(item?): FormGroup {
    return this.formBuilder.group({
      addressType: ["Communication", [Validators.required]],
      address1: [item ? item.address1 : [''], [Validators.pattern(".*[a-zA-Z].*")]],
      address2: [item ? item.address2 : [''], [Validators.pattern(".*[a-zA-Z].*")]],
      city: item ? item.city : [''],
      state: item ? item.state : [''],
      zipCode: item ? item.zipCode : [''],
      country: item ? item.country : [''],
      residenceType: item ? item.residenceType : ['']
    })
  }
  getResidenceDetails(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayResidence = data;
    });
  }
  getCountryName(key: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].authStatus === 'A') {
          this.arrayCountry.push(data[i].countryName);
          this.arrayCountryPerm.push(data[i].countryName);
        }
      }
    });
  }

  getStateName(country: string, addressType) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].countryName == country) {
          this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
            if (addressType === 'Communication') {
              this.arrayState = resp;
            } else if (addressType === 'Permanent') {
              this.arrayStatePerm = resp;
            }
          })
          break;
        }
      }
    })
  }

  getCityName() {
    // this.entityName = "city";
    // console.log(state)
    // this.apiService.getDropDownForStateCity(this.entityName, state).subscribe((data) => {
    //   if (addressType === 'Communication') {
    //     this.arrayCity = data;
    //     console.log("-->", this.arrayCity)
    //   } else if (addressType === 'Permanent') {
    //     this.arrayCityPerm = data;
    //   }

    // });
    this.apiService.getAuthorizedCityInfo().subscribe(res => res.map(r => {
      this.arrayCity.push(r.cityName);
    }));
  }
  onSubmit() {
    var obj: any = {}
    obj.customerId = this.customerId;
    obj.userAddress = this.userAddress_array.value;
    if (obj.userAddress && obj.userAddress.length == 2) {
      obj.userAddress[1].addressType = 'Permanent';
    }
    this.apiService.insertNewUserOnboarding(obj).subscribe((result) => {
      this.dialogRef.close({
        status: 'INITIATED',
        resp: obj.userAddress
      })
    }, err => {
      this.snack.open("Server Error!", "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }
  isAllSameDigit(event, zipIndex) {
    console.log('zipIndex :: ', zipIndex);
    this.zipCodeIndex = zipIndex;
    this.cdr.detectChanges();
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      console.log('number :: ', number);
      console.log('isAllSameDigitVal :: ', count, number.length)
      if (count == number.length) {
        console.log('if loop')
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    } else {
      this.isAllSameDigitVal = false;
    }
  }

  close() {
    this.dialogRef.close({
      status: 'UNDER_PROCESS'
    })
  }
}
