import { Component, OnInit , Inject, ChangeDetectorRef} from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-loan-offer-issue-details-update',
  templateUrl: './loan-offer-issue-details-update.component.html',
  styleUrls: ['./loan-offer-issue-details-update.component.scss']
})
export class LoanOfferIssueDetailsUpdateComponent implements OnInit {
  loanAccountId: any;
  loanaccountID: any;
  loanOfferIssueForm: FormGroup;
  disbursementDetails: any;
  offerIssueDetails: any;

  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0
  constructor(
    private apiService: ApiService, @Inject(MAT_DIALOG_DATA) public data: any, private fb: FormBuilder,
    private loader: AppLoaderService,private taskSumService: TasksummaryService, private cdr: ChangeDetectorRef,
    private ls: LocalStoreService, public dialogRef: MatDialogRef<LoanOfferIssueDetailsUpdateComponent>, private snack: MatSnackBar
  ) { }

  ngOnInit(): void {
    this.loanaccountID = this.data.loanAccountId;
    console.log('loanaccountID', this.loanaccountID)
    this.buildLoanOfferIssueForm(this.data)
  }
  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
   buildLoanOfferIssueForm(data?) {
    console.log('item',data)
   
     
    this.loanOfferIssueForm= this.fb.group({
        loanAccountId: [this.loanaccountID],
        applicantName:[data ? data?.loanOfferIssueInfo?.applicantName:''],
        interest: [data ? data?.loanOfferIssueInfo?.interest : ''],
        rateOfInterest: [data ? data?.loanOfferIssueInfo?.rateOfInterest : ''],
        principal:[data ? data?.loanOfferIssueInfo?.principal:''],
        installmentType: [data ? data?.loanOfferIssueInfo?.installmentType: ''],
        installmentFrequency:[data ? data?.loanOfferIssueInfo?.installmentFrequency:''],
        approvedLoanAmount:[data ? data?.loanOfferIssueInfo?.approvedLoanAmount:''],
        generateOffer: [data ? data?.loanOfferIssueInfo?.generateOffer : ''],
        charges: [data ? data?.loanOfferIssueInfo?.charges : ''],
        loanTenure: [data ? data?.loanOfferIssueInfo?.loanTenure: ''],
        
 
    });
  }
  getLoanOfferIssue() {
 
    this.apiService.getOfferIssueDetails(this.loanaccountID).subscribe((offerIssueDetails) => {
 
      this.offerIssueDetails = offerIssueDetails;
      console.log("this.offerIssueDetails::", this.offerIssueDetails);
    

      this.buildLoanOfferIssueForm(this.offerIssueDetails);
      this.loanOfferIssueForm.value.applicantName = this.offerIssueDetails.applicantName;
      this.loanOfferIssueForm.value.rateOfInterest = this.offerIssueDetails.rateOfInterest;
      this.loanOfferIssueForm.value.installmentType = this.offerIssueDetails.installmentType;
      this.loanOfferIssueForm.value.installmentFrequency = this.offerIssueDetails.installmentFrequency;
      this.loanOfferIssueForm.value.generateOffer = this.offerIssueDetails.generateOffer;
      this.loanOfferIssueForm.value.charges = this.offerIssueDetails.charges;
      this.loanOfferIssueForm.value.loanTenure = this.offerIssueDetails.loanTenure;
      this.loanOfferIssueForm.value.principal = this.offerIssueDetails.principal;
      this.loanOfferIssueForm.value.interest = this.offerIssueDetails.interest;
      this.loanOfferIssueForm.value.offerIssueDate = this.offerIssueDetails.offerIssueDate;
      this.loanOfferIssueForm.value.approvedLoanAmount = this.offerIssueDetails.approvedLoanAmount

    }, (err: any) => {
      this.loader.close();
    });
  }

  goToClose(): void {
    this.dialogRef.close();
    this.apiService.getOfferIssueDetails(this.loanaccountID).subscribe((offerIssueDetails: any) => {
      console.log("All Account Service", offerIssueDetails);
      this.offerIssueDetails = offerIssueDetails;
      //this.buildformGroup(this.loanAccountDetails);
      console.log("this.accountDetails", this.offerIssueDetails);
      console.log('form',this.loanOfferIssueForm.value)
    
   });
   
  }
  submitForm1(){
    
   

    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      applicantName: this.loanOfferIssueForm.value.applicantName,
      charges:this.loanOfferIssueForm.value.charges,
      rateOfInterest: this.loanOfferIssueForm.value.rateOfInterest,  
      installmentType:this.loanOfferIssueForm.value.installmentType,
      installmentFrequency:this.loanOfferIssueForm.value.installmentFrequency,
      generateOffer: this.loanOfferIssueForm.value.generateOffer,
      loanTenure: this.loanOfferIssueForm.value.loanTenure,
      approvedLoanAmount: this.loanOfferIssueForm.value.approvedLoanAmount,
      principal: this.loanOfferIssueForm.value.principal,
      interest: this.loanOfferIssueForm.value.interest,
      offerIssueDate: this.loanOfferIssueForm.value.offerIssueDate
  
    }
    console.log("payload",payload)
    this.apiService.upsertOfferIssue(payload).subscribe(data => {
    console.log("data",data.loanAccountId);
   

      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName);
   
        
    this.loanaccountID = this.data.loanAccountId;
    console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }
    
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
  }

}







