import { HttpEventType } from '@angular/common/http';
import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CustomerOnboardingMandateEsignComponent } from 'app/views/others/customer-onboarding-mandate-esign/customer-onboarding-mandate-esign.component';
import { CorporateOnboardingService } from 'app/views/others/onboarding-corporate/corporate-onboarding.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-update-mandate-info',
  templateUrl: './update-mandate-info.component.html',
  styleUrls: ['./update-mandate-info.component.scss']
})
export class UpdateMandateInfoComponent implements OnInit {
  signatureUpdateForm: FormGroup;
  corporateId: any;
  noOfSignSelect: any;
  values: any[] = [1, 2, 3, 4, 5];
  selectedFiles: any;
  signatureimg: any;
  listOfSignatures: any[] = [];
  enableButton: boolean;
  signIndex: any;
  sign_array: any;
  accountSignatureId: number;
  customerId: string;
  signCount: string;
  difference: number;
  show: any;
  uploadSub: Subscription;
  progressValOthers: number = 0;
  deleteRecord: any[] = [];


  constructor(
    private dialogRef: MatDialogRef<UpdateMandateInfoComponent>,
    private formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) private signatures,
    private apiService: ApiService,
    private corporateService: CorporateOnboardingService,
    private ls: LocalStoreService,
    private dialog: MatDialog,
    private dialogService: DialogService,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    this.ls.getItem('corporateId');
    if (this.signatures) {
      this.buildSignatureForm()
      this.noOfSignSelect = this.signatures.length
      this.signatureUpdateForm.get('noOfSignatures').setValue(this.signatures.length)
      for (let i = 0; i < this.signatures.length; i++) {
        (this.signatureUpdateForm.get('sign_array') as FormArray).push(this.createSignatureBoxes(this.signatures[i]))
      }
    }
  }
  buildSignatureForm(data?) {
    this.signatureUpdateForm = this.formBuilder.group({
      noOfSignatures: [data ? data.noOfSignatures : '', [Validators.required]],
      sign_array: this.formBuilder.array([])
    });
  }
  createSignatureBoxes(item): FormGroup {
    return this.formBuilder.group({
      accountSignatureId: item ? item.accountSignatureId : 0,
      imageURL1: [item ? item.fileUrl : 'assets/images/no-image.jpg',Validators.required],
      documentName: item ? item.documentName : "Signature",
      documentType: item ? item.documentType : '',
      fileType: item ? item.fileType : item.fileType,
      fileName: item ? item.fileName : item.fileName,
      verificationType: item ? item.verificationType : "New Customer Signature"
    }
    );
  }
  get getSignArray() {
    return this.signatureUpdateForm.get('sign_array') as FormArray;
  }
  changeValue(noOfSign) {
    var message = '';
    let arrlength;
    this.noOfSignSelect = noOfSign.value;
    this.enableButton = false;
    if (this.signatureUpdateForm.get('sign_array') as FormArray == undefined || this.getSignArray == null) {
      if ((this.signatureUpdateForm.get('sign_array') as FormArray).length != 0) {
        if (this.noOfSignSelect > (this.signatureUpdateForm.get('sign_array') as FormArray).length) {
          var newSignIndex = this.noOfSignSelect - (this.signatureUpdateForm.get('sign_array') as FormArray).length
          for (let index = 0; index < newSignIndex; index++) {
            (this.signatureUpdateForm.get('sign_array') as FormArray).push(this.createSignatureBoxes(''));
          }
        } else if (this.noOfSignSelect < (this.signatureUpdateForm.get('sign_array') as FormArray).length) {
          var newSignIndex = (this.signatureUpdateForm.get('sign_array') as FormArray).length - this.noOfSignSelect
          for (let index = 0; index < newSignIndex; index++) {
            (this.signatureUpdateForm.get('sign_array') as FormArray).controls.pop();
          }
        }

      } else {
        for (let index = 0; index < noOfSign.value; index++) {
          (this.signatureUpdateForm.get('sign_array') as FormArray).push(this.createSignatureBoxes(''));
        }
      }
    } else if (this.getSignArray.length == 0) {
      for (let index = 0; index < noOfSign.value; index++) {
        (this.signatureUpdateForm.get('sign_array') as FormArray).push(this.createSignatureBoxes(''));
      }
    } else if (noOfSign.value < this.getSignArray.length) {
      message = 'Are you sure, value is less than no. of signs?'
      this.dialogService.doConfirmDialog(message).subscribe(res => {
        if (res === 'Yes') {

          arrlength = this.getSignArray.length;
          for (let i = arrlength; i >= noOfSign.value; i--) {
            this.removeSign(i);
            if (i > noOfSign.value) {
              this.deleteSignature(((this.signatureUpdateForm.get('sign_array') as FormArray).at(i - 1) as FormGroup).value, i, 'noOfSignChange', noOfSign.value);
            }
          }
        }
      });
    } else if (noOfSign.value > this.getSignArray.length) {
      console.log('2nd if', noOfSign.value);
      message = 'Are you sure?'
      this.difference = noOfSign.value - this.getSignArray.length;
      for (let i = 0; i < this.difference; i++) {
        ((this.signatureUpdateForm.get('sign_array') as FormArray).push(this.createSignatureBoxes('')));
      }
    }
  }
  removeSign(i) {
    const remove = this.signatureUpdateForm.get('sign_array') as FormArray;
    remove.removeAt(i);
  }
  deleteSignature(report, index, action, noOfSign) {
    if (action === 'delete') {
      this.dialogService.doConfirmDialog('').subscribe(resp => {
        if (resp === 'Yes') {
          console.log('yes => ', resp);
          this.confirmDelete(report, index, action, (this.signatureUpdateForm.controls.noOfSignatures.value - 1));
        } else {
          console.log('no => ', resp);
        }
      });
    } else {
      this.confirmDelete(report, index, action, (this.signatureUpdateForm.controls.noOfSignatures.value - 1));
    }

  }
  confirmDelete(report, index, action, noOfSign) {

    this.apiService.fetchSignatureInfo(localStorage.getItem('customerId')).subscribe((resp: any) => {
      console.log(resp);
      this.listOfSignatures = resp ? resp : [];
      this.deleteRecord = resp;
      this.apiService.getSignData().subscribe(data => {
        this.signatureUpdateForm.get('sign_array')['controls'][index].accountSignatureId = data.uploadModel.accountSignatureId

      })
      var v: string = report.imageURL1;
      if (v.toString().includes('no-image.jpg')) {
        this.signatureUpdateForm.get('sign_array')['controls'].splice(index, 1);
        this.signatureUpdateForm.controls.noOfSignatures.setValue(this.signatureUpdateForm.controls.noOfSignatures.value - 1);
        this.noOfSignSelect = this.signatureUpdateForm.controls.noOfSignatures.value;
        if (this.sign_array.length == this.signatureUpdateForm.controls.noOfSignatures.value) {
          this.enableButton = true;
        } else {
          this.enableButton = false;
        }
        return;
      }
      if (this.deleteRecord[index].accountSignatureId != 0 && this.deleteRecord[index].accountSignatureId != null && this.deleteRecord[index].accountSignatureId != undefined &&
        this.deleteRecord[index].accountSignatureId != '') {
        this.apiService.deleteCustomerSign(this.deleteRecord[index].accountSignatureId).subscribe((data) => {
        }, error => {

          this.apiService.fetchSignatureInfo(localStorage.getItem('customerId')).subscribe((resp: any) => {
            this.listOfSignatures = resp ? resp : [];
            this.sign_array = resp;
            if (action === 'delete') {
            }
            if (this.sign_array.length == this.signatureUpdateForm.controls.noOfSignatures.value) {
              if ((this.listOfSignatures.length === 0 && this.signatureUpdateForm.controls['noOfSignatures'].value === 0)) {
                this.enableButton = false;
              } else {
                this.enableButton = true;
              }
            } else if (this.sign_array.length == 0) {
              this.enableButton = false;
            }
            else {
              this.enableButton = false;
            }
          })
        }


        );
        this.signatureUpdateForm.get('sign_array')['controls'].splice(index, 1);
        this.signatureUpdateForm.controls.noOfSignatures.setValue(this.signatureUpdateForm.controls.noOfSignatures.value - 1);
        this.noOfSignSelect = this.signatureUpdateForm.controls.noOfSignatures.value;
      }
    });
  }
  openPopUp(report, index: any, data?: any) {
    this.signIndex = index;
    this.customerId = localStorage.getItem('customerId');
    this.signCount = localStorage.getItem('noOfSign');
    this.accountSignatureId = report.accountSignatureId ? report.accountSignatureId : null;
    this.enableButton = false;
    if (this.listOfSignatures.length == 1 && index == 0) {
      this.accountSignatureId = this.listOfSignatures[index].accountSignatureId;
    } else if (index >= 0 && this.listOfSignatures.length > 1) {
      if (this.listOfSignatures[index]?.accountSignatureId) {
        this.accountSignatureId = this.listOfSignatures[index].accountSignatureId;
      } else {
        this.accountSignatureId = 0;
      }
    }
    else {
      console.log("else")
      this.accountSignatureId = 0;
    }
    if (!this.show) {
      let dialogRef: MatDialogRef<any> = this.dialog.open(CustomerOnboardingMandateEsignComponent, {
        width: '720px',
        disableClose: true,

        data: {
          screen: "NewCustomerOnboarding", customerId: this.customerId, signCount: this.signatureUpdateForm.get('noOfSignatures').value,
          accountSignatureId: this.accountSignatureId,
          signatureType: "DigitalSign"
        },
      });
      dialogRef.afterClosed().subscribe(res => {
        if (res.image !== null && res.image !== undefined) {
          this.enableButton = true;
          ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('imageURL1').patchValue(res.image);
          this.show = !res.enable;
          this.getListOfSignatures(this.customerId, "Yes");
        }

        else {
          this.enableButton = false;
        }
      });
    }
  }
  getListOfSignatures(customerId: any, popupSign) {
    this.apiService.fetchSignatureInfo(customerId).subscribe((resp: any) => {
      if (popupSign === "Yes") {
        this.signatureUpdateForm.get('sign_array')['controls'][this.signIndex].accountSignatureId = resp[resp.length - 1].accountSignatureId;
      }
      this.listOfSignatures = resp ? resp : [];
      this.cdr.detectChanges();
      this.signatureUpdateForm.get('noOfSignatures').setValue(this.noOfSignSelect);
      if (this.listOfSignatures.length == this.noOfSignSelect) {
        this.enableButton = true;
      } else {
        this.enableButton = false;
      }
    });
  }
  onFileSelect(report, event, index) {
    this.apiService.fetchSignatureInfo(this.customerId).subscribe((resp: any) => {
    })
    this.selectedFiles = event.target.files[0];
    if (event.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        this.signatureimg = event.target.result;
        console.log(this.signatureimg, "Upload image");
      }
      console.log(this.signatureimg);
      this.uploadDoc(report, event.target.files[0], this.selectedFiles, index, event);
      console.log("sign array -- ", this.signatureUpdateForm.get('sign_array'));
    }
  }
  uploadDoc(report, event, url, index, img) {
    if (img.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(img.target.files[0]);
      reader.onload = (img: any) => {
        this.signatureimg = img.target.result;
        this.customerId = localStorage.getItem('customerId');
        this.signCount = this.signatureUpdateForm.get('noOfSignatures').value;
        const uploadData = new FormData();
        const data = new FormData();
        if (this.listOfSignatures.length == 1 && index == 0) {
          console.log("if");
          this.accountSignatureId = this.listOfSignatures[index].accountSignatureId;
        } else if (index >= 0 && this.listOfSignatures.length != 1) {
          console.log(" else if");
          this.accountSignatureId = this.listOfSignatures[index].accountSignatureId;
        } else {
          console.log("else");
          this.accountSignatureId = 0;
        }
        const upload_model = ({
          noOfSignatures: this.signCount,
          accountSignatureId: this.accountSignatureId,
          documentName: "Signature",
          customerId: this.customerId,
          documentType: "10",
          fileType: event.type,
          fileName: event.name,
          verificationType: "New Customer Signature",
          signatureType: "Uploaded Sign"
        });

        uploadData.append("noOfSignatures", this.signCount);
        uploadData.append("uploadModel", JSON.stringify(upload_model));
        uploadData.append("file", url);

        const txtCont = '{"noOfSignatures":' + this.signCount + ', "uploadModel":' + JSON.stringify(upload_model) + ' } ';

        data.append("data", txtCont);
        data.append("file", url);
        ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('imageURL1').patchValue(this.signatureimg);
        this.uploadSub = this.apiService.uploadSignature(data).subscribe((res: any) => {
          if (res !== null) {
            ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('accountSignatureId').
              patchValue(res.accountSignatureId);
            ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('documentName').
              patchValue(res.documentName);
            ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('documentType').
              patchValue(res.documentType);
            ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('fileType').
              patchValue(res.fileType);
            ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('fileName').
              patchValue(res.fileName);
            ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('verificationType').
              patchValue(res.verificationType);
            ((this.signatureUpdateForm) as FormGroup).get('noOfSignatures').
              patchValue(res.noOfSignatures);
            console.log(this.signatureUpdateForm);
          }
          this.getListOfSignatures(this.customerId, "No");
          if (res.type == HttpEventType.UploadProgress) {
            this.progressValOthers = Math.round(100 * (res.loaded / res.total));
          } else if (res.type == HttpEventType.Response) {
            this.progressValOthers = 0;
          }
        }, (error: any) => {
          if (error.status == 201) {
            this.shotToast("Document Uploaded!");
          } else {
            ((this.signatureUpdateForm.get('sign_array') as FormArray).at(index) as FormGroup).get('imageURL1').patchValue('');
            this.shotToast(error.message);
          }
        }
        );
      }

    }
  }
  onSubmit(){
    this.snack.open('Signature Updated',"OK",{
      duration: 4000,
      verticalPosition: "top",
      horizontalPosition: "right",
    })
    this.dialogRef.close({
      status:'INITIATED',
      resp: this.signatureUpdateForm.get('sign_array').value,
      screen: 'signatureUpload'
    })
  }
  shotToast(msg: string) {
    this.snack.open(`${msg}`, "OK", {
      duration: 6000,
      verticalPosition: "top",
      horizontalPosition: "right",
    });
  }
  close() {
    this.dialogRef.close({
      status: 'UNDER_PROCESS'
    })
  }
}
