import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-qualitative-details-update',
  templateUrl: './qualitative-details-update.component.html',
  styleUrls: ['./qualitative-details-update.component.scss']
})
export class QualitativeDetailsUpdateComponent implements OnInit {
qualitativeUpdateForm:FormGroup;
accountId:any;
accountType:any;
scoreCardId:any;
numberOfApplicant:any;
qualitative: FormArray = new FormArray([]);

  constructor(
    private formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public qualitativeDetails,
    private ls: LocalStoreService,
    private apiService:ApiService,
    private dialogRef: MatDialogRef<QualitativeDetailsUpdateComponent>,
    private snack: MatSnackBar
  ) { }

  ngOnInit(): void {
    console.log(this.qualitativeDetails);
    this.accountId = this.ls.getItem('accountId');
    this.accountType = this.ls.getItem('accountType');
    this.scoreCardId = this.qualitativeDetails?.scorecardId;
    this.numberOfApplicant = this.qualitativeDetails?.numberOfApplicant;
    this.buildQualitativeForm();
    this.apiService.getScorecardQuestions(this.accountType).subscribe((response) => { 
      this.qualitative = this.qualitativeUpdateForm.get('scorecardMapping') as FormArray;
      this.qualitative.push(this.addScorecardMappiing());
      for(let i=0; i<response.length; i++){
        this.addScorecardDetails(response[i],this.qualitativeDetails?.scoreCardMapping[0].scoreCardDetails[i].scorecardAnswer,i);
      }
      
    });
  }

  buildQualitativeForm(){
    this.qualitativeUpdateForm = this.formBuilder.group({
      accountId:this.accountId,
      accountType:this.accountType,
      scoreCardId:this.scoreCardId ? this.scoreCardId : '',
      loggedinUser:'123',
      numberOfApplicant:this.numberOfApplicant,
      scorecardMapping: this.formBuilder.array([])
    })
  }
addScorecardMappiing(){
  return this.formBuilder.group({
    applicantId:'',
    scoreCardDetails:this.formBuilder.array([])
  })
}
  addScorecardDetails(data?,resp?,index?){
    (this.qualitative.controls[0].get('scoreCardDetails') as FormArray).push(this.formBuilder.group({
      scoreCardDetailsId:[data?data.scoreCardDetailsId:''],
      scoreCardMappingId: [data?data.scoreCardMappingId:''],
      scoreCardQuestionId: [data?data.scoreCardQuestionId:''],
      scorecardQuestion:[data?data.scorecardQuestion:''], 
      scorecardAnswer: [data?data.scorecardAnswer:'', [Validators.required]],
      scorecardAnswers:[data?data.scorecardAnswers:''],
      attributeType:[data?data.attributeType:'']
    })
    )
    if(resp){
      (this.qualitative.controls[0].get('scoreCardDetails') as FormArray).at(index).get('scorecardAnswer').setValue(resp)
    }
  }

  saveReports(formData){
    this.apiService.saveQualitativeScorecard(formData).subscribe((resp) => {
      this.snack.open('Qualitative Scoreboard Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      this.dialogRef.close({
        status:"BACKLOG"
      })
    }, err => {
      this.snack.open('Server Error!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    });
  }
  close(){
this.dialogRef.close({
  status:'ONGOING'
})
  }
}
