import { ChangeDetectorRef, Component, Inject, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { ApiService } from 'app/shared/services/api.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { DOCUMENT } from '@angular/common';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DomSanitizer } from '@angular/platform-browser';
import { NavigationExtras, Router } from '@angular/router';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TasksummaryService } from '../tasksummary.service';
import * as moment from 'moment';
import { MatSnackBar } from '@angular/material/snack-bar';
import { UpdatePrimaryInfoComponent } from './update-primary-info/update-primary-info.component';
import { UpdateContactInfoComponent } from './update-contact-info/update-contact-info.component';
import { UpdateProofOfIdentityComponent } from './update-proof-of-identity/update-proof-of-identity.component';
import { UpdateAdditionalInfoComponent } from './update-additional-info/update-additional-info.component';
import { UpdateMandateInfoComponent } from './update-mandate-info/update-mandate-info.component';
import { TextFieldModule } from '@angular/cdk/text-field';
import { environment } from 'app/environments/environment.prod';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';

@Component({
  selector: 'app-cust-on-boarding-approver',
  templateUrl: './cust-on-boarding-approver.component.html',
  styleUrls: ['./cust-on-boarding-approver.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class CustOnBoardingApproverComponent implements OnInit {
  @Input("initiatedArr") initiatedArr: any;

  // custOnBoardingApprovet:CustOnBoardingApprover;
  selectedIndex: number = 0;
  imgUrl: string | ArrayBuffer;
  profilePicture: any;
  proofDocForm: FormGroup;
  userAddressForm: FormGroup;
  otherDocForm: FormGroup;
  proofDoc_array: FormArray;
  otherDoc_array: FormArray;
  userAddress_array: FormArray;
  addNewProof: boolean = false;
  arrayPrefix: any[] = [];
  arrayGender: any[] = [];
  arrayMaritalStatus: any[] = [];
  arrayCountry: any[] = [];
  arrayResidence: any[] = [];
  arrayDocumentType: any[] = [];
  arrayEmploymentType: any[] = [];
  arrayEmployerName: any[] = [];
  otherDocumentArray: any[] = [];
  proofDocumentArray: any[] = [];
  accountId: any;
  primaryInfo: any;

  @ViewChild('ncForm') ncForm: NgForm;
  // image: string | ArrayBuffer;
  image: Object;
  custIdProofs_array: any;
  signatureDetails_array: any;
  status: any;
  rejectedStatus: boolean;
  approvedStatus: boolean;
  statusUpdate: any;
  document_type: string;
  custOtherDoc_array: any;
  customerId: any;
  documentName: any;
  customerColorCode: any;
  phoneNum: any;
  emailID: any;
  errorStatus: any;
  imageUrl: any;
  loading: boolean= true;
  noOFSigns: any;
  editScreen: boolean;
  kycStatus: any;
  editStatus: boolean;
  nameCreater:any[]=['Y','B','','D','E','T','A','E','R','C'];
  continueStatus:boolean=true;

  constructor(private tasSumService: TasksummaryService,
    public cdr: ChangeDetectorRef,private router:Router,
    private apiService:ApiService, private ls: LocalStoreService,
    public dialogRef: MatDialogRef<CustOnBoardingApproverComponent>,
    @Inject(MAT_DIALOG_DATA) public dataResponse: any,private sanitizer: DomSanitizer,
    private snack: MatSnackBar, private dialog: MatDialog, private jwtService: JwtAuthService
  ) { }

  ngOnInit(): void {

    setTimeout(() => {
      this.loading = true;
      console.log("dataResponse.dataResp.customerId",this.dataResponse.dataResp.customerId);
      console.log("dataResponse.dataResp.accountId",this.dataResponse.dataResp.accountId);
      this.customerId = this.dataResponse.dataResp.customerId
      this.ls.setItem('customerId',this.customerId)
      //not required as we are uploading only documentType=2 in new customer onboarding
      this.getCustomerDocumentsDetails(this.customerId)
      console.log(this.customerId);
      this.phoneNum = this.dataResponse.dataResp.phoneNumber;
      this.emailID = this.dataResponse.dataResp.primaryEmailAdress;
      console.log("Dataas", this.dataResponse,this.phoneNum, this.emailID)
      this.customerColorCode = localStorage.getItem('CustomerColorCode');
      // console.log('dataResponse :: ', this.dataResponse.dataResp);
      // console.log('recordStatus :: ', this.dataResponse.recordStatus);
      this.imageUrl = this.dataResponse.dataResp.profileImage;
      this.status = this.dataResponse.dataResp.custStatus;
      this.errorStatus = this.dataResponse.dataResp.custStatus;
      this.userAddress_array = this.dataResponse.dataResp.userAddress;
      this.custIdProofs_array = this.dataResponse.dataResp.custIdProofs;
      console.log("result::", this.custIdProofs_array);
      if (this.status === 'REJECTED') {
        this.rejectedStatus = true;
        this.approvedStatus = true;
        this.editScreen = false;
        this.continueStatus=true
      } else if (this.status === 'INITIATED') {
        this.rejectedStatus = false;
        this.approvedStatus = false;
        this.editScreen = false;
        this.continueStatus=true;
      } else if (this.status === 'APPROVED') {
        this.rejectedStatus = true;
        this.approvedStatus = true;
        this.editScreen = true;
        this.continueStatus=true;
      } else if (this.status === 'UNDER_PROCESS') {
        this.rejectedStatus = false;
        this.approvedStatus = true;
        this.editScreen = false;
        this.continueStatus=false
      }
      // localStorage.setItem('kycStatus',this.customerDetails[0].customerAdditionalInfo.kycStatus);
      // console.log(this.dataResponse.dataResp.customerId);
      this.getProfileImage(this.dataResponse.dataResp.customerId);
      this.fetchSignatureDetails(this.dataResponse.dataResp.customerId);
    }, 5000);
    if (this.dataResponse.dataResp.customerAdditionalInfo.kycStatus !== "NULL") {
      this.kycStatus = this.dataResponse.dataResp.customerAdditionalInfo.kycStatus;
    }
  }
  goToCustOnboarding() {
    console.log("Navigating to edit screen");

    this.apiService
      .updateKycStatusByCustId(this.dataResponse.dataResp.customerId, '', 'UNDER_PROCESS').subscribe(data => {
      });
    this.router.navigateByUrl('/others/newCustomerOnboarding');
    console.log(this.dataResponse);
    var splitKyc = this.dataResponse.dataResp.kycReference.split('KYC_');
    console.log(splitKyc[1]);
    this.ls.setItem('EDIT-KYCDETAILS', splitKyc[1]);
    console.log(this.ls.getItem('EDIT-KYCDETAILS'));

    // const navigationExtras: NavigationExtras = {
    //   queryParams: {
    //     // 'id': this.modifyUserObject.id,
    //     kyc_Id:this.dataResponse.dataResp.kycReference,

    //   },
    // };

    // this.apiService.sendNavParam(navigationExtras);
    this.dialogRef.close({
      message: 'Confirm',
    });

  }
  getCustomerDocumentsDetails(customerId: string) {
    let document_type: number[] = [6, 7, 8];
    // this.document_type= "6";
    this.apiService.getCustomerAllDocuments(customerId, document_type).subscribe((result) => {
      this.loading = false;
      this.custOtherDoc_array = result;
      if (result != null) {
        this.documentName = this.custOtherDoc_array[0].fileName;
        console.log("custOtherDoc_array::", this.custOtherDoc_array)
      }
    });
  }

  updateApplicationStatus(formData, status, isEdit?) {
    // console.log('formData :: ', formData.dataResp);
    // console.log('status :: ', status);
    formData.dataResp.customerAdditionalInfo.kycStatus = status;

    this.statusUpdate = status;
    // console.log('formData :: ', formData.dataResp);
    /* TO-DO: update correct api this method call */
    // console.log('customerId :: ', formData.dataResp.customerId);
    this.apiService.updateKycStatusByCustId(formData.dataResp.customerId, '', status)
      .subscribe(data => {
        // console.log('update response :: ', data)
        if (data != null) {
          this.cdr.markForCheck();
          this.cdr.detectChanges();
          if (isEdit != true){
            this.dialogRef.close({
              message: 'Confirm',
              recordStatus: status,
              data
            });
          }
          this.snack.open(`${status}`, 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
      }, error => {
        console.log('error resp :: ', error);
        this.errorStatus = status;
        this.snack.open(`${error.error.message} `, 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
  }

  close(data) {
    // this.dialogRef.close();
    data.recordStatus = this.statusUpdate
    this.dialogRef.close({
      message: 'Confirm',
      data,
      recordStatus:this.status
    });
    //  console.log("conf data", data);
  }
  getProfileImage(customerId) {
    this.apiService.getProfileDetails(customerId)
      .subscribe((data: any) => {
        // console.log(data);
        // this.profileData = data;
        // console.log(data.profileImage); 

        if (data.profileImage != null) {
          //let objectURL = 'data:image/jpeg;base64,' + data.profileImage.fileData;
          let objectURL = data.profileImage.fileUrl;
          this.image = this.sanitizer.bypassSecurityTrustUrl(objectURL)
        }
        // console.log(this.image,"Profile Data");
        // console.log(this.profileData.customerName,"Profile Data");
        // const blob = base64StringToBlob(this.profileData.fileData, contentType);
        // console.log(blob);
      }, (error: any) => {
        // console.log(error);
      });
  }
  fetchSignatureDetails(customerId) {
    console.log("inside fetchSignatureDetails method");
    // this.apiService.fetchSignatureInfo(customerId)
    // .subscribe((data:any) => {
    //    console.log(data);
    // },(error:any) => {
    //    console.log(error);
    // });
    this.apiService.getSignatureDocuments(customerId)
      .subscribe((data: any) => {
        console.log(data);
        console.log(data.length);
        // this.noOFSigns = data[0].noOfSignatures;
        this.noOFSigns = data.length;
        this.signatureDetails_array = data;
      }, (error: any) => {
        console.log(error);
      });
  }
  openEditScreen(screen,data){
    this.status = "UNDER_PROCESS"
    this.updateApplicationStatus(this.dataResponse, "UNDER_PROCESS",true)
    const dialogRef = this.dialog.open(screen, {
      panelClass: 'full-width-dialog',
      data: data,
      width: '80%',
      disableClose: true
    });
    dialogRef.afterClosed().subscribe((submit) => {
      console.log(submit);
      this.status = submit.status
      if (submit.status == 'INITIATED') {
        this.updateApplicationStatus(this.dataResponse, "INITIATED",true)
        if(!submit.screen){
        Object.assign(data,submit.resp)
        }
        if(submit.screen == 'documentUpload'){
          this.getCustomerDocumentsDetails(this.customerId)
        }
        if(submit.screen == "signatureUpload"){
          this.fetchSignatureDetails(this.customerId)
        }
      }
      this.cdr.markForCheck();
      this.cdr.detectChanges();
    })
  }
  editPrimaryInfo() {
 this.openEditScreen(UpdatePrimaryInfoComponent,this.dataResponse.dataResp)
  }
  editContactInfo() {
this.openEditScreen(UpdateContactInfoComponent,this.userAddress_array)
  }
  editProofOfIdentity() {
this.openEditScreen(UpdateProofOfIdentityComponent,this.proofDoc_array)
  }
  editAdditionalInfo() {
this.openEditScreen(UpdateAdditionalInfoComponent,this.dataResponse.dataResp.customerAdditionalInfo)
  }
  editMandateInfo() {
this.openEditScreen(UpdateMandateInfoComponent,this.signatureDetails_array)
  }
  continue(){    
  this.ls.setItem('customerId', this.dataResponse.dataResp.customerId);
  this.ls.setItem("currentKycTabIndex",0);
  const theme=null;
  const type ="INDIVIDUALONBOARDING";
  window.open(
  `${environment.redirectUrl}?code=${this.jwtService.getEncryptedToken()}&appType=${type}&theme=${theme}`,
  '_blank'
  );
   this.dialogRef.close();
  }
}