import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CorporateOnboardingService } from 'app/views/others/onboarding-corporate/corporate-onboarding.service';
// import { CorporateOnboardingService } from '../../../others/onboarding-corporate/corporate-onboarding.service';


@Component({
  selector: 'app-update-corporate-basic-info',
  templateUrl: './update-corporate-basic-info.component.html',
  styleUrls: ['./update-corporate-basic-info.component.scss']
})
export class UpdateCorporateBasicInfoComponent implements OnInit {
  corporateInfoUpdateForm: FormGroup;
  corporateId: any
  countryArr: any = [];
  categoryArr: string[] = ['Privileged', 'Un Privileged'];
  constructor(
    @Inject(MAT_DIALOG_DATA) public basicInfo,
    private dialogRef: MatDialogRef<UpdateCorporateBasicInfoComponent>,
    private apiService: ApiService,
    private ls: LocalStoreService,
    private formBuilder: FormBuilder,
    private snack: MatSnackBar,
    private corporateService: CorporateOnboardingService
    // private corporateService: CorporateOnboardingService
  ) { }

  ngOnInit(): void {
    this.corporateId = this.ls.getItem('corporateId');
    console.log(this.basicInfo)
    this.getCountryName('country')
    if (this.basicInfo) {
      this.buildBasicForm(this.basicInfo)
    }
  }
  buildBasicForm(data) {
    this.corporateInfoUpdateForm = this.formBuilder.group({
      corporateId: [data.corporateId],
      companyName: [data ? data.companyName : '', [Validators.required]],
      category: [data ? data.category : '', [Validators.required]],
      country: [data ? data.country : '', [Validators.required]]
    })
  }
  getCountryName(key: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].authStatus === 'A') {
          this.countryArr.push(data[i].countryName);
        }
      }
    });
  }
  onSubmit() {
    this.corporateService
      .saveOrUpdateCorporateAccountInfo(this.corporateInfoUpdateForm.value)
      .subscribe((data) => {
        if (data != null) {
          this.snack.open('Basic Information Updated' + ' !', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right',
          });
          this.dialogRef.close({
            status:"INITIATED",
            resp:data
          })
        
        } else {
          this.snack.open(
            'Not able to connect to server!',
            'INTERNAL SERVER ERROR',
            {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right',
            }
          );
        }

        (error) => {
          console.log('error resp :: ', error);
          this.snack.open(`${error.error.message} `, 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right',
          });
        };
      });
  }

    close(){
      this.dialogRef.close({
        status: 'UNDER_PROCESS'
      })
    }
  }
