import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup} from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router, ActivatedRoute } from '@angular/router';
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { TasksummaryService } from '../../tasksummary.service';


@Component({
  selector: 'app-loan-collateral-details-update',
  templateUrl: './loan-collateral-details-update.component.html',
  styleUrls: ['./loan-collateral-details-update.component.scss']
})
export class LoanCollateralDetailsUpdateComponent implements OnInit {
  loanCollateralForm: FormGroup;
  loanaccountID: any;
  arrayCountry: any[] = [];
  arrayState: any[] = [];
  arrayCity: any[] = [];
  arrayCollateralType: string[];
  arrayRateValue: string[];
  entityName: string;
  assetDetails: any
  loanCollateralDetails: any;
  loanAccountId: any;
  collateralDetails: any;

  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;

  constructor(private fb: FormBuilder, @Inject(MAT_DIALOG_DATA) public data: any,
  private dialogue: MatDialog,private taskSumService: TasksummaryService, private cdr: ChangeDetectorRef,
  public dialogRef: MatDialogRef<LoanCollateralDetailsUpdateComponent>,
  private apiService: ApiService, private ls: LocalStoreService, private snack: MatSnackBar,
  private router: Router,  private loader: AppLoaderService) { }

  ngOnInit(): void {
    this.loanaccountID = this.data.loanAccountId;
    this.buildCollateralForm();
    this.getCountryName("country");
    this.getCollateralType("collateral_type");
    this.getCollateralDetails();
    this.getRateValue("currency");
  }

  buildCollateralForm(data?) {
    console.log('item',data)
    // this.getBusinessProductDetails("business_product_name");
    // this.getAccountBranchDetails();
     
    if(data){ 
      this.getStateName(data.country);
      this.getCityName(data.state);
    }
      
    this.loanCollateralForm = this.fb.group({
        loanAccountId: [this.loanaccountID],
        country:[data ? data?.collateralInfo?.country:''],
        state:[data ? data?.collateralInfo?.state:''],
        city: [data ? data?.collateralInfo?.city : ''],
        collateralType:[data ? data?.collateralInfo?.collateralType:''],
        collateralCurrency:[data ?data?.collateralInfo?.collateralCurrency:''],
        collateralValue:[data ? data?.collateralInfo?.collateralValue:''],
        locality:[data ? data?.collateralInfo?.locality:''],
        street:[data ? data?.collateralInfo?.street:''],
        building:[data ? data?.collateralInfo?.building:''],
        zipCode: [data ? data?.collateralInfo?.zipCode : ''],
        homeType:[data ? data?.collateralInfo?.homeType:''],
      attributes: [data ? data?.collateralInfo?.attributes : ''],
      thirdPartyCollateral: [data ? data?.collateralInfo?.thirdPartyCollateral:'']
     
  
  
 
    
    });
    console.log('form before',this.loanCollateralForm)
    console.log('form aftr',this.loanCollateralForm.value)
  }
  getRateValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayRateValue = data;
    });
  }
  getCollateralDetails() {
   // this.loader.open();
    this.apiService.getCollateralByLoanAccId(this.loanaccountID).subscribe((collateralDetails) => {
   //   this.loader.close();
      this.collateralDetails = collateralDetails;
      console.log(this.collateralDetails.collateralDetails.length);
      console.log(this.collateralDetails);
      this.buildCollateralForm(this.collateralDetails);
    //  this.populateCollateral(this.collateralDetails);
    }, (err: any) => {
    //  this.loader.close();
    });
  }
  getCollateralType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayCollateralType = data;
    });
  }
  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }

  submitForm1(){
    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      attributes:this.loanCollateralForm.value.attributes,
      building:this.loanCollateralForm.value.building,
      city:this.loanCollateralForm.value.city,
      zipCode:this.loanCollateralForm.value.zipCode,
      country:this.loanCollateralForm.value.country,
      state:this.loanCollateralForm.value.state,
      dimensions: this.loanCollateralForm.value.dimensions,
      locality: this.loanCollateralForm.value.locality,
      thirdpartyCollateral: this.loanCollateralForm.value.thirdPartyCollateral,
      collateralValue: this.loanCollateralForm.value.collateralValue,
      collateralType: this.loanCollateralForm.value.collateralType,
      collateralCurrency: this.loanCollateralForm.value.collateralCurrency
    }
    console.log("payload",payload)
    this.apiService.upsertCollateralDetails(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
      // this.loanAccountId=data.loanAccountId;
      // console.log("loanAccountid",this.loanAccountId);

      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      
     
      // this.assign(data.loanAccountId)
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName);
        
    this.loanaccountID = this.data.loanAccountId;
    console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }

  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
}
  getCountryName(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayCountry = data;
      console.log("-->", this.arrayCountry)
    });
  }
  getStateName(country) {
    this.entityName = "state";
    console.log("State -- ", country)
    this.apiService.getDropDownValues1(this.entityName, country).subscribe((data) => {
      this.arrayState = data;
      console.log("-->", this.arrayState)
    });
  }
  getCityName(state: any) {
    this.entityName = "city";
    console.log(state)
    this.apiService.getDropDownValues1(this.entityName, state).subscribe((data) => {
      this.arrayCity = data;
      console.log("-->", this.arrayCity)
     
  
    });
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }
goToClose(): void {
  this.dialogRef.close();
 
 // this.getLoanDetails();
  this.apiService.getCollateralByLoanAccId(this.loanaccountID).subscribe((loanCollateralDetails: any) => {
    console.log("All Account Service", loanCollateralDetails);
    this.loanCollateralDetails = loanCollateralDetails;
    //this.buildformGroup(this.loanAccountDetails);
    console.log("this.loanCollateralDetails", this.loanCollateralDetails);
    console.log('form',this.loanCollateralForm.value)
    this.loanCollateralForm.value.building = this.loanCollateralDetails.building;
    this.loanCollateralForm.value.dimensions = this.loanCollateralDetails.dimensions;
   this.loanCollateralForm.value.attributes = this.loanCollateralDetails.attributes;
    this.loanCollateralForm.value.collateralCurrency = this.loanCollateralDetails.collateralCurrency;
    this.loader.close();
 //  this.loanUpdateForm.value.businessProductName = this.loanAccountDetails.businessProductName;
  });
 
}

}
