import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FundingStageService } from 'app/shared/services/funding-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/home/account.service';
import { TasksummaryService } from '../../tasksummary.service';
@Component({
  selector: 'app-funding-details-update',
  templateUrl: './funding-details-update.component.html',
  styleUrls: ['./funding-details-update.component.scss']
})
export class FundingDetailsUpdateComponent implements OnInit {
  fundStageUpdateForm: FormGroup;
  accountId: any;
  disableFundByAccount: boolean;
  disablechequeNumber: boolean;
  disabletellerTrxnRefNo: boolean;
  disabletellerTrxnStatus: boolean;
  accountInfo: any;
  
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<FundingDetailsUpdateComponent>,
    private formBuilder: FormBuilder,
    private cdr: ChangeDetectorRef,
    private accountService: AccountService,
    private ls: LocalStoreService,
    private fundingStageService: FundingStageService,
    private snack: MatSnackBar,
    private apiService: ApiService,
    private loader: AppLoaderService,
    private tasksummaryService: TasksummaryService
  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.accountId = this.ls.getItem('accountId')
    this.accountInfo = this.ls.getItem('accountDetails')
    if (this.data) {
      if (this.data.fundBy == "Fund Transfer") {
        this.disableFundByAccount = true;
        this.disabletellerTrxnStatus = true;
        this.disabletellerTrxnRefNo = false;
        this.disablechequeNumber = false;
      }
      if (this.data.fundBy == "Cheque") {
        this.disableFundByAccount = true;
        this.disablechequeNumber = true;
        this.disabletellerTrxnRefNo = false;
        this.disabletellerTrxnStatus = false;
      }
      if (this.data.fundBy == "Cash") {
        this.disabletellerTrxnRefNo = true;
        this.disableFundByAccount = false;
        this.disablechequeNumber = false;
        this.disabletellerTrxnStatus = false;
      }
      this.buildFundFormGroup(this.data);
    }
    else {
      this.buildFundFormGroup()
    }
  }
  buildFundFormGroup(data?) {
    this.fundStageUpdateForm = this.formBuilder.group({
      fundId: [data ? data.fundId : ''],
      fundBy: [data ? data.fundBy : '', [Validators.required]],
      amount: [data ? data.amount : ''],
      tellerTrxnRefNo: [data ? data.tellerTrxnRefNo : ''],
      fundByAccount: [data ? data.fundByAccount : ''],
      chequeNumber: [data ? data.chequeNumber : '', [Validators.maxLength]],
      branchCode: [data ? data.branchCode : ''],
      tellerTrxnStatus: [data ? data.tellerTrxnStatus : ''],
      createdBy: [data ? data.createdBy : ''],
      createdTime: [data ? data.createdTime : ''],
      modifiedBy: [data ? data.modifiedBy : ''],
      modifiedTime: [data ? data.modifiedTime : ''],
      accountId: [this.accountId],
    });
    if (data?.fundByAccount) {
      this.fetchAccountBalance(data.fundByAccount);
    }
  }
  fetchAccountBalance(accountId, isForce?: boolean) {
    this.loader.open();
    this.apiService.fetchAccountBalanceByAccountId(accountId)
      .subscribe(res => {
        console.log("RES", res);
        this.accountInfo = res;
        if (!isForce) {
          if (this.fundStageUpdateForm.get('fundBy').value == "Fund Transfer") {
            this.fundStageUpdateForm.get('fundByAccount').patchValue(res.accountId);
            this.fundStageUpdateForm.get('branchCode').patchValue(res.branchCode);
          }
        }
        this.loader.close();
        this.snack.open('Fetched!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }, (err: any) => {
        this.loader.close();
        this.snack.open('Invalid AccountId!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      })
  }
  selected(item) {
    console.log(item);
    if (item == "Fund Transfer") {
      this.disableFundByAccount = true;
      this.disabletellerTrxnStatus = true;
      this.disabletellerTrxnRefNo = false;
      this.disablechequeNumber = false;
      if (this.accountInfo != null) {
        this.fundStageUpdateForm.get('branchCode').patchValue(this.accountInfo.branchCode);
        this.fundStageUpdateForm.get('fundByAccount').patchValue(this.accountInfo.accountId);
      }
      this.fundStageUpdateForm.get('chequeNumber').patchValue("");
      this.fundStageUpdateForm.get('tellerTrxnRefNo').patchValue("");
      this.fundStageUpdateForm.get('amount').patchValue("");
      this.fundStageUpdateForm.get('fundByAccount').patchValue("");
    }
    if (item == "Cheque") {
      this.disableFundByAccount = true;
      this.disablechequeNumber = true;
      this.disabletellerTrxnRefNo = false;
      this.disabletellerTrxnStatus = false;
      if (this.accountInfo != null) {
        this.fundStageUpdateForm.get('fundByAccount').patchValue(this.accountInfo.accountId);
      }
      this.fundStageUpdateForm.get('branchCode').patchValue("");
      this.fundStageUpdateForm.get('tellerTrxnRefNo').patchValue("");
      this.fundStageUpdateForm.get('amount').patchValue("");
      this.fundStageUpdateForm.get('fundByAccount').patchValue("");
    }
    if (item == "Cash") {
      this.disabletellerTrxnRefNo = true;
      this.disableFundByAccount = false;
      this.disablechequeNumber = false;
      this.disabletellerTrxnStatus = false;
      this.fundStageUpdateForm.get('branchCode').patchValue("");
      this.fundStageUpdateForm.get('chequeNumber').patchValue("");
      this.fundStageUpdateForm.get('amount').patchValue("");
      this.fundStageUpdateForm.get('fundByAccount').patchValue("");
    }
    this.fundStageUpdateForm.get('fundBy').setValue(item);
  }
  onSubmit(fundStageUpdateForm) {

    fundStageUpdateForm.accountId = this.accountId;
    this.fundingStageService.saveOrUpdateFundStageDetails(fundStageUpdateForm)
      .subscribe(fundResp => {
        if (fundResp.accountId != null || fundResp.accountId != '' || fundResp.accountId != "") {
          this.fundingStageService.isSubmittedResp(true);
          this.snack.open('Funding Details Updated!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
              this.dialogRef.close({
                status: 'BACKLOG'
              })
          this.cdr.markForCheck();
        } else {
          this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          console.log("500 error")
        }
      }, error => {
        console.log("error :: ", error);
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
  }
  _keyPress(event: any) {
    console.log("PRessed", event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  onChangeEvent(accountId: any) {
    console.log("Onchange event", accountId);
    this.fetchAccountBalance(accountId);
  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }
}
