  debitCardData(cardType) {
    console.log('dardType-- ', cardType);
    
    this.api.getCardType(this.cardType.cardType)
      .subscribe(resp => {
        console.log('resp-- ', resp);
        
        this.debitCardDataValue = resp;        
        console.log(resp);

      }, err => console.log('ERROR: ', err));
  }

  openEditScreen(screen,data){
    let statusModel = {
      status: "ONGOING",
      accountId: this.accountId
    };
    this.tasksummaryService.updateCreateAccStatus(statusModel).subscribe(data => {
      console.log(data);
      if(data != null){
        this.approvedStatus = true;
        this.rejectedStatus = false;
      }
    });
    const dialogRef = this.dialog.open(screen,
      {
        panelClass: 'full-width-dialog',
        data: data,
        width: '80%',
        disableClose: true
      });
    dialogRef.afterClosed().subscribe((submit) => {

      this.accInfo.status = submit.status
      if (this.accInfo.status == 'BACKLOG') {
        this.getAccountInfoByAccountId(this.accountId)
        let statusModel = {
          status: "BACKLOG",
          accountId: this.accountId
        };
        this.tasksummaryService.updateCreateAccStatus(statusModel).subscribe(data => {
          console.log(data);
          if(data != null){
            this.approvedStatus = false;
            this.rejectedStatus = false;
          }
        });
      }    
      if(submit == true){
        this.getProfileImage(this.accInfo.customerId);
      }
      this.cdr.markForCheck();
      })
  }

  editAccountDetails() {
    
    if (this.accountDetails)
    this.accountDetails.customerName=this.accInfo?.firstName +' ' + this.accInfo?.lastName;
    this.openEditScreen(AccountDetailsUpdateComponent, this.accountDetails)
  }

