import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-card-charge-details-update',
  templateUrl: './card-charge-details-update.component.html',
  styleUrls: ['./card-charge-details-update.component.scss']
})
export class CardChargeDetailsUpdateComponent implements OnInit {
cardChargeUpdateForm:FormGroup;
  currencySymbol: string;
  accountType: String = "Card Origin";

  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    public dialogRef: MatDialogRef<CardChargeDetailsUpdateComponent>,
    public snack: MatSnackBar,
    private api: ApiService
  ) { }

  ngOnInit(): void {
    this.data.currencytype=localStorage.getItem('Currency Type');
    if(this.data.currencytype==="INR"){
      this.currencySymbol=""
    }else if(this.data.currencytype==="USD"){
      this.currencySymbol="$"
    }else if(this.data.currencytype==="CNY"){
      this.currencySymbol=""
    }else if(this.data.currencytype==="GBP"){
      this.currencySymbol=""
    }else if(this.data.currencytype==="ZAR"){
      this.currencySymbol="R"
    }

    this.cardChargeUpdateForm = this.formBuilder.group({
      chargeInfo: new FormArray([]),
    });
    if(this.data.chargeInfo){
      this.onChangeInterest(this.data.chargeInfo.length)
      this.in.patchValue([...this.data.chargeInfo])
    }
    else{
    this.apiService.getFlexcubeDetails("chargeDetails", this.accountType).subscribe((response) => {
        this.onChangeInterest(response.length);
        this.in.patchValue([{...response[0], amount: history.state.amount ? history.state.amount : ''}]);
      });
    }    
  }

  onChangeInterest(e) {
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            amount: ["",Validators.required],
            waiver: [false],
            cardId: [this.data.cardId],
            chargeDetailsId: [""],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }

  get f() {
    return this.cardChargeUpdateForm.controls;
  }
  get in() {
    return this.f.chargeInfo as FormArray;
  }
  onKeyPress(event: any) {
    const number = /[0-9\+\-\ ]/;
    let inputCharacter = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !number.test(inputCharacter)) {
      event.preventDefault();
      console.log("event:", event)
    }
  }
  checkbox(event) {
    console.log(event.checked);
  }

  onSubmit(form){
      console.log("form", form);
      this.api.cardChargeDetailsSave(form).subscribe(
        (resp) => {
          this.snack.open("Charge Details Updated!", "OK", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
          console.log("backend response from intrest saving", resp);
          let statusModel = {
            cardId: this.data.cardId,
            status: "BACKLOG"
          };
          this.apiService.updateCardStatus(statusModel).subscribe(data => {
            this.dialogRef.close({
              status: data.status
             });
          });
        },
        (err) => {
          this.snack.open(
            "Not able to connect to server!",
            "INTERNAL SERVER ERROR",
            {
              duration: 4000,
              verticalPosition: "top",
              horizontalPosition: "right",
            }
          );
        }
      );
    }
  
  close(){
    this.dialogRef.close({
      status:"ONGOING"
    })
  }
}
