import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-loan-guarantor-details-update',
  templateUrl: './loan-guarantor-details-update.component.html',
  styleUrls: ['./loan-guarantor-details-update.component.scss']
})
export class LoanGuarantorDetailsUpdateComponent implements OnInit {
  loanGuarantorForm: FormGroup;
  loanaccountID: any;
  loanAccountId: any;
  arrayCountry: any[] = [];
  arrayState: any[] = [];
  arrayCity: any[] = [];
  entityName: string;
  arrayPrefix: string[];
  guarantorDetails: any;
  tomorrow = new Date();

  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;
  
  constructor(private fb: FormBuilder, private loader: AppLoaderService, private ls: LocalStoreService, private snack: MatSnackBar,
    private taskSumService: TasksummaryService, private cdr: ChangeDetectorRef,@Inject(MAT_DIALOG_DATA) public data: any, private apiService: ApiService, public dialogRef: MatDialogRef<LoanGuarantorDetailsUpdateComponent>)
  { 
    this.tomorrow.setDate(this.tomorrow.getDate());
  }

  ngOnInit(): void {
    this.loanaccountID = this.data.loanAccountId;
    console.log('loanaccountID',this.loanaccountID)
    console.log('component call check');
    this.buildGuarantorForm(this.data);
   // this.getGuarantorDetails();
    this.getCountryName("country");
    this.getStateName("state");
    this.getPrefixValue("prefix");
  }
  buildGuarantorForm(data?) {
    console.log('item',data)
   
     
    if(data && data.country){
      this.getStateName(data.country);
      this.getCityName(data.state);
    }
    this.loanGuarantorForm = this.fb.group({
        loanAccountId: [this.loanaccountID],
        country:[data ? data?.gurantorInfo?.country:''],
        state:[data ? data?.gurantorInfo?.state:''],
        city: [data ? data?.gurantorInfo?.city : ''],
        prefix:[data ? data?.gurantorInfo?.prefix:''],
        firstName:[data ?data?.gurantorInfo?.firstName:''],
        lastName:[data ? data?.gurantorInfo?.lastName:''],
        mobileNumber:[data ? data?.gurantorInfo?.mobileNumber:''],
        building:[data ? data?.gurantorInfo?.building:data.gurantorInfo.address1],
        street:[data ? data?.gurantorInfo?.street:data.gurantorInfo.address2],
        zipCode: [data ? data?.gurantorInfo?.zipCode : ''],
        emailAddress:[data ? data?.gurantorInfo?.emailAddress:''],
        dateOfBirth: [data ? data?.gurantorInfo?.dateOfBirth:'']
     
  
  
 
    
    });

  }
  getGuarantorDetails() {
    this.loader.open();
    this.apiService.getGuarantorByLoanAccId(this.loanaccountID).subscribe((guarantorDetails) => {
      this.loader.close();
      this.guarantorDetails = guarantorDetails;
      console.log("this.guarantorDetails::", this.guarantorDetails);
      // this.loanGuarantorForm.value.building = this.guarantorDetails.address1;
     // this.loanGuarantorForm.value.street = this.guarantorDetails.address2;
     

      this.getStateName(this.guarantorDetails.country);
      this.getCityName(this.guarantorDetails.state);
      this.buildGuarantorForm(this.guarantorDetails);
      this.guarantorDetails.address1 = this.loanGuarantorForm.value.building;
      this.guarantorDetails.address2 = this.loanGuarantorForm.value.street;
    }, (err: any) => {
      this.loader.close();
    });
  }
  getPrefixValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayPrefix = data;
            console.log('data',this.arrayPrefix)
    });
  }
  
  getCountryName(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayCountry = data;
      console.log("-->", this.arrayCountry)
    });
  }

  getStateName(country) {
    this.entityName = "state";
    console.log("State -- ", country)
    this.apiService.getDropDownValues1(this.entityName, country).subscribe((data) => {
      this.arrayState = data;
      console.log("-->", this.arrayState)
    });
  }

  getCityName(state: any) {
    this.entityName = "city";
    console.log(state)
    this.apiService.getDropDownValues1(this.entityName, state).subscribe((data) => {
      this.arrayCity = data;
      console.log("-->", this.arrayCity)
    });
  }

  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
 

  goToClose(): void {
    this.dialogRef.close();
    this.apiService.getGuarantorByLoanAccId(this.loanaccountID).subscribe((guarantorDetails: any) => {
      console.log("All Account Service", guarantorDetails);
      this.guarantorDetails = guarantorDetails;
      console.log("this.accountDetails", this.guarantorDetails);
      console.log('form',this.loanGuarantorForm.value)

   });
   
  }
  submitForm1(){
    
  

    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      country:this.loanGuarantorForm.value.country,
      state:this.loanGuarantorForm.value.state,
      city: this.loanGuarantorForm.value.city,  
      address1:this.loanGuarantorForm.value.building,
      address2:this.loanGuarantorForm.value.street,
      zipCode: this.loanGuarantorForm.value.zipCode,
      mobileNumber: this.loanGuarantorForm.value.mobileNumber
      
  
    }
    console.log("payload",payload)
    this.apiService.upsertGuarantorDetails(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName); 
        this.loanaccountID = this.data.loanAccountId;
        console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
}
}
