import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-card-assessment-report-update',
  templateUrl: './card-assessment-report-update.component.html',
  styleUrls: ['./card-assessment-report-update.component.scss']
})
export class CardAssessmentReportUpdateComponent implements OnInit {
  cardAssessmentUpdateForm:FormGroup;
  arraySystemRecommendation = [
    'Recomended For Approval',
    'Recommended For Reject',
  ];
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    public dialogRef: MatDialogRef<CardAssessmentReportUpdateComponent>,
    private apiService: ApiService,
    public snack: MatSnackBar,
    private cdr: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    if(this.data.assessmentInfo){
      this.buildAssessmentForm(this.data.assessmentInfo);
    }
    else{
      this.buildAssessmentForm();
    }
  }
  buildAssessmentForm(item?) {
    this.cardAssessmentUpdateForm = this.fb.group({
      cardId: [this.data.cardId],
      assessmentId: [(item.assessmentId = '1')],
      requestedCardLimit: [item ? item.requestedCardLimit : ''],
      systemRecommendation: [item ? item.systemRecommendation : '' ,Validators.required],
      approvedCardLimit: [item?item.approvedCardLimit : ''],
      userRecommendation: [item ? item.userRecommendation : '',[Validators.required],],
      recommendedCardLimit: [item ? item.recommendedCardLimit : '',[Validators.required],],
      remark: [item ? item.remark : ''],
    });
  }
  close(){
    this.dialogRef.close({
      status:"ONGOING"
    })
  }
  _keyPress1(event: any) {
    const pattern = /^[0-9]*\.?[0-9]*$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    const input = event.target.value;
    if (input.length === 0 && event.which === 48) {
      event.preventDefault();
    }
  }
  onSubmit(){
    console.log(this.cardAssessmentUpdateForm);
    
    this.apiService.cardAssessmentDetails(this.cardAssessmentUpdateForm.value).subscribe((data) => {
      console.log('data', data);
      if (data != null) {
        this.snack.open('Assessment details Updated successfully!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right',
        });
        let statusModel = {
          cardId: data.cardId,
          status: "BACKLOG"
        };
        this.apiService.updateCardStatus(statusModel).subscribe(data => {
          this.dialogRef.close({
            status: data.status
           });
        });
      } err => {
        this.snack.open('Error Unable to Update Data!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      
       
      }
    });
  }
}
