import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CorporateOnboardingService } from 'app/views/others/onboarding-corporate/corporate-onboarding.service';
import * as moment from 'moment';

@Component({
  selector: 'app-update-corporate-identity',
  templateUrl: './update-corporate-identity.component.html',
  styleUrls: ['./update-corporate-identity.component.scss']
})
export class UpdateCorporateIdentityComponent implements OnInit {
  identityUpdateForm: FormGroup;
  minDate = new Date();
  validDate = new Date();
  maxDate = new Date();
  corporateId: any;
  constructor(
    private dialogRef: MatDialogRef<UpdateCorporateIdentityComponent>,
    @Inject(MAT_DIALOG_DATA) private data,
    private formBuilder: FormBuilder,
    private ls: LocalStoreService,
    private apiService: ApiService,
    private corporateService: CorporateOnboardingService,
    private snack: MatSnackBar
  ) { }

  ngOnInit(): void {
    this.corporateId = this.ls.getItem('corporateId')
    console.log(this.data);
    if (this.data) {
      this.buildIdentityForm();
      this.identityUpdateForm.patchValue(this.data)
    }
    else {
      this.buildIdentityForm();
    }
  }
  buildIdentityForm() {
    this.identityUpdateForm = this.formBuilder.group({
      corporateId: [this.corporateId],
      incorporationNumber: ['', [Validators.required, Validators.pattern("^[a-zA-Z0-9]{0,35}$")]],
      dateOfIssue: ['', [Validators.required]],
      dateOfExpiry: ['', [Validators.required]],
      isAnotherIdProofRequired: ['NO'],
      idProofList: this.formBuilder.array([])
    })
  }
  get getDocumentList() {
    return this.identityUpdateForm.get('idProofList') as FormArray;
  }
  addDocument() {
    this.getDocumentList.push(this.formBuilder.group({
      idType: ['', [Validators.required]],
      idNumber: ['', [Validators.required]],
    }));
  }
  deleteDocument(item, i) {
    console.log('delete record :: ', item, ' index :: ', i);
    this.getDocumentList.removeAt(i);
  }
  onSubmit(directorFormGroup) {
    let obj = {}
    Object.keys(directorFormGroup).map(function (key, index) {
      if (key === 'dateOfIssue' || key === 'dateOfExpiry') {
        directorFormGroup[key] = moment(directorFormGroup[key]).format('YYYY-MM-DD');
        obj[key] = directorFormGroup[key]
      } else {
        if (key != 'isAnotherIdProofRequired' && key != 'idProofList') {
          obj[key] = directorFormGroup[key]
        }
      }
    });
    this.corporateService.saveOrUpdateCorporateAccountInfo(obj)
      .subscribe(data => {
        console.log('data :: ', data);
        if (data != null) {
          this.snack.open(`Proof of identity Updated` + ' !', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          this.dialogRef.close({
            status:"INITIATED",
            resp:obj
          })
        } else {
          this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
      }, error => {
        console.log('error resp :: ', error);
        this.snack.open(`${error.error.message} `, 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
  }

  close() {
    this.dialogRef.close({
      status: 'UNDER_PROCESS'
    })
  }
}
