import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-secured-overdraft-details-update',
  templateUrl: './secured-overdraft-details-update.component.html',
  styleUrls: ['./secured-overdraft-details-update.component.scss']
})
export class SecuredOverdraftDetailsUpdateComponent implements OnInit {

  securedOverdraftUpdateForm: FormGroup;
  accountId: any
  onbackSpace: boolean;
  issecurityValid: boolean;
  finalRatevalue: number;
  linkangePercent: any;
  arrayCollateralType: string[];
  currency: any;
  showSpinner: boolean;
  branchData: any;
  branch: any;
  showSpinn: boolean;
  fileName: any;
  docUrl: string;
  showDelete: boolean;
  loggedInUser: any;
  showEditOption: boolean = false;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<SecuredOverdraftDetailsUpdateComponent>,
    private ls: LocalStoreService,
    private formBuilder: FormBuilder,
    private api: ApiService,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private confirmService: AppConfirmService,
    private tasksummaryService: TasksummaryService
  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.accountId = this.ls.getItem('accountId')
    this.loggedInUser = this.ls.getItem('userName')
    this.getCollateralType("collateral_type");
    this.getCurrencyDetails();
    if (this.data) {
      this.showEditOption = true;
      this.buildSecuredOverdraftForm(this.data)
      this.addFormGroup(this.data);
    }
    else {
      this.showEditOption = false;
      this.buildSecuredOverdraftForm();
      this.addFormGroup();

    }
  }

  buildSecuredOverdraftForm(data?) {
    this.securedOverdraftUpdateForm = this.formBuilder.group({
      collaterals: this.formBuilder.array([])
    });
  }
  get f() { return this.securedOverdraftUpdateForm.controls; }
  get c() { return this.f.collaterals as FormArray; }

  addFormGroup(data?) {
    const numberOfItems = data ? data.length : 1;
    console.log("numberOfItems", numberOfItems);
    for (let i = 0; i < numberOfItems; i++) {
      this.c.push(this.formBuilder.group({
        id: [data[i].id],
        collateralType: [data[i] ? data.collateralType : ''],
        collateralCurrency: [data[i] ? data.collateralCurrency : ''],
        accountId: this.accountId,
        collateralValue: [data[i] ? data[i].collateralValue : ''],
        attributes: [data[i] ? data[i].attributes : ''],
        dimensions: [data[i] ? data[i].dimensions : ''],
        securityReferenceNumber: [data[i] ? data[i].securityReferenceNumber : ''],
        branchCode: [data[i] ? data[i].branchCode : ''],
        availableLinkageAccount: [data[i] ? data[i].availableLinkageAccount : ''],
        linkagePercentage: [data[i] ? data[i].linkagePercentage : ''],
        overdraftLimitAmount: [data[i] ? data[i].overdraftLimitAmount : ''],
        limitStartDate: [data[i] ? data[i].limitStartDate : ''],
        limitEndDate: [data[i] ? data[i].limitEndDate : ''],
        rateValue: [data[i] ? data[i].rateValue : ''],
        rate: [data[i] ? data[i].rate : ''],
        spread: [data[i] ? data[i].spread : ''],
        finalAmount: [data[i] ? data[i].finalAmount : ''],
        documentUrl: [data[i] ? data[i].documentUrl : ''],
        fileName: [data[i] ? data[i].documentUrl : '']
      })
      );
    }
  }

  addCFormGroup() {
    this.c.push(this.formBuilder.group({
      collateralType: [''],
      collateralCurrency: [''],
      accountId: this.accountId,
      collateralValue: [''],
      attributes: [''],
      dimensions: [''],
      securityReferenceNumber: ['',],
      branchCode: [''],
      availableLinkageAccount: [''],
      linkagePercentage: [''],
      overdraftLimitAmount: [''],
      limitStartDate: [''],
      limitEndDate: [''],
      rateValue: [''],
      rate: [''],
      spread: [''],
      finalAmount: [''],
      documentUrl: [''],
      fileName: ['']

    }));
  }
  getCollateralType(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayCollateralType = data;
    });
  }
  getCurrencyDetails() {
    this.api.fetchCurrencyMaintAuthRecs().subscribe(resp => {
      this.currency = resp
    })
  }
  overDraftCondition(event: any, i) {
    let overDRaftValue: number = event.target.value;
    console.log(overDRaftValue, this.linkangePercent);
    if (overDRaftValue > this.linkangePercent) {
      console.log("Bigger");
      this.confirmService
        .confirm({ title: "Confirm", message: " Overdraft Limit Amount should be less than Available Linkage Amount. Do you want to Proceed?", ok: "Yes", cancel: "No" })
        .subscribe(res => {
          console.log("Response", res);
          if (res === true) {
            this.cdr.markForCheck();
          } else {
            this.c.at(i).get('overdraftLimitAmount').setValue('');
          };
        });
    }
  }
  fetchBySecurityNum(securityReferenceNum, i) {

    if (!!securityReferenceNum) {
      this.showSpinner = true;
      console.log("Caling", securityReferenceNum);
      this.api.fetchBySecurityNumber(securityReferenceNum).subscribe((res: any) => {
        console.log(res);
        this.showSpinner = false;
        if (res == null) {
          this.issecurityValid = false;
          this.snack.open('Invalid Security Reference Number', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          this.c.at(i).get('securityReferenceNumber').setValue('');
          return;
        } else {
          this.issecurityValid = true;
          this.branchData = res.securedOverDrafts;
          console.log(this.branchData);
          this.branchData.forEach((element, index) => {
            this.branch = this.branchData[index].branchCode;
          });

          this.c.at(i).get('branchCode').setValue(this.branch);
        }
        this.cdr.markForCheck();

      });
    }
  }
  uploadCollateralType(event, i) {
    this.showSpinn = true;
    const file: File = event.target.files[0];
    this.fileName[i] = file.name;
    const data = {
      accountId: this.accountId,
      documentName: this.fileName[i],
    };
    const uploadData = new FormData();
    uploadData.append("data", JSON.stringify(data));
    uploadData.append("file", file);
    this.api.uploadcollateralDoc(uploadData).subscribe((res: any) => {
      console.log("Document Resp", res, res.url);
      if (res && res.url) {
        this.showSpinn = false;
        this.snack.open('Document Saved Successfully!', 'OK', { duration: 4000, verticalPosition: 'top', horizontalPosition: 'right' });
        this.c.at(i).get('documentUrl').setValue(res.url);
        this.c.at(i).get('fileName').setValue(res.fileName);
      }
    });


  }
  deleteDoc(i) {
    this.docUrl = ' ';
    this.fileName = ' ';
    this.c.at(i).get('documentUrl').setValue('');
    this.c.at(i).get('fileName').setValue('');
    this.showDelete = false;
  }
  onSubmit(i) {
    this.c.value.forEach(data => {
      if ((data.linkagePercentage == "" || data.securityReferenceNumber == "" || data.collateralValue == ""
        || data.branchCode == "") && this.securedOverdraftUpdateForm.touched == true) {
        this.snack.open('mandatory details are not filled so need to be filled!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }

      if (data.limitStartDate != null && data.limitStartDate._d != undefined && data.limitStartDate._d != null) {
        data.limitStartDate = this.convertDate(data.limitStartDate._d);
      }

      if (data.limitEndDate != null && data.limitEndDate._d != undefined && data.limitEndDate._d != null) {
        data.limitEndDate = this.convertDate(data.limitEndDate._d);
      }
    }
    )
    let payload = { securedOverDrafts: this.c.value, loggedInUser: this.loggedInUser };

    if (this.securedOverdraftUpdateForm.invalid) {
      console.log("this.securedOverdraftUpdateForm.touched", this.securedOverdraftUpdateForm.touched)

      return;
    }
    this.api.securedDraft(payload).subscribe(resp => {
      this.snack.open('Secured Overdraft Details is already filled, click on Next button to continue!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      this.dialogRef.close({
        status: 'BACKLOG'
      })
    }, err => {
      this.snack.open('Error Unable to Save!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })

  }
  onReset() {
    // reset whole form back to initial state
    this.securedOverdraftUpdateForm.reset();
    this.c.clear();
  }
  removeCollateral(i: number) {
    this.c.controls[i].reset()
  }
  convertDate(value) {
    let date = moment(value).format('YYYY-MM-DDThh:mm:ss'); // ISO 8601 format
    console.log("date", date);
    if (date)
      return date;
  }

  keyUpevent(event, i) {
    console.log("event", event);
    if (event.keyCode === 8) {
      this.onbackSpace = true;
      this.issecurityValid = false;
      this.c.at(i).get('branchCode').setValue('');
    }
    else {
      this.onbackSpace = false;
    }
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }

  }
  calculateLinkagePercent(value, i) {
    this.linkangePercent = value.collateralValue * (value.linkagePercentage / 100);

    this.c.at(i).get('availableLinkageAccount').setValue(this.linkangePercent.toFixed(2));
  }
  calculateFinalrate(value, i) {
    this.finalRatevalue = value.rate * 1 + value.spread * 1;
    console.log(this.linkangePercent, this.c.controls[i]);
    this.c.at(i).get('finalAmount').setValue(this.finalRatevalue);

  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }
}  