import { Component, OnInit, Inject, ChangeDetectorRef } from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-loan-legal-opinion-updates',
  templateUrl: './loan-legal-opinion-updates.component.html',
  styleUrls: ['./loan-legal-opinion-updates.component.scss']
})
export class LoanLegalOpinionUpdatesComponent implements OnInit {
  loanAccountId: any;
  loanaccountID: any;
  loanLegalForm: FormGroup;
  disbursementDetails: any;
  showEditOption: boolean = false;
  isChecked = true;
  disabled = true;
  disabledToggle = true;
  legalDetails: any;
  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;
  
  constructor(private apiService: ApiService, @Inject(MAT_DIALOG_DATA) public data: any, private fb: FormBuilder,
    private loader: AppLoaderService,   private taskSumService: TasksummaryService, private cdr: ChangeDetectorRef,
    private ls: LocalStoreService, public dialogRef: MatDialogRef<LoanLegalOpinionUpdatesComponent>, private snack: MatSnackBar,
  ) { }

  ngOnInit(): void {
    this.loanaccountID = this.data.loanAccountId;
    console.log('loanaccountID', this.loanaccountID)
    this.buildLegalOpinionForm(this.data)
    //this.gettingLegalOpinionDetail();
  }
  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
  //buildDisbursementForm(data?, Response?: boolean)
  buildLegalOpinionForm(data?){
    console.log('item',data)
   
     
    this.loanLegalForm= this.fb.group({
        loanAccountId: [this.loanaccountID],
        opinion:[data ? data?.loanLegalInfo?.opinion:''],
        lawyerName: [data ? data?.loanLegalInfo?.lawyerName : ''],
     // isDecisionFavorable: [data ? data?.loanLegalInfo?.isDecisionFavorable : this.isChecked],
      isDecisionFavorable: [data ? data?.loanLegalInfo?.isDecisionFavorable : ''],
        mktValOfAsset:[data ? data?.loanLegalInfo?.mktValOfAsset:''],
        opinionDate: [data ? data?.loanLegalInfo?.opinionDate : ''],
        validationDate:[data ? data?.loanLegalInfo?.validationDate:''],
        description:[data ? data?.loanLegalInfo?.description:''],
        assetAreaSize: [data ? data?.loanLegalInfo?.assetAreaSize : '']
    });

 
   }
   gettingLegalOpinionDetail() {
    this.loader.open();
    this.apiService.gettingLegalOpinionDetails(this.loanaccountID)
      .subscribe((resp: any) => {
        // if(resp.opinion=="Favourable"){
        //   this.isChecked=true;
        // }
        // else{
        //   this.isChecked=false;
        // }
        this.loader.close();
       console.log(resp);
       
      this.buildLegalOpinionForm(resp);;
    }, (err: any) => {
      this.loader.close();
      
    });
   
  }
  toggle(event){
    // let value = event.target.value;
     console.log("value",event);
     if(event=="Not Favourable"){
       this.disabled = false;
       this.isChecked = false;
       this.disabledToggle = false;
 
     }
     else if(event=="Favourable"){
       this.disabled = true;
       this.isChecked = true;
       this.disabledToggle = true;
     }
     else{
       this.isChecked = false;
     }

     
 
   }
  getDisbursementDetails() {
 
    this.apiService.loanDisbursementFetchById(this.loanaccountID).subscribe((legalDetails) => {
 
      this.legalDetails = legalDetails;
      console.log("this.legalDetails::", this.legalDetails);
    

      this.buildLegalOpinionForm(this.legalDetails);
      this.loanLegalForm.value.opinion = this.legalDetails.opinion;
      this.loanLegalForm.value.opinionDate = this.legalDetails.opinionDate;
      this.loanLegalForm.value.validationDate = this.legalDetails.validationDate;
      this.loanLegalForm.value.mktValOfAsset = this.legalDetails.mktValOfAsset;
      this.loanLegalForm.value.lawyerName = this.legalDetails.lawyerName;
      this.loanLegalForm.value.description = this.legalDetails.description;
      this.loanLegalForm.value.isDecisionFavorable = this.legalDetails.isDecisionFavorable;
       this.loanLegalForm.value.assetAreaSize = this.legalDetails.assetAreaSize;
    }, (err: any) => {
      this.loader.close();
    });
  }

  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
  }

  goToClose(): void {
    this.dialogRef.close();
   
    //  this.loader.open();
   // this.getLoanDetails();
    this.apiService.gettingLegalOpinionDetails(this.loanaccountID).subscribe((legalDetails: any) => {
      console.log("All Account Service", legalDetails);
      this.legalDetails = legalDetails;
      //this.buildformGroup(this.loanAccountDetails);
      console.log("this.accountDetails", this.legalDetails);
      console.log('form',this.loanLegalForm.value)
   });
   
  }
  submitForm1(){
    
   

    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      description:this.loanLegalForm.value.description,
      mktValOfAsset:this.loanLegalForm.value.mktValOfAsset,
      opinion: this.loanLegalForm.value.opinion,  
      opinionDate:this.loanLegalForm.value.opinionDate,
      valuationDate:this.loanLegalForm.value.valuationDate,
      lawyerName: this.loanLegalForm.value.lawyerName,
      isDecisionFavorable: this.loanLegalForm.value.isDecisionFavorable,
      assetAreaSize: this.loanLegalForm.value.assetAreaSize,
      
  
  
    }
    console.log("payload",payload)
    this.apiService.saveLegalOpinion(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
   
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName);

        
    this.loanaccountID = this.data.loanAccountId;
    console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }

}

