  getLoanAccountInfo(loanAccountId: string){

    this.taskSumService.getLoanAccountDetails(loanAccountId).subscribe((data) => {
      console.log("data", data);
      this.loanTaskDetails=data
      this.loanAccount_array = data;
      this.createDataInfo = Object.values(data)[20];
      
      
      // this.loanDetails_array= this.dataResponse.dataResp.loanDetailsInfo
      // console.log(this.loanAccount_array[0].offerAcceptRejectInfo);
      console.log(Object.values(data))
      console.log(Object.values(data)[0])
      this.loanAccount_array= Object.values(data)[0];
      this.assest_array= Object.values(data)[1];
      this.mandate_array = Object.values(data)[4];
      console.log('mandate_array', this.mandate_array.mandateId)
      this.colateral_array= Object.values(data)[6];
      console.log('collateral', this.colateral_array)
      console.log('collateral obj',Object.values(data)[6])
      console.log(Object.values(data))
      this.guranator_array= Object.values(data)[7];
      this.loanInterest_array= Object.values(data)[8];
      this.loanDisbusment_array= Object.values(data)[9];
      this.loanRepayment_array= Object.values(data)[10];
      this.loanCharge_array= Object.values(data)[11];
      this.accountService_array=Object.values(data)[12];
      this.loanCreditScore_array= Object.values(data)[13];
      this.valueofassest_array=  Object.values(data)[14];
      this.legaopninio_array= Object.values(data)[15];
      this.loanassement_array= Object.values(data)[16];
      this.loanapproval_array= Object.values(data)[17];
      this.offerissue_array=  Object.values(data)[18];

      
      if (Object.values(data)[0].status === 'REJECTED') {
        this.approvedStatus = true;
        this.rejectedStatus = true;
        this.editScreen=false;
        this.continueStatus=true;
      } else if (Object.values(data)[0].status === 'APPROVED') {
        this.approvedStatus = true;
        this.rejectedStatus = true;
        this.editScreen=true;
        this.continueStatus=true;
      } else if (Object.values(data)[0].status === 'ONGOING') {
        this.approvedStatus = true;
        this.rejectedStatus = false;
        // this.isOnGoing = status;
        this.editScreen=false;
        this.continueStatus=false ;
      } else if (Object.values(data)[0].status === 'BACKLOG') {
        this.approvedStatus = false;
        this.rejectedStatus = false;
        this.editScreen=false;
        this.continueStatus=true;
      }
    });

  }
