import { Component, OnInit,Inject, ChangeDetectorRef } from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import * as moment from 'moment';
import { DatePipe } from '@angular/common';
import { TasksummaryService } from '../../tasksummary.service';



@Component({
  selector: 'app-loan-repayment-details-update',
  templateUrl: './loan-repayment-details-update.component.html',
  styleUrls: ['./loan-repayment-details-update.component.scss']
})
export class LoanRepaymentDetailsUpdateComponent implements OnInit {
  loanAccountId: any;
  loanaccountID: any;
  loanRepaymentForm: FormGroup;
  disbursementDetails: any;
  selectedDay: number = 1;
  days: any[] = [];
  months: any[] = [];
  selectedYear: number;
  years: any[] = [];
  day: number;
  year: number;
  repaymentDetails: any;
  tomorrow = new Date();

  
  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0
  

  constructor(private apiService: ApiService, @Inject(MAT_DIALOG_DATA) public data: any, private fb: FormBuilder,
  private loader: AppLoaderService, private taskSumService: TasksummaryService, private cdr: ChangeDetectorRef,
    private ls: LocalStoreService, public dialogRef: MatDialogRef<LoanRepaymentDetailsUpdateComponent>, private snack: MatSnackBar,)
  { 
    this.tomorrow.setDate(this.tomorrow.getDate());
  }

  ngOnInit(): void {
    this.selectedYear = new Date().getFullYear();
    for (let year = 0; year <= 50; year++) {
      this.years.push(year+ " year");
    }
    for (let month = 0; month <= 11; month++) {
      this.months.push(month+ " month");
    }
    for (let day = 0; day <= 30; day++) {
      this.days.push(day+ " day");
    }
    this.loanaccountID = this.data.loanAccountId;
    console.log('loanaccountID', this.loanaccountID)
    this.buildRepaymentForm(this.data)
  }

  getRepaymenttDetails() {
      this.loader.open();
      this.apiService.loanRepaymentFetchById(this.loanaccountID).subscribe(
      (repaymentDetails) => {
      this.loader.close();
      this.repaymentDetails = repaymentDetails;
      this.buildRepaymentForm(this.repaymentDetails);
      this.loanRepaymentForm.value.loanTenure = this.repaymentDetails.loanTenure;
      this.loanRepaymentForm.value.maturityeDate = this.repaymentDetails.maturityeDate;
      this.loanRepaymentForm.value.firstRepaymentDate = this.repaymentDetails.firstRepaymentDate;
      this.loanRepaymentForm.value.moratariumPeriod = this.repaymentDetails.moratariumPeriod;
      this.loanRepaymentForm.value.branchCode = this.repaymentDetails.branchCode;
      this.loanRepaymentForm.value.customerAccount = this.repaymentDetails.customerAccount;
      this.loanRepaymentForm.value.bankName = this.repaymentDetails.bankName;
      this.loanRepaymentForm.value.typeOfRepayment = this.repaymentDetails.typeOfRepayment;
      this.loanRepaymentForm.value.repaymentMode = this.repaymentDetails.repaymentMode;
      this.loanRepaymentForm.value.repaymentFrequency = this.repaymentDetails.repaymentFrequency
      },
      (err: any) => {
        this.loader.close();
      }
    );
  }

  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
   buildRepaymentForm(data?) {
    console.log('item',data)
   
     
    this.loanRepaymentForm= this.fb.group({
        loanAccountId: [this.loanaccountID],
        disbursementMode:[data ? data?.loanRepaymentInfo?.disbursementMode:''],
        firstRepaymentDate: [data ? data?.loanRepaymentInfo?.firstRepaymentDate : ''],
        moraturioumPeriod: [data ? data?.loanRepaymentInfo?.moraturioumPeriod : ''],
        typeOfRepayment:[data ? data?.loanRepaymentInfo?.typeOfRepayment:''],
        branchCode: [data ? data?.loanRepaymentInfo?.branchCode : ''],
        customerAccount:[data ? data?.loanRepaymentInfo?.customerAccount:''],
        bankName: [data ? data?.loanRepaymentInfo?.bankName : ''],
        repaymentMode: [data ? data?.loanRepaymentInfo?.repaymentMode : ''],
        repaymentFrequency:[data ? data?.loanRepaymentInfo?.repaymentFrequency:''],
   
    //  firstRepaymentDate: [ data ? data?.loanRepaymentInfo  ? new Date(data?.loanRepaymentInfo.firstRepaymentDate).toISOString().substring(0, 10): ''],
        loanTenure:[data ? data?.loanRepaymentInfo?.loanTenure:''],
        day: [data ? Number(data?.loanRepaymentInfo?.loanTenure.split("-")[2]) : ""],
        month: [data ? data?.info?.loanRepaymentInfo.split("-")[1] : ""],
        year: [data ? Number(data?.loanRepaymentInfo?.loanTenure.split("-")[0]) : ""],
  
 
    });
   }

  goToClose(): void {
    this.dialogRef.close();
    this.apiService.loanRepaymentFetchById(this.loanaccountID).subscribe((repaymentDetails: any) => {
      console.log("All Account Service", repaymentDetails);
      this.repaymentDetails = repaymentDetails;
      console.log("this.accountDetails", this.repaymentDetails);
      console.log('form',this.loanRepaymentForm.value)
   });
   
  }
  submitForm1() {
  
    
    let now = new Date();
    now=this.loanRepaymentForm.value.firstDisbursementDate;
    console.log("firstDisbursementDate",this.loanRepaymentForm.value.firstDisbursementDate);
    
    var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
    console.log("dateStringWithTime",dateStringWithTime);
    const renewData =
    this.loanRepaymentForm.value.year +
    "-" +
    this.loanRepaymentForm.value.month +
    "-" +
    this.loanRepaymentForm.value.day;
  this.loanRepaymentForm.get("loanTenure").setValue(renewData);
  delete this.loanRepaymentForm.value.year;
  delete this.loanRepaymentForm.value.month;
  delete this.loanRepaymentForm.value.day;

    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      typeOfRepayment:this.loanRepaymentForm.value.typeOfRepayment,
      loanTenure:this.loanRepaymentForm.value.loanTenure,
      maturityeDate: this.loanRepaymentForm.value.maturityeDate,  
      firstRepaymentDate: this.loanRepaymentForm.value.firstRepaymentDate,  
      moratariumPeriod:this.loanRepaymentForm.value.moratariumPeriod,
      branchCode:this.loanRepaymentForm.value.branchCode,
      customerAccount: this.loanRepaymentForm.value.customerAccount,
      bankName: this.loanRepaymentForm.value.bankName,
      repaymentMode: this.loanRepaymentForm.value.repaymentMode,
      repaymentFrequency: this.loanRepaymentForm.value.repaymentFrequency
      
  
  
    }
    console.log("payload",payload)
    this.apiService.loanRepaymentSave(payload).subscribe(data => {
      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName);
        
        this.loanaccountID = this.data.loanAccountId;
        console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad(); 
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }

    
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
  }

}

