import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-customer-info-update',
  templateUrl: './customer-info-update.component.html',
  styleUrls: ['./customer-info-update.component.scss']
})
export class CustomerInfoUpdateComponent implements OnInit {

  customerInfoUpdateForm: FormGroup;
  listOfAccounts: any = [];
  arrayOwnership: string[];
  arrayPrefix: string[];
  arrayGender: string[];
  arrayCountry: any[] = [];
  arrayCity: string[] = [];
  arrayState: any[] = [];
  accountId: any;
  arrayResidence: string[];
  arrayDocumentType: string[];
  toggle: boolean;
  index: any;
  isAllSameDigitVal: boolean;
  productCustomerId: any;
  isCifValid: boolean;
  customerIdProofs: any;
  selectedApplicant: any;
  arrayAddressType: string[];

  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<CustomerInfoUpdateComponent>,
    private appApiService: ApplicationEntryStageService,
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private tasksummaryService: TasksummaryService,
    private ls: LocalStoreService,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private loader: AppLoaderService,
    private confirmService: AppConfirmService

  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.accountId = this.ls.getItem('accountId');
    this.productCustomerId = this.ls.getItem('accountDetails.customerId');
    this.getPrefixValue("prefix");
    this.getGenderValue("gender");
    this.getOwnershipValue("ownership");
    this.getResidenceDetails("residence");
    this.getCountryName("country");
    this.getDocumentType("document_type");
    this.getAdressType("address_type");
    if (this.data) {
      this.getStateValueArray(this.data.data[0].userAddress[0].country);
      this.getAllAuthCities();
      this.buildCustomerForm(this.data.data);
      this.addCustFormGroup(this.data.data, this.data.data.length)
    }
    else {
      this.buildCustomerForm();
      this.addCustFormGroup(null, 1)
    }
    console.log(this.customerInfoUpdateForm);

  }
  get f() { return this.customerInfoUpdateForm.controls; }
  get applicantCount() { return this.f.numOfApplicants; }
  get applicant() { return this.f.applicantDetails as FormArray; }

  buildCustomerForm(data?) {
    this.customerInfoUpdateForm = this.formBuilder.group({
      productAccountId: this.accountId,
      ownership: data && data.length > 1 ? 'Join' : 'Self',
      numOfApplicants: data ? data.length.toString() : 1,
      applicantDetails: this.formBuilder.array([])
    });
  }
  getStateValueArray(country){
    this.apiService.getAllCountries().subscribe((data: any) => {
      for(let i=0; i<data.length;i++){     
        if(data[i].countryName == country){
          this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
          this.arrayState = resp
          })
          break;   
        }
      }
    }) ;
  }
  getAllAuthCities(){
    this.apiService.getAuthorizedCityInfo().subscribe(res => res.map(r => this.arrayCity.push(r.cityName)))
    
  }

  convertDate(value) {
    let date = moment(value).format('YYYY-MM-DD'); // ISO 8601 format
    console.log("date", date);
    if (date)
      return date;
  }

  addCustFormGroup(data?, num?) {
    for (let i = 0; i < num; i++) {
      this.applicant.push(this.formBuilder.group({
        existingCustomer: [data[i] ? data[i].existingCustomer : ''],
        primaryCustomer: [data[i] ? data[i].primaryCustomer : ''],
        cifNumber: [data[i] ? data[i].cifNumber : ''],
        prefix: [data[i] ? data[i].prefix : '', Validators.required],
        firstName: [data[i] ? data[i].firstName : '', Validators.required],
        middleName: [data[i] ? data[i].middleName : ''],
        lastName: [data[i] ? data[i].lastName : '', Validators.required],
        gender: [data[i] ? data[i].gender : '', Validators.required],
        dateOfBirth: [data[i] ? data[i].dateOfBirth : '', Validators.required],
        placeOfBirth: [data[i] ? data[i].placeOfBirth : '', Validators.required],
        residenceStatus: [data[i] ? data[i].residenceStatus : '', Validators.required],
        nationality: [data[i] ? data[i].nationality : '', Validators.required],
        countryOfResidence: [data[i] ? data[i].countryOfResidence : '', Validators.required],
        occupationType: [data[i] ? data[i].occupationType : '', Validators.required],
        phoneNumber: [data[i] ? data[i].phoneNumber : '', [Validators.required, Validators.minLength(3), Validators.maxLength(10), Validators.pattern("^[0-9]{10,12}$")]],
        landlineNumber: [data[i] ? data[i].landlineNumber : '', [Validators.required, Validators.pattern("^[0-9]{10,12}$")]],
        primaryEmailAdress: [data[i] ? data[i].primaryEmailAdress : '', [Validators.required, Validators.email]],
        userAddress: this.formBuilder.array([]),
        custIdProofs: this.formBuilder.array([])
      })
      )
      this.addCustIdProofs(i,data[i].custIdProofs[0]);
      this.addAddress(i,data[i].userAddress[0])
    }
  }
  addAddress(index,data?) { 
    (this.applicant.controls[index].get('userAddress') as FormArray).push(this.formBuilder.group({
      address1: [data ? data.address1 : '', [Validators.required]],
      address2: [data ? data.address2 : '', [Validators.required]],
      addressType: [data ? data.addressType : '', [Validators.required]],
      city: [data ? data.city : '', [Validators.required]],
      state: [data ? data.state : '', [Validators.required]],
      country: [data ? data.country : '', [Validators.required]],
      zipCode: [data ? data.zipCode : '', [Validators.required]]
    })
    )

  }
  addCustIdProofs(index,data?) {
    (this.applicant.controls[index].get('custIdProofs') as FormArray).push(this.formBuilder.group({
      idNumber: [data ? data.idNumber : '', Validators.required],
      idType: [data ? data.idType : '', Validators.required],
      expiryDate: [data ? data.expiryDate : '',]
    })
    )
  }
  changeOwner() {
    if (this.customerInfoUpdateForm.get('ownership').value == 'Join') {
      this.customerInfoUpdateForm.get('numOfApplicants').patchValue(2, { onlySelf: true, emitEvent: false });
    } else {
      this.customerInfoUpdateForm.get('numOfApplicants').patchValue(1, { onlySelf: true, emitEvent: false });
    }
  }
  onChangeApplicantNo(event) {
    let val = event.target.value;
    let applicantNo = this.customerInfoUpdateForm.get('numOfApplicants').value
    if (val > applicantNo) {
      this.addCustFormGroup(null, val - applicantNo)
    }
    else {
      for (let i = applicantNo - 1; i >= val; i++)
        this.applicant.removeAt(i);
    }
    this.customerInfoUpdateForm.get('numOfApplicants').setValue(val);
  }
  changeIdType(doc) {
    console.log("doc::", doc)
    console.log("this.customerIdProofs::", this.customerIdProofs)
    let idProofs: any = this.customerIdProofs.filter(item => {
      return item.idType == doc;
    })
    console.log("idProofs::", idProofs)
    if (idProofs.length > 0) {
      this.applicant.get('idNumber').patchValue(idProofs[0].idNumber);
      this.applicant.get('expiryDate').patchValue(this.convertDate(idProofs[0].expiryDate));
    } else {
      this.applicant.get('idNumber').patchValue('');
      this.applicant.get('expiryDate').patchValue('');
    }
  }
  getPrefixValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayPrefix = data;
    });
  }

  getGenderValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayGender = data;
    });
  }

  getOwnershipValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayOwnership = data;
    });
  }

  getCountryName(key: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].authStatus === 'A') {
          this.arrayCountry.push(data[i].countryName);
        }
      }
    });
  }
  getStateName(country) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].countryName === country) {
          this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
            for (let i = 0; i < resp.length; i++) {
              if (resp[i].authStatus === 'A') {
                this.arrayState.push(resp[i].stateName);
              }
            }
          })
          break;
        }
      }
    })
  }

  getCityName(state: any) {
    let entityName = "city";
    console.log(state)
    this.apiService.getDropDownValues1(entityName, state).subscribe((data) => {
      this.arrayCity = data;
    });
  }


  getResidenceDetails(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayResidence = data;
    });
  }

  getDocumentType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayDocumentType = data;
    });
  }
  getAdressType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayAddressType = data;
    });
  }

  onSubmit(formData, index) {
    console.log(formData);

    this.isCifValid = false;
    if (this.customerInfoUpdateForm.invalid) {
      return;
    }
    this.loader.open();

    if (formData.value.idType == 'Passport') {
      formData.value.idType = this.convertDate(formData.value.idType);
    }

    this.appApiService.createAndUpdateAccountants(formData)
      .subscribe((data: any) => {
        console.log("data::", data)
        this.closeDetails();
        this.loader.close();
        console.log("Response of service 2 ---> ", data)
        if (data.status === 200) {
          this.snack.open('Customer Information Details Saved!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          this.customerInfoUpdateForm.get('ownership').patchValue(data.custDependentModel.ownership);
          this.customerInfoUpdateForm.get('numOfApplicants').patchValue(data.custDependentModel.numOfApplicants);
          if (this.customerInfoUpdateForm.get('ownership').value == 'Self' || this.applicant.controls.length - 1 == index) {
                this.dialogRef.close({
                  status: 'BACKLOG'
            })
          }
        }
      }, (err: any) => {
        console.log(err);
        this.loader.close();
        this.snack.open('Error', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      })
  }

  isAllSameDigit(event) {
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      console.log('number :: ', number);
      console.log('isAllSameDigitVal :: ', count, number.length)
      if (count == number.length) {
        console.log('if loop')
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    } else {
      this.isAllSameDigitVal = false;
    }
  }
  letterOnly(event) {
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }

  onKeyPress(event: any) {
    const number = /[0-9\+\-\ ]/;
    let inputCharacter = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !number.test(inputCharacter)) {
      event.preventDefault();
      console.log("event:", event)
    }
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  validateNumber(event) {
    const keyCode = event.keyCode;
    const excludedKeys = [8, 37, 39, 46];
    if (!((keyCode >= 48 && keyCode <= 57) ||
      (keyCode >= 96 && keyCode <= 105) ||
      (excludedKeys.includes(keyCode)))) {
      event.preventDefault();
    }
  }
  onSave(){
    this.dialogRef.close({
      status:"BACKLOG"
    })
  }
  closeDetails(): void {
    this.selectedApplicant = null;
    this.applicant.reset();
    this.customerInfoUpdateForm.get('numOfApplicants').enable({ onlySelf: true });
  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }
}

