@Component({
  selector: 'app-corporate-approver',
  templateUrl: './corporate-approver.component.html',
  styleUrls: ['./corporate-approver.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class CorporateApproverComponent implements OnInit {
  @Input("initiatedArr") initiatedArr: any;

  // custOnBoardingApprovet:CustOnBoardingApprover;
  selectedIndex: number = 0;
  imgUrl: string | ArrayBuffer;
  profilePicture: any;
  proofDocForm: FormGroup;
  userAddressForm: FormGroup;
  otherDocForm: FormGroup;
  proofDoc_array: FormArray;
  otherDoc_array: FormArray;
  userAddress_array: FormArray;
  directorsInfo_array: FormArray;
  addNewProof: boolean = false;
  arrayPrefix: any[] = [];
  arrayGender: any[] = [];
  arrayMaritalStatus: any[] = [];
  arrayCountry: any[] = [];
  arrayResidence: any[] = [];
  arrayDocumentType: any[] = [];
  arrayEmploymentType: any[] = [];
  arrayEmployerName: any[] = [];
  otherDocumentArray: any[] = [];
  proofDocumentArray: any[] = [];
  accountId: any;
  primaryInfo: any;

  @ViewChild('ncForm') ncForm: NgForm;
  // image: string | ArrayBuffer;
  image: Object;
  custIdProofs_array: any;
  signatureDetails_array: any;
  status: any;
  rejectedStatus: boolean;
  approvedStatus: boolean;
  statusUpdate: any;
  document_type: string;
  custOtherDoc_array: any;
  custDoc_array:any;
  corporateId: any;
  documentName: any;
  customerColorCode: any;
  phoneNum: any;
  emailID: any;
  errorStatus: any;
  imageUrl: any;
  loading: boolean= true;
  noOFSigns: any;
  newImageurl: any;
  editScreen: boolean;
  isOnGoing : string = "";

  constructor(private tasSumService: TasksummaryService,
    public cdr: ChangeDetectorRef,private router:Router,
    private ls: LocalStoreService,
    private apiService:ApiService,
    public dialogRef: MatDialogRef<CorporateApproverComponent>,
    @Inject(MAT_DIALOG_DATA) public dataResponse: any,private sanitizer: DomSanitizer,
    private snack: MatSnackBar,
    private dialog: MatDialog,
    private jwtService: JwtAuthService
  ) { }

  ngOnInit(): void {

    setTimeout(() => {
      this.loading = true;
      this.corporateId=this.dataResponse.dataResp.corporateId;
      this.ls.setItem('corporateId',this.corporateId
      )
      this.custDoc_array=this.dataResponse.dataResp.corporateDocs;
      
     // this.getCustomerDocumentsDetails(this.corporateId);
      this.getCustomerDocumentsDetails1(this.corporateId);
    // this.phoneNum = this.dataResponse.dataResp.phoneNumber;
    // this.emailID = this.dataResponse.dataResp.primaryEmailAdress;
    this.phoneNum = this.dataResponse.dataResp.category;
    this.emailID = this.dataResponse.dataResp.companyName;
    
    this.customerColorCode=localStorage.getItem('CustomerColorCode');
    this.apiService.getCoropratedetails(this.corporateId)
    .subscribe((data:any) => {
      this.imageUrl =  data.profileImage;
    });
   
    this.status = this.dataResponse.dataResp.corporateStatus;
    this.errorStatus=this.dataResponse.dataResp.custStatus;
    // this.userAddress_array = this.dataResponse.dataResp.corporateAddress;
    this.userAddress_array = this.dataResponse?.dataResp?.customerInfo[0]?.userAddress;
    this.custIdProofs_array = this.dataResponse.dataResp.custIdProofs;
    this.directorsInfo_array = this.dataResponse.dataResp.customerInfo
    console.log("status::",this.status)
    if (this.status === 'REJECTED') {
      this.approvedStatus = true;
      this.rejectedStatus = true;
      this.editScreen=false;
    } else if (this.status === 'APPROVED') {
      this.approvedStatus = true;
      this.rejectedStatus = true;
      this.editScreen=true;
    } else if (this.status === 'UNDER_PROCESS') {
      this.approvedStatus = true;
      this.rejectedStatus = false;
      this.isOnGoing = this.status;
      this.editScreen=false;
    } else if (this.status === 'INITIATED') {
      this.approvedStatus = false;
      this.rejectedStatus = false;
      this.editScreen=false;
    }
    // localStorage.setItem('kycStatus',this.customerDetails[0].customerAdditionalInfo.kycStatus);
    // console.log(this.dataResponse.dataResp.corporateId);
    this.getProfileImage(this.dataResponse.dataResp.corporateId);
    // this.fetchSignatureDetails(this.dataResponse.dataResp.corporateId);
    },5000);
  }
  goToCorporate(){
    console.log("Navigating to edit screen");
    this.router.navigateByUrl('/onboardingCorporate/corporate');
    console.log(this.dataResponse);
    this.ls.setItem('EDIT-CORPORATEID', this.dataResponse.dataResp.corporateId);
  //  console.log(this.ls.getItem('EDIT-KYCDETAILS'));

    // const navigationExtras: NavigationExtras = {
    //   queryParams: {
    //     // 'id': this.modifyUserObject.id,
    //     kyc_Id:this.dataResponse.dataResp.kycReference,
       
    //   },
    // };

    // this.apiService.sendNavParam(navigationExtras);
    this.dialogRef.close({
      message: 'Confirm',
   });
 
  }
  getCustomerDocumentsDetails(corporateId: string) {
    this.document_type= "3";
    this.apiService.getCustomerDocuments(corporateId,this.document_type).subscribe((result) => {
      this.loading=false;
      this.custOtherDoc_array = result;
      if(result != null){
      this.documentName = this.custOtherDoc_array[0].fileName;
      // console.log("custOtherDoc_array::",this.custOtherDoc_array)
    }
    });
  }
   
  getCustomerDocumentsDetails1(corporateId: string) {
    this.document_type= "2";
    this.apiService.getCustomerDocuments(corporateId,this.document_type).subscribe((result) => {
      this.loading=false;
      this.custOtherDoc_array = result;
      if(result != null){
      this.documentName = this.custOtherDoc_array[0].fileName;
      // console.log("custOtherDoc_array::",this.custOtherDoc_array)
    }
    });
  }
     

  
  updateApplicationStatus(formData, status, isEdit?) {
    formData.dataResp.corporateStatus = status;
    this.statusUpdate=status;
    this.apiService.updateCorporateSatatus(formData.dataResp.corporateId, status)
      .subscribe(data => {
        if(data != null ){
            this.cdr.markForCheck();
            this.cdr.detectChanges();
            if(isEdit != true){
            this.dialogRef.close({
              message: 'Confirm',
              recordStatus:status,
              data
            });
          }
          this.snack.open(`${status} `, 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
      },error=>{
        console.log('error resp :: ',error);
        this.errorStatus=status;
        this.snack.open(`${error.error.message} `, 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
  }

  close(data) {
    // this.dialogRef.close();
    data.recordStatus=this.statusUpdate
    this.dialogRef.close({
      message: 'Confirm',
      data,
      recordStatus:this.status
   });
  //  console.log("conf data", data);
  }
  getProfileImage(corporateId){
    this.apiService.getCoropratedetails(corporateId)
    .subscribe((data:any) => {
    console.log(data);      
      if(data.profileImage!=null){
        //let objectURL = 'data:image/jpeg;base64,' + data.profileImage.fileData;
        let objectURL = data.profileImage.fileUrl;
        this.image = this.sanitizer.bypassSecurityTrustUrl(objectURL)
        }
      // console.log(this.image,"Profile Data");
      // console.log(this.profileData.customerName,"Profile Data");
      // const blob = base64StringToBlob(this.profileData.fileData, contentType);
      // console.log(blob);
    },(error:any) => {
      // console.log(error);
    });
  }
  // fetchSignatureDetails(corporateId){
  //   console.log("inside fetchSignatureDetails method");
  //   this.apiService.getSignatureDocuments(corporateId)
  //   .subscribe((data:any) => {
  //      console.log(data);
  //       this.noOFSigns = data[0].noOfSignatures;
  //      this.signatureDetails_array = data;
  //   },(error:any) => {
  //      console.log(error);
  //   });
  // }

openEditScreen(screen,data){
  this.status = 'UNDER_PROCESS'
  this.updateApplicationStatus(this.dataResponse, "UNDER_PROCESS",true)
  const dialogRef = this.dialog.open(screen, {
    panelClass: 'full-width-dialog',
    data: data,
    width: '80%',
    disableClose: true
  });
  dialogRef.afterClosed().subscribe((submit) => {
    console.log(submit);
    this.status = submit.status;
    if (submit.status == 'INITIATED') {
      this.status = 'INITIATED'
      this.updateApplicationStatus(this.dataResponse, "INITIATED",true)
      Object.assign(data,submit.resp)
    }
  })
  
}
editBasicInfo(){
this.openEditScreen(UpdateCorporateBasicInfoComponent,this.dataResponse.dataResp)
}
editDirectorDetails(){
this.openEditScreen(UpdateDirectorDetailsComponent,this.directorsInfo_array)
}
editContactInfo(){
this.openEditScreen(UpdateCorporateContactInfoComponent,this.userAddress_array)
}
editProofOfIdentity(){
this.openEditScreen(UpdateCorporateIdentityComponent,this.dataResponse.dataResp)
}
editOtherInfo(){
this.openEditScreen(UpdateCorporateAddititonalInfoComponent,this.dataResponse.dataResp)
}
continue(){    
  this.ls.setItem('corporateId', this.dataResponse.dataResp.corporateId);
  const theme=null;
  const type ="CORPORATE";
  window.open(
  `${environment.redirectUrl}?code=${this.jwtService.getEncryptedToken()}&appType=${type}&theme=${theme}`,
  '_blank'  
  );
   this.dialogRef.close();
  
}
}