import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-card-interest-details-update',
  templateUrl: './card-interest-details-update.component.html',
  styleUrls: ['./card-interest-details-update.component.scss']
})
export class CardInterestDetailsUpdateComponent implements OnInit {
  cardInterestUpdateForm: FormGroup;
  accountType: String = "Card Origin";
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private formBuilder: FormBuilder,
    private api: ApiService,
    private snack: MatSnackBar,
    public dialogRef: MatDialogRef<CardInterestDetailsUpdateComponent>
  ) { }

  ngOnInit(): void {
    this.cardInterestUpdateForm = this.formBuilder.group({
      interestInfo: new FormArray([]),
    });
    console.log(this.data);
    if(this.data.interestInfo){
      this.onChangeInterest(this.data.interestInfo.length);
      this.in.patchValue(this.data.interestInfo);
    }
    else{
    this.api
    .getFlexcubeDetails("intrestDetails", this.accountType)
    .subscribe((response) => {
      this.onChangeInterest(response.length);
      this.in.patchValue(response);
    });
  }
  console.log(this.cardInterestUpdateForm);
  
  }
  onChangeInterest(e) {
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: ["Fixed Rate", Validators.required],
            intrestRateApplicable: [5.0, Validators.required],
            marginIn :[null,[Validators.required, Validators.max(100), Validators.min(0)],],
            effectiveRate: [10.0,[Validators.required, Validators.max(100), Validators.min(0)],],
            cardInterestId: [""],
            cardId: [this.data.cardId],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }
  onSubmit() {
    console.log(this.cardInterestUpdateForm
.value)
    this.api.cardintrestDetailsSave(this.cardInterestUpdateForm
.value).subscribe(
      (resp) => {
        this.snack.open("Interest Details Saved!", "OK", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
        console.log("backend response from intrest saving", resp);
      },
      (err) => {
        this.snack.open(
          "Not able to connect to server!",
          "INTERNAL SERVER ERROR",
          {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          }
        );
      }
    );
    
  }
  get f() {
    return this.cardInterestUpdateForm.controls;
  }
  get in() {
    return this.f.interestInfo as FormArray;
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  close(){
    this.dialogRef.close({
      status:"ONGOING"
    })
  }
}
