  noDataFound: boolean = false;
  redirectedFromTellerSearch: boolean;

  constructor(public dialog: MatDialog, private ls: LocalStoreService, private taskSumService: TasksummaryService,
    public cdr: ChangeDetectorRef, private apiService: ApiService, private snack: MatSnackBar, private sanitizer: DomSanitizer, private route: ActivatedRoute) {
    this._setSearchSubscription();
  }
  @HostListener('scroll', ['$event'])
  onScroll(event: any) {
    if (this.isScroll) {
      this.BACKLOGPage++;
      this.getDataOnScroll("BACKLOG", this.BACKLOGPage, this.BACKLOGTotalPages);
      this.ONGOINGPage++;
      this.getDataOnScroll("ONGOING", this.ONGOINGPage, this.ONGOINGTotalPages);
      this.APPROVEDPage++;
      this.getDataOnScroll("APPROVED", this.APPROVEDPage, this.APPROVEDTotalPages);
      this.REJECTEDPage++;
      this.getDataOnScroll("REJECTED", this.REJECTEDPage, this.REJECTEDTotalPages);
    }
  }

  ngOnInit(): void {
    var task: boolean = true;

    this.apiService.setTask(task);
    this.accountId = this.ls.getItem('accountId');
    this.route.queryParamMap.subscribe((params: any) => {
      if (params.get('searchTerm')) {
        this.redirectedFromTellerSearch = true
        this.searchText = params.get('searchTerm')
        //this.updateSearch(this.searchText);
        console.log(this.searchText);

        setTimeout(() => {
          this.openSearchedApprover()
        }, 3000);
      }
      else {
        /* @called :: on screen load */
        this.getDataOnLoad();
      }
    })



    this.listOfOptions.filter(item => {
      if (item.checked) {
        this.radioButtonSelected = item.value;
      }
    });

    this.criteria = {
      property: this.radioButtonSelected,
      descending: true
    };
  }
  radioChange(event: MatRadioChange, data) {
    this.radioButtonSelected = data.value;
    this.criteria.property = this.radioButtonSelected;
  }

  public updateSearch(searchTextValue: string) {
    console.log("searchTextValue::", searchTextValue)
    this._searchSubject.next(searchTextValue);
  }
  private _setSearchSubscription() {
    this._searchSubject.pipe(
      debounceTime(1000)
    ).subscribe((searchValue: string) => {
      this.setValue.emit(searchValue);
      console.log('search value :: ', searchValue)
      console.log('search text :: ', this.searchText);
      if (searchValue == null || searchValue == '' || searchValue == "") {
        this.noDataFound = false;
        this.searchable = false;
        this.getDataOnLoad();
        return;
      }
      this.searchcorporateID(searchValue);
    });
  }

  clear() {
    this.searchText = "";
    this.isScroll = true;
    this.getDataOnLoad();
  }

  /* @method :: to load all data on screen load */
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }
  }
  fetchCustomerInfo(status: string) {

    this.taskSumService.getallLoanAccountData(status)
      .subscribe((custResp: any) => {
        console.log("Response", custResp);
        this.updateStatusResponse(status, custResp);
      });
  }

  updateStatusResponse(status, data) {
    console.log("data", data)
    console.log("status", status)
    if (status == 'BACKLOG') {
      this.BACKLOGArr = data?.loanList;
      this.BACKLOGTotalPages = data?.totalPages;
      this.BACKLOGTotalRecords = data?.totalNoOfElements;
    } else if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    } else if (status == 'APPROVED') {
      this.APPROVEDArr = data?.loanList;
      this.APPROVEDTotalPages = data?.totalPages;
      this.APPROVEDTotalRecords = data?.totalNoOfElements;
    } else if (status == 'REJECTED') {
      this.REJECTEDArr = data?.loanList;
      this.REJECTEDTotalPages = data?.totalPages;
      this.REJECTEDTotalRecords = data?.totalNoOfElements;
      console.log("this.REJECTEDArr", this.REJECTEDArr);
    }

    this.cdr.markForCheck();
  }


  /* @method :: to load data on scroll of data*/
  onScrollingFinished(e, status) {
    if (status == 'BACKLOG') {
      this.BACKLOGPage++;
      this.getDataOnScroll(status, this.BACKLOGPage, this.BACKLOGTotalPages);

    } else if (status == 'ONGOING') {
      this.ONGOINGPage++;
      this.getDataOnScroll(status, this.ONGOINGPage, this.ONGOINGTotalPages);

    } else if (status == 'APPROVED') {
      this.APPROVEDPage++;
      this.getDataOnScroll(status, this.APPROVEDPage, this.APPROVEDTotalPages);
    } else if (status == 'REJECTED') {
      this.REJECTEDPage++;
      this.getDataOnScroll(status, this.REJECTEDPage, this.REJECTEDTotalPages);
    }
  }

  /* @method :: to call service ,when scroll method called */
  getDataOnScroll(status, page, totalPage) {
    console.log('---------scrolled----------')
    if (page === totalPage) {
      console.log('No scroll requied')
      return;
    }

    this.taskSumService.getAllLoanDetailOnScroll(status, page)
      .subscribe(data => {
        console.log('count data :: ', data);
        this.cdr.markForCheck();
        this.updateDataOnScroll(status, data.loanList);
      });

  }

  goToLoanAccountSummary(data, status, colCode?) {

    localStorage.setItem('CustomerColorCode', colCode);
    const dialogRef = this.dialog.open(LoanAccountApproverComponent,
      {
        panelClass: 'full-width-dialog',
        data: { dataResp: data, recordStatus: status },
        width: '80%',
        disableClose: false
      });
    dialogRef.afterClosed().subscribe((submit) => {
      console.log('dialog closed:: ', submit)
      // if (submit.recordStatus === 'REJECTED' || submit.recordStatus === 'APPROVED'
      //   || submit.data.kycStatus === 'REJECTED' || submit.data.kycStatus === 'APPROVED') {
      //   this.getDataOnLoad()
      // }
      if (this.redirectedFromTellerSearch == true) {
        this.updateSearch(this.searchText)
      }
      else {
        this.getDataOnLoad()
      }

    })
  }

