import { Component, OnInit,Inject, ChangeDetectorRef } from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-loan-assessment-details-update',
  templateUrl: './loan-assessment-details-update.component.html',
  styleUrls: ['./loan-assessment-details-update.component.scss']
})
export class LoanAssessmentDetailsUpdateComponent implements OnInit {
  loanAccountId: any;
  loanaccountID: any;
  loanAssessmentUpdate: FormGroup;
  disbursementDetails: any;
  selectedDay: number = 1;
  day: number;
  days: any[] = [];
  months: any[] = [];
  selectedYear: number;
  year: number;
  years: any[] = [];
  arraySystemRecommendation:any[]=['Approved','Rejected'];
  arrayUserRecommendation:any[]=['Approved','Rejected'];
  assessmentDetails: any;

  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;
  
  constructor(private loader: AppLoaderService, public apiService: ApiService, @Inject(MAT_DIALOG_DATA) public data: any,
    private fb: FormBuilder, private ls: LocalStoreService, public dialogRef: MatDialogRef<LoanAssessmentDetailsUpdateComponent>,
    private snack: MatSnackBar,private taskSumService: TasksummaryService, private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
  this.selectedYear = new Date().getFullYear();
  
    for (let year = 0; year <= 50; year++) {
      this.years.push(year+ " year");
    }

    for (let month = 0; month <= 11; month++) {
      this.months.push(month+ " month");
    }

    for (let day = 0; day <= 30; day++) {
      this.days.push(day+ " day");
    }
    this.loanaccountID = this.data.loanAccountId;
    console.log('loanaccountID', this.loanaccountID)
    this.buildDisbursementForm(this.data)
  }
  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
   buildDisbursementForm(data?) {
    console.log('item',data)
   
     
    this.loanAssessmentUpdate= this.fb.group({
        loanAccountId: [this.loanaccountID],
        loanAmountRecommendation: [data ? data?.loanAssessmentInfo?.loanAmountRecommendation : ''],
        approvedLoanAmount: [data ? data?.loanAssessmentInfo?.approvedLoanAmount : ''],
        systemRecommendation: [data ? data?.loanAssessmentInfo?.systemRecommendation : ''],
        userRecommendation:[data ? data?.loanAssessmentInfo?.userRecommendation:''],
        rateOfInterest:[data ? data?.loanAssessmentInfo?.rateOfInterest:''],
        requestedLoanAmount: [data ? data?.loanAssessmentInfo?.requestedLoanAmount : ''],
        loanTenure: [data ? data?.loanAssessmentInfo?.loanTenure : ''],
        day: [data ? (data?.loanAssessmentInfo?.loanTenure.split("-")[2]) : ""],
        month: [data ? data?.loanAssessmentInfo?.loanTenure.split("-")[1] : ""],
        year: [data ? (data?.loanAssessmentInfo?.loanTenure.split("-")[0]) : ""],
  
 
    });

    // console.log('form before',this.loanAssetForm)
    // console.log('form aftr',this.loanAssetForm.value)
  }
  getDisbursementDetails() {
 
    this.apiService.fetchLoanAssessment(this.loanaccountID).subscribe((assessmentDetails) => {
 
      this.assessmentDetails = assessmentDetails;
      console.log("this.assessmentDetails::", this.assessmentDetails);
    

      this.buildDisbursementForm(this.disbursementDetails);
      this.loanAssessmentUpdate.value.requestedLoanAmount = this.assessmentDetails.requestedLoanAmount;
      this.loanAssessmentUpdate.value.rateOfInterest = this.assessmentDetails.rateOfInterest;
      this.loanAssessmentUpdate.value.userRecommendation = this.assessmentDetails.userRecommendation;
      this.loanAssessmentUpdate.value.systemRecommendation = this.assessmentDetails.systemRecommendation;
      this.loanAssessmentUpdate.value.loanTenure = this.assessmentDetails.loanTenure;
      this.loanAssessmentUpdate.value.approvedLoanAmount = this.assessmentDetails.approvedLoanAmount;
      this.loanAssessmentUpdate.value.loanAmountRecommendation = this.assessmentDetails.loanAmountRecommendation;
    ;
    }, (err: any) => {
      this.loader.close();
    });
  }

  goToClose(): void {
    this.dialogRef.close();

    this.apiService.fetchLoanAssessment(this.loanaccountID).subscribe((assessmentDetails: any) => {
      console.log("All Account Service", assessmentDetails);
      this.assessmentDetails = assessmentDetails;
 
      console.log("this.assessmentDetails", this.assessmentDetails);
      console.log('form',this.loanAssessmentUpdate.value)

   });
   
  }
  submitForm1(){
    
    const renewData =
    this.loanAssessmentUpdate.value.year +
    "-" +
    this.loanAssessmentUpdate.value.month +
    "-" +
    this.loanAssessmentUpdate.value.day;
  this.loanAssessmentUpdate.get("loanTenure").setValue(renewData);
  delete this.loanAssessmentUpdate.value.year;
  delete this.loanAssessmentUpdate.value.month;
  delete this.loanAssessmentUpdate.value.day;

    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      rateOfInterest:this.loanAssessmentUpdate.value.rateOfInterest,
      requestedLoanAmount:this.loanAssessmentUpdate.value.requestedLoanAmount,
      loanTenure: this.loanAssessmentUpdate.value.loanTenure,  
      userRecommendation:this.loanAssessmentUpdate.value.userRecommendation,
      systemRecommendation:this.loanAssessmentUpdate.value.systemRecommendation,
      approvedLoanAmount: this.loanAssessmentUpdate.value.approvedLoanAmount,
      loanAmountRecommendation: this.loanAssessmentUpdate.value.loanAmountRecommendation
     
  
  
    }
    console.log("payload",payload)
    this.apiService.updateAssessmentDetails(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName);
 
    this.loanaccountID = this.data.loanAccountId;
    console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }

  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
  
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
}

}








