import { CdkRow } from '@angular/cdk/table';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-card-initiation-details-update',
  templateUrl: './card-initiation-details-update.component.html',
  styleUrls: ['./card-initiation-details-update.component.scss']
})
export class CardInitiationDetailsUpdateComponent implements OnInit {
cardInitiationUpdateForm: FormGroup;
employmentTypeArray: any[];
localCurrencyArr = ['CNY', 'GBP', 'INR', 'USD', 'ZAR', 'GBP'];

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any, 
    public dialogRef: MatDialogRef<CardInitiationDetailsUpdateComponent>,
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private router: Router,
    private snack: MatSnackBar
    ) { }

  ngOnInit(): void {
    this.getEmploymentType("employment_type");
    console.log(this.data);
    
    if(this.data){
      this.buildInitiationUpdateForm(this.data);
    }
    else
    {
      this.buildInitiationUpdateForm();
    }
    
  }
  getEmploymentType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.employmentTypeArray = data;

    });
  }
  buildInitiationUpdateForm(data?) {
  
    this.cardInitiationUpdateForm = this.formBuilder.group({
      existingCustomer: [data && data.existingCustomer? data.existingCustomer : '',],
      cardType: [data && data.cardType ? data.cardType : '', [Validators.required]],
      customerAccount: [data && data.customerAccount ? data.customerAccount : ''],
      applicantName: [data && data.applicantName ? data.applicantName : ''],
      currency: [data && data.currency ? data.currency : '', [Validators.required]],
      employmentType: [data && data.employmentType ? data.employmentType : '', [Validators.required]],
      affinityProgram: [data && data.affinityProgram ? data.affinityProgram : '', [Validators.required]]  
    });
  }
close(){
  this.dialogRef.close({
    status:"ONGOING"
  })
}

onSubmit() {
  let payload = {
    existingCustomer: this.cardInitiationUpdateForm.value.existingCustomer,
    cardType: this.cardInitiationUpdateForm.value.cardType,
    customerAccount: this.cardInitiationUpdateForm.value.customerAccount,
    applicantName: this.cardInitiationUpdateForm.value.applicantName,
    currency: this.cardInitiationUpdateForm.value.currency,
    employmentType: this.cardInitiationUpdateForm.value.employmentType,
    affinityProgram: this.cardInitiationUpdateForm.value.affinityProgram,
    nameOnCard: this.data.nameOnCard,
    status: this.data.status,
    kycStatus: this.data.kycStatus,
    kycReference: this.data.kycRefrence,
    customerId: this.data.customerId,

    cardId: this.data.cardId,
  }
  this.apiService.uploadCardInitiation(payload)
    .subscribe((data: any) => {
      this.snack.open('Data Updated!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      let statusModel = {
        cardId: data.cardId,
        status: "BACKLOG"
      };
      this.apiService.updateCardStatus(statusModel).subscribe(data => {
        this.dialogRef.close({
          status: data.status
         });
      });
    }, err => {
      this.snack.open('Error Unable to Update Data!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    });
  
}

}
