import { MatSnackBar } from '@angular/material/snack-bar';
import { ChangeDetectorRef, Component, EventEmitter, HostListener, OnInit, Output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { ApproverComponent } from '../approver/approver.component';
import { CustOnBoardingApproverComponent } from '../cust-on-boarding-approver/cust-on-boarding-approver.component';
import { TasksummaryService } from '../tasksummary.service';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { DomSanitizer } from '@angular/platform-browser';
import { MatRadioChange } from '@angular/material/radio';
import { isNumber } from 'highcharts';
import { SortCriteria } from 'app/shared/pipes/custom-task-summary-sorting.pipe';
import * as _ from 'lodash';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-cust-on-boarding-task-summary',
  templateUrl: './cust-on-boarding-task-summary.component.html',
  styleUrls: ['./cust-on-boarding-task-summary.component.scss']
})

export class CustOnBoardingTaskSummaryComponent implements OnInit {

  searchText: any;
  statusArr: any = ['INITIATED', 'UNDER_PROCESS', 'APPROVED', 'REJECTED']
  arr: any = ['INITIATEDArr', 'UNDER_PROCESSArr', 'APPROVEDArr', 'REJECTEDArr']
  INITIATEDArr: any = [];
  UNDER_PROCESSArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];

  INITIATEDTotalPages: number = 0
  UNDER_PROCESSTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  INITIATEDCount: number = 0
  UNDER_PROCESSCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  INITIATEDImage: any[] = [];
  UNDER_PROCESSImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  INITIATEDTotalRecords: number = 0;
  UNDER_PROCESSTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  INITIATEDPage: number = 0;
  UNDER_PROCESSPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;

  loading: boolean;

  image: Object;
  searchable: boolean = false;
  isScroll: boolean = true
  //variable for sorting data
  public criteria: SortCriteria;

  @Output() setValue: EventEmitter<string> = new EventEmitter();
  private _searchSubject: Subject<string> = new Subject();
  radioButtonSelected: any;

  public listOfOptions: any = [
    { label: 'Customer ID ', value: 'customerId', checked: true },
    { label: 'Name ', value: 'firstName', checked: false }
  ];
  noDataFound: boolean = false;
  redirectedFromTellerSearch: boolean = false;

  constructor(public dialog: MatDialog, private ls: LocalStoreService, private tasSumService: TasksummaryService,
    public cdr: ChangeDetectorRef, private apiService: ApiService, private snack: MatSnackBar, private sanitizer: DomSanitizer,
    private router: Router, private route: ActivatedRoute) {
    this._setSearchSubscription();
  }
  @HostListener('scroll', ['$event'])
  onScroll(event: any) {
    if (this.isScroll) {
      this.INITIATEDPage++;
      this.getDataOnScroll("INITIATED", this.INITIATEDPage, this.INITIATEDTotalPages);

      this.UNDER_PROCESSPage++;
      this.getDataOnScroll("UNDER_PROCESS", this.UNDER_PROCESSPage, this.UNDER_PROCESSTotalPages);

      this.APPROVEDPage++;
      this.getDataOnScroll("APPROVED", this.APPROVEDPage, this.APPROVEDTotalPages);

      this.REJECTEDPage++;
      this.getDataOnScroll("REJECTED", this.REJECTEDPage, this.REJECTEDTotalPages);
    }
  }

  ngOnInit(): void {
    var task: boolean = true;
    this.apiService.setTask(task);

    this.getDataOnLoad();

    this.listOfOptions.filter(item => {
      if (item.checked) {
        this.radioButtonSelected = item.value;
      }
    });

    this.criteria = {
      property: this.radioButtonSelected,
      descending: true
    };
  }

  radioChange(event: MatRadioChange, data) {
    this.radioButtonSelected = data.value;
    this.criteria.property = this.radioButtonSelected;
  }

  public updateSearch(searchTextValue: string) {
    this._searchSubject.next(searchTextValue);
  }

  private _setSearchSubscription() {
    this._searchSubject.pipe(
      debounceTime(1000)
    ).subscribe((searchValue: string) => {
      this.setValue.emit(searchValue);
      console.log('search value :: ', searchValue)
      console.log('search text :: ', this.searchText);
      if (searchValue == null || searchValue == '' || searchValue == "") {
        this.noDataFound = false;
        this.searchable = false;
        this.getDataOnLoad();
        return;
      }
      this.searchCustomerId(searchValue);
    });
  }

  clear() {
    this.searchText = "";
    this.isScroll = true;
    this.getDataOnLoad();
  }

  /* @method :: to load all data on screen load */
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerByStatus(statusItem, 0, 0);
    }
  }

  fetchCustomerByStatus(status: string, page: number, size: number) {
    this.tasSumService.getAllCustOnBoardingData(status, page)
      .subscribe((custResp: any) => {
        console.log("Response", custResp);
        this.updateStatusResponse(status, custResp);
      });
  }
  GoToCorporate() {
    setTimeout(() => {
      this.router.navigateByUrl('tasks/corporate-task-summary');
    }, 300);
  }

  updateStatusResponse(status, data) {
    if (status == 'INITIATED') {
      this.INITIATEDArr = data.customerInfoList;
      this.INITIATEDTotalPages = data.totalPages;
      this.INITIATEDTotalRecords = data.totalRecords;
    } else if (status == 'UNDER_PROCESS') {
      this.UNDER_PROCESSArr = data.customerInfoList;
      this.UNDER_PROCESSTotalPages = data.totalPages;
      this.UNDER_PROCESSTotalRecords = data.totalRecords;
    } else if (status == 'APPROVED') {
      this.APPROVEDArr = data.customerInfoList;
      this.APPROVEDTotalPages = data.totalPages;
      this.APPROVEDTotalRecords = data.totalRecords;
    } else if (status == 'REJECTED') {
      this.REJECTEDArr = data.customerInfoList;
      this.REJECTEDTotalPages = data.totalPages;
      this.REJECTEDTotalRecords = data.totalRecords;
      console.log("this.REJECTEDArr", this.REJECTEDArr);
    }

    this.cdr.markForCheck();
  }


  /* @method :: to load data on scroll of data*/
  onScrollingFinished(e, status) {
    if (status == 'INITIATED') {
      this.INITIATEDPage++;
      this.getDataOnScroll(status, this.INITIATEDPage, this.INITIATEDTotalPages);

    } else if (status == 'UNDER_PROCESS') {
      this.UNDER_PROCESSPage++;
      this.getDataOnScroll(status, this.UNDER_PROCESSPage, this.UNDER_PROCESSTotalPages);

    } else if (status == 'APPROVED') {
      this.APPROVEDPage++;
      this.getDataOnScroll(status, this.APPROVEDPage, this.APPROVEDTotalPages);
    } else if (status == 'REJECTED') {
      this.REJECTEDPage++;
      this.getDataOnScroll(status, this.REJECTEDPage, this.REJECTEDTotalPages);
    }
  }

  /* @method :: to call service ,when scroll method called */
  getDataOnScroll(status, page, totalPage) {
    console.log('---------scrolled----------')
    if (page === totalPage) {
      console.log('No scroll requied')
      return;
    }

    this.tasSumService.getAllCustOnBoardingData(status, page)
      .subscribe(data => {
        console.log('count data :: ', data);
        this.cdr.markForCheck();
        this.updateDataOnScroll(status, data.customerInfoList);
      });

  }

  goToCustOnBoardingSummary(data, status, colCode?) {

    localStorage.setItem('CustomerColorCode', colCode);
    const dialogRef = this.dialog.open(CustOnBoardingApproverComponent,
      {
        panelClass: 'full-width-dialog',
        data: { dataResp: data, recordStatus: status },
        width: '80%',
        disableClose: false
      });
    dialogRef.afterClosed().subscribe((submit) => {
      console.log('dialog closed:: ', submit)
      if (submit.recordStatus === 'REJECTED' || submit.recordStatus === 'APPROVED' || submit.recordStatus === 'INITIATED' || submit.recordStatus === 'UNDER_PROCESS'
        || submit.data.kycStatus === 'REJECTED' || submit.data.kycStatus === 'APPROVED') {
        if (this.redirectedFromTellerSearch == true) {
          this.updateSearch(this.searchText)
        }
        else {
          this.getDataOnLoad()
        }
      }

    })
  }

  searchCustomerId(searchTerm) {

    let text: any;
    let params: any;
    if (searchTerm === '') {
      console.log('empty')
      this.getDataOnLoad();
      this.isScroll = true;
      return;
    }
    if (this.isNum(searchTerm)) {
      if (searchTerm.length == 10) {
        console.log("contact");
        text = this.searchText.trim();
        params = 'phoneNumber=' + text;
      }
      else {
        console.log("cifId");
        text = this.searchText.trim();
        // params = 'customerId='+text;
        this.apiService.getKycDetails1(text)
          .subscribe(data => {
            console.log(data)
            if (data != null) {
              params = 'cifNumber=' + text;
              console.log('cif', params);
            }
            else {
              console.log('not a cif')
              params = 'customerId=' + text;
              console.log('customer', params);
            }
          })
      }

    }
    else if (this.isString(searchTerm)) {
      if (/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(searchTerm)) {
        console.log("email");
        text = this.searchText.trim();
        params = 'primaryEmailAdress=' + text;
      }
      else {
        console.log("name");
        text = this.searchText.trim();
        params = 'customerName=' + text;
      }
    }
    setTimeout(() => {
      this.getCustomerData(params);
    }, 1000);
    this.isScroll = false;
  }

  getCustomerData(params) {
    this.loading = true;
    console.log("params::", params)
    this.apiService.getCustomerOnBoardingSummary(params)
      .subscribe(data => {
        this.INITIATEDTotalPages = data?.totalPages;
        this.loading = false;
        this.resetSearchData();
        console.log('resp data :: ', data);
        this.INITIATEDArr = [];
        for (let index = 0; index < data?.customerInfoList.length; index++) {
          const element = data?.customerInfoList[index];
          this.searchResponseUpdate(element?.custStatus
            , data.customerInfoList[index]);
        }
      })
  }

  resetSearchData() {
    this.INITIATEDArr = [];
    this.UNDER_PROCESSArr = [];
    this.APPROVEDArr = [];
    this.REJECTEDArr = [];
    this.cdr.markForCheck();
  }

  searchResponseUpdate(status: string, data: any) {
    if (status == 'INITIATED') {
      // this.INITIATEDArr = [];
      this.INITIATEDArr.push(data);
      console.log('this.INITIATEDArr :: ', this.INITIATEDArr);
    } else if (status == 'UNDER_PROCESS') {
      // this.UNDER_PROCESSArr = [];
      this.UNDER_PROCESSArr.push(data);
      console.log('this.UNDER_PROCESSArr :: ', this.UNDER_PROCESSArr);
    } else if (status == 'APPROVED') {
      // this.APPROVEDArr = [];
      this.APPROVEDArr.push(data);
      console.log('this.APPROVEDArr :: ', this.APPROVEDArr);
    } else if (status == 'REJECTED') {
      // this.REJECTEDArr = [];
      this.REJECTEDArr.push(data);
      console.log('this.REJECTEDArr :: ', this.REJECTEDArr);
    }
    this.cdr.markForCheck();
  }

  getProfileImage(customerId) {
    this.apiService.getProfileDetails(customerId)
      .subscribe((data: any) => {
        console.log(data);
        console.log(data.profileImage);
        this.searchable = false;
        if (data.profileImage != null) {
          let objectURL = 'data:image/jpeg;base64,' + data.profileImage.fileData;
          this.searchable = true;
          this.image = this.sanitizer.bypassSecurityTrustUrl(objectURL);
          console.log(this.searchable, "Searchable");
        }

      }, (error: any) => {
        console.log(error);
      });
  }
  getCustomerIdData(valueToFilter) {
    console.log('get kyc')
    if (valueToFilter != '' && valueToFilter != undefined && valueToFilter != null) {
      this.apiService.getKycTaskSummaryAll(valueToFilter, this.searchText)
        .subscribe(data => {
          this.cdr.markForCheck();
          this.cdr.detectChanges();
          if (data != null) {
            var value = data[0].customerAdditionalInfo.kycStatus + 'Arr';
            this[value].splice(0, 0, data[0])
            this[value] = this[value].slice();
            console.log('resp value :: ', this[value]);
          } else {
            this.noDataFound = true;
          }

        })
    }
  }

  ngOnDestroy() {
    this._searchSubject.unsubscribe();

    this.UNDER_PROCESSArr = [];
    this.APPROVEDArr = [];
    this.REJECTEDArr = [];
    this.INITIATEDArr = [];

    this.INITIATEDPage = 0;
    this.REJECTEDPage = 0;
    this.APPROVEDPage = 0;
    this.UNDER_PROCESSPage = 0;

    this.INITIATEDTotalPages = 0;
    this.REJECTEDTotalPages = 0;
    this.APPROVEDTotalPages = 0;
    this.UNDER_PROCESSTotalPages = 0;
  }

  /* @method :: to update the existing array with scroll api response */
  async updateDataOnScroll(status: string, data: any) {
    if (status == 'INITIATED') {
      const result = _.unionBy(this.INITIATEDArr, data, 'customerId');
      this.INITIATEDArr = result;
    } else if (status == 'UNDER_PROCESS') {
      const result = _.unionBy(this.UNDER_PROCESSArr, data, 'customerId');
      this.UNDER_PROCESSArr = result;
    } else if (status == 'APPROVED') {
      const result = _.unionBy(this.APPROVEDArr, data, 'customerId');
      this.APPROVEDArr = result;
    } else if (status == 'REJECTED') {
      const result = _.unionBy(this.REJECTEDArr, data, 'customerId');
      this.REJECTEDArr = result;
    }
    this.cdr.markForCheck();
  }


  isNum(val) {
    return !isNaN(val);
  }

  isString(val) {
    return val;
  }
  openSearchedApprover() {
    let redirectArr = [this.REJECTEDArr, this.APPROVEDArr, this.UNDER_PROCESSArr, this.INITIATEDArr].filter(arr => arr.length == 1);
    console.log(redirectArr);
    if (redirectArr.length == 1)
      this.goToCustOnBoardingSummary(redirectArr[0][0], redirectArr[0][0].custStatus);
  }

  getRandomColor(idx) {
    var col0 = '#0d856b';
    var col1 = '#d66f1b';
    var col2 = '#9f52e7';
    var col3 = '#e9318d';
    var col4 = '#1175a3';
    var col5 = '#e93131';
    var col6 = '#2316d3';
    var col7 = '#f557f5';
    var col8 = '#d6c31b';
    var col9 = '#40d61b';

    if ((idx % 10) == 0) return col0;
    if ((idx % 10) == 1) return col1;
    if ((idx % 10) == 2) return col2;
    if ((idx % 10) == 3) return col3;
    if ((idx % 10) == 4) return col4;
    if ((idx % 10) == 5) return col5;
    if ((idx % 10) == 6) return col6;
    if ((idx % 10) == 7) return col7;
    if ((idx % 10) == 8) return col8;
    if ((idx % 10) == 9) return col9;
    return '#d86315';

  }
}
