import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import * as moment from 'moment';

@Component({
  selector: 'app-card-financial-details-update',
  templateUrl: './card-financial-details-update.component.html',
  styleUrls: ['./card-financial-details-update.component.scss']
})
export class CardFinancialDetailsUpdateComponent implements OnInit {
  cardFinancialDetailsUpdateForm: FormGroup;
  incomeErrMsg: any;
  disableNext: boolean;
  ExpensesErrMsg: any;
  LiabilitiesErrMsg: any;
  assetErrMsg: any;
  arrayEmploymentType: String[];
  arrayEmploymentCategory: String[];
  maxDate = new Date();
  

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<CardFinancialDetailsUpdateComponent>,
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private cdr: ChangeDetectorRef,
    private snack: MatSnackBar
  ) { }

  ngOnInit(): void {
    console.log(this.data);
    
    this.getEmploymentType("fin_employment_type");
    this.getEmploymentCategory("employment_category");
    if(this.data && this.data.financialInfo){
      this.buildFinacialFormGroup(this.data.financialInfo[0])
    }
    else{
      this.buildFinacialFormGroup()
    }
    
  }

  buildFinacialFormGroup(data?) {
    console.log(data);
   this.cardFinancialDetailsUpdateForm = this.formBuilder.group({
     // Financial Details
     id:[data ? data.id: ''],
     applicantName: [this.data.initiationInfo.applicantName, Validators.required],
     currentlyWorking: [data ? data.currentlyWorking : ''],
     applicantTotalIncome: [data ? data.applicantTotalIncome : 0, Validators.required],
     applicantTotalExpense: [data ? data.applicantTotalExpense : 0, Validators.required],
     lastUpdatedon: [data ? new Date(data.lastUpdatedon) : ''],
     //limit_end_date: [data ? new Date(data.limit_end_date) : ''],
     
     // Employement Details
     employmentType: [data ? data.employmentType : '', Validators.required],
     employmentCategory: [data ? data.employmentCategory : '', Validators.required],
     employeeNumber: [data ? data.employeeNumber : '', Validators.required],
     officeName: [data ? data.officeName : '', Validators.required],
     designation: [data ? data.designation : '', Validators.required],
     employmentStartDate: [data ? new Date(data.employmentStartDate): '', Validators.required],
     employmentEndDate: [data ? new Date(data.employmentEndDate): ''],
     
     // Monthly Income
     salary: [data ? data.salary : ''],
     interestIncome: [data ? data.interestIncome : ''],
     bonus: [data ? data.bonus : ''],
     cashGifts: [data ? data.cashGifts : ''],
     business: [data ? data.business : ''],
     pension: [data ? data.pension : ''],
     monthlyIncomeRentals: [data ? data.monthlyIncomeRentals : ''],
     monthlyIncomeOthers: [data ? data.monthlyIncomeOthers : ''],
     monthlyIncomeTotal: [data ? data.monthlyIncomeTotal : 0],
           
     // Monthly Expense
     household: [data ? data.household : ''],
     education: [data ? data.education : ''],
     vehicleMaintenance: [data ? data.vehicleMaintenance : ''],
     monthlyExpenseRentals: [data ? data.monthlyExpenseRentals : ''],
     monthlyExpenseOthers: [data ? data.monthlyExpenseOthers : ''],
     medical: [data ? data.medical : ''],
     travel: [data ? data.travel : ''],
     monthlyExpenseTotal: [data ? data.monthlyExpenseTotal : 0],
     
     // Annual Liability
     propertyLoan: [data ? data.propertyLoan : ''],
     vehicleLoan: [data ? data.vehicleLoan : ''],
     personalLoans: [data ? data.personalLoans : ''],
     overdraft: [data ? data.overdraft : ''],
     cardOutstandings: [data ? data.cardOutstandings : ''],
     liabilityOthers: [data ? data.liabilityOthers : ''],
     liabilityTotal: [data ? data.liabilityTotal : 0],
     
     // Assets
     savingDeposits: [data ? data.savingDeposits : ''],
     properties: [data ? data.properties : ''],
     automobiles: [data ? data.automobiles : ''],
     fixedDeposits: [data ? data.fixedDeposits : ''],
     lands: [data ? data.lands : ''],
     assetOthers: [data ? data.assetOthers : ''],
     stocks: [data ? data.stocks : ''],
     assetTotal: [data ? data.assetTotal : 0],
     
     // accountId; -->need to get from other screens
    // accountId: this.accountId != null ? this.accountId : '',
  }, {validator:(formGroup:FormGroup)=>{
    return this.validateInputGroups(formGroup)
  }
})
  };
 validateInputGroups(formgroup:FormGroup) {
   if(formgroup.controls["salary"].value || formgroup.controls["interestIncome"].value || 
   formgroup.controls["bonus"].value || formgroup.controls["cashGifts"].value || 
   formgroup.controls["business"].value || formgroup.controls["pension"].value || 
   formgroup.controls["monthlyIncomeRentals"].value || formgroup.controls["monthlyIncomeOthers"].value) {
    
     this.incomeErrMsg = null
       this.disableNext = true;
     
   }
   else {
     this.incomeErrMsg = "Entering any one field is mandatory for Monthly Income Section";
     this.disableNext = false;
   }
   
   if(formgroup.controls["household"].value || formgroup.controls["education"].value ||
   formgroup.controls["vehicleMaintenance"].value || formgroup.controls["monthlyExpenseRentals"].value ||
   formgroup.controls["monthlyExpenseOthers"].value || formgroup.controls["medical"].value || 
   formgroup.controls["travel"].value) {

     this.ExpensesErrMsg = null;
       this.disableNext = true;
     
   }
   else {
     this.ExpensesErrMsg = "Entering any one field is mandatory for Monthly Expenses Section";
     this.disableNext = false;
   }
   
   if(formgroup.controls["propertyLoan"].value || formgroup.controls["vehicleLoan"].value ||
   formgroup.controls["personalLoans"].value || formgroup.controls["overdraft"].value ||
   formgroup.controls["cardOutstandings"].value || formgroup.controls["liabilityOthers"].value) {
     
     this.LiabilitiesErrMsg = null;
       this.disableNext = true;
     
   }
   else {
     this.LiabilitiesErrMsg = "Entering any one field is mandatory for Annual Liabilities Section";
     this.disableNext = false;
   }
   
   if(formgroup.controls["savingDeposits"].value || formgroup.controls["properties"].value ||
   formgroup.controls["automobiles"].value || formgroup.controls["fixedDeposits"].value ||
   formgroup.controls["lands"].value || formgroup.controls["assetOthers"].value || 
   formgroup.controls["stocks"].value) {

     this.assetErrMsg = null;
       this.disableNext = true;
     
   }
   else {
     this.assetErrMsg = "Entering any one field is mandatory for Asset Section";
     this.disableNext = false;
   }
 }
 converDate(value) {
  let date = moment(value).format('YYYY-MMM-DD');
  console.log("date", date);
  if (date)
    return date;
}
 onSubmit() {
  let startDate = this.converDate(this.cardFinancialDetailsUpdateForm.controls.employmentStartDate.value);
  if(startDate && startDate!= "Invalid date"){
  console.log("startDate", startDate);
  this.cardFinancialDetailsUpdateForm.value['employmentStartDate'] = startDate;
  }else{
    this.cardFinancialDetailsUpdateForm.value['employmentStartDate'] = null;
  }
  let endDate = this.converDate(this.cardFinancialDetailsUpdateForm.controls.employmentEndDate.value);
  if(endDate && endDate!= "Invalid date"){
    console.log("startDate", endDate);
    this.cardFinancialDetailsUpdateForm.value['employmentEndDate'] = endDate;
  }else{
    this.cardFinancialDetailsUpdateForm.value['employmentEndDate'] = null;
  }
  
  //let lastUpdatedon = this.converDate(this.cardFinancialDetailsUpdateForm.controls.lastUpdatedon.value);
  let lastUpdatedon =moment(this.cardFinancialDetailsUpdateForm.controls.lastUpdatedon.value).format('YYYY-MM-DD HH:MM:SS');
  console.log("lastUpdatedon",lastUpdatedon)
  if(lastUpdatedon && lastUpdatedon!= "Invalid date"){
    console.log("startDate", lastUpdatedon);
    this.cardFinancialDetailsUpdateForm.value['lastUpdatedon'] = lastUpdatedon;
  }else{
    this.cardFinancialDetailsUpdateForm.value['lastUpdatedon'] = null;
  }

  this.cardFinancialDetailsUpdateForm.value.accountType="cardOrigin";
  this.cardFinancialDetailsUpdateForm.value.cardId=this.data.initiationInfo.cardId; 
  console.log("submit", this.cardFinancialDetailsUpdateForm.value);
  console.log(this.cardFinancialDetailsUpdateForm.value)
  var payload: any[]=[];
  payload.push(this.cardFinancialDetailsUpdateForm.value)
  console.log(payload);
  // if (!this.cardFinancialDetailsUpdateForm.value.applicantName || !this.cardFinancialDetailsUpdateForm.value.employmentType || !this.cardFinancialDetailsUpdateForm.value.employmentCategory) {
  //   console.log("if--->")
  //   Swal.fire('Please Enter The Mandatory Fields !');
  //   return false;
  // }
  this.apiService.upsertCardFinancialDetails(payload).subscribe(fincialResp => {

    console.log(fincialResp);
    this.snack.open('Financial Details Updated!', 'OK', {
      duration: 4000,
      verticalPosition: 'top',
      horizontalPosition: 'right'
    });
      let statusModel = {
        cardId: this.data.initiationInfo.cardId,
        status: "BACKLOG"
      };
      this.apiService.updateCardStatus(statusModel).subscribe(data => {
        this.dialogRef.close({
          status: data.status
         });
      });
    

  }, err => {
    this.snack.open('Error Unable to Save!', 'OK', {
      duration: 4000,
      verticalPosition: 'top',
      horizontalPosition: 'right'
    });
  })

}
// Total Monthly Income
calculateTotalMonthlyIncome(value) {
  let income:number = Number(value.salary) + Number(value.business) + 
  Number(value.interestIncome) + Number(value.pension) + 
  Number(value.bonus) + Number(value.monthlyIncomeOthers) + 
  Number(value.monthlyIncomeRentals) + Number(value.cashGifts);
  
  // this.cardFinancialDetailsUpdateForm.controls.monthlyIncome.get('monthlyIncomeTotal').setValue(income);
  this.cardFinancialDetailsUpdateForm.controls.monthlyIncomeTotal.setValue(income);
  this.cdr.markForCheck();
  this.cdr.detectChanges();

  this.calculateTotalIncomeAndExpense(this.cardFinancialDetailsUpdateForm.value);
}

// Total Monthly Expense
calculateTotalMonthlyExpenses(value) {
  let expense:number = Number(value.household) + Number(value.education) + 
    Number(value.vehicleMaintenance) + Number(value.monthlyExpenseOthers) + 
    Number(value.medical) + Number(value.travel) + 
    Number(value.monthlyExpenseRentals);

  // this.cardFinancialDetailsUpdateForm.controls.monthlyExpense.get('monthlyExpenseTotal').setValue(expense);
  this.cardFinancialDetailsUpdateForm.controls.monthlyExpenseTotal.setValue(expense);
  this.cdr.markForCheck();
  this.cdr.detectChanges();

  this.calculateTotalIncomeAndExpense(this.cardFinancialDetailsUpdateForm.value);
}

// Total Liabilities
calculateTotalLiabilities(value) {
  let liability:number = Number(value.propertyLoan) + Number(value.personalLoans) + 
  Number(value.overdraft) + Number(value.vehicleLoan) + 
  Number(value.cardOutstandings) + Number(value.liabilityOthers);

  // this.cardFinancialDetailsUpdateForm.controls.annualLiability.get('liabilityTotal').setValue(liability);
  this.cardFinancialDetailsUpdateForm.controls.liabilityTotal.setValue(liability);
  this.cdr.markForCheck();
  this.cdr.detectChanges();
  
  this.calculateTotalIncomeAndExpense(this.cardFinancialDetailsUpdateForm.value);
}

// Total Assets
calculateTotalAssets(value) {
  let asset:number = Number(value.savingDeposits) + Number(value.properties) + 
  Number(value.fixedDeposits) + Number(value.stocks) + 
  Number(value.automobiles) + Number(value.lands) + Number(value.assetOthers);

  // this.cardFinancialDetailsUpdateForm.controls.assets.get('assetTotal').setValue(asset);
  this.cardFinancialDetailsUpdateForm.controls.assetTotal.setValue(asset);
  this.cdr.markForCheck();
  this.cdr.detectChanges();
  
  this.calculateTotalIncomeAndExpense(this.cardFinancialDetailsUpdateForm.value);
}

// Total Income, Expense and Final Total
calculateTotalIncomeAndExpense(value) {

  let totalIncome:number = (value.monthlyIncomeTotal*12) + value.assetTotal;
  let totalExpense:number = (value.monthlyExpenseTotal*12) + value.liabilityTotal;

  this.cardFinancialDetailsUpdateForm.controls.applicantTotalIncome.setValue(totalIncome);
  this.cardFinancialDetailsUpdateForm.controls.applicantTotalExpense.setValue(totalExpense);
  this.cdr.markForCheck();
  this.cdr.detectChanges();
 }
  close(){
    this.dialogRef.close({
      status:"ONGOING"
    })
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
   letterOnly(event) {
    var charCode = event.keyCode;
            if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
                return true;
            else
                return false;
  }
   getEmploymentType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayEmploymentType = data;
    });
  }
  getEmploymentCategory(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayEmploymentCategory = data;
    });
  }
}
