import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-mandate-details-update',
  templateUrl: './mandate-details-update.component.html',
  styleUrls: ['./mandate-details-update.component.scss']
})
export class MandateDetailsUpdateComponent implements OnInit {

  mandatesUpdateForm: FormGroup;
  accountId: any;
  no_of_sig: any;
  no_of_applicants: any;
  flag: boolean;
  flag1: boolean;
  arrayModeOfOperation: any;
  showEditOption: boolean;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<MandateDetailsUpdateComponent>,
    private formBuilder: FormBuilder,
    private apiService: ApplicationEntryStageService,
    public snack: MatSnackBar,
    private ls: LocalStoreService,
    private api: ApiService,
    private confirmService: AppConfirmService,
    private tasksummaryService: TasksummaryService

  ) { }

  ngOnInit(): void {
    console.log(this.data);

    this.accountId = this.ls.getItem('accountId')
    this.getModeOfOperation('opr_mode');
    if (this.data) {
      this.showEditOption = true;
      this.buildMandateForm()
      this.addMandateToFormGroup(this.data)
    } else {
      this.showEditOption = false;
      this.buildMandateForm();
      this.addMandateToFormGroup();
    }
  }

  buildMandateForm() {
    this.mandatesUpdateForm = this.formBuilder.group({
      mandates: this.formBuilder.array([]),
    });
  }

  addMandateToFormGroup(data?) {
    const numberOfItems = data? data.length: 1;
    console.log('numberOfItems', numberOfItems);
    for (let i = 0; i < numberOfItems; i++) {
      this.m.push(
        this.formBuilder.group({
          mandateId: [data?data.mandateId:''],
          productAccountId: [this.accountId],
          modeOfOperation: ['', Validators.required],
          amountFrom: ['', Validators.required],
          amountTo: ['', Validators.required],
          totalSignaturesRequired: [''],
          remarks: [''],
        })
      );
      this.m.at(i).patchValue(data[i])
    }
  }
  removeMandate(i: number) {
    let mandateId = this.m.at(i).get('mandateId').value;
    this.confirmService
      .confirm({
        title: 'Confirm',
        message: 'Are you sure want to remove?',
        ok: 'Yes',
        cancel: 'No'
      })
      .subscribe((res) => {
        console.log('res:: removed ', res);
        if (res) {
          this.apiService.deleteMandateById(mandateId, 'ADMIN').subscribe(
            (data: any) => {
              console.log('data', data);
            },
            (err) => {
              const control = <FormArray>(
                this.mandatesUpdateForm.controls['mandates']
              );
              control.removeAt(i);
              if (err.status === 200) {
                this.snack.open('Record Deleted Successfully', 'OK', {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'right',
                });
              }
            }
          );
        } else return;
      });
  }
  get f() {
    return this.mandatesUpdateForm.controls;
  }
  get m() {
    return this.f.mandates as FormArray;
  }
  getModeOfOperation(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayModeOfOperation = data;
    });
  }
  onSubmit() {
    this.apiService.saveMandateDetails(this.mandatesUpdateForm.value).subscribe(
      (data: any) => {
        console.log(data);
        if (data === 200 || data !== null) {
          this.snack.open(' Mandate Details Updated!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right',
          });
              this.dialogRef.close({
                status: 'BACKLOG'
              })
        } else if (data === 500) {
          this.snack.open(
            'Not able to connect to server!',
            'INTERNAL SERVER ERROR',
            {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right',
            }
          );
        }
      },
      (err) => {
        console.log(err);
      }
    );
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  _keyPress2(event: any) {
    this.no_of_sig = event.key;
    if (isNaN(event.key)) {
      console.log('input is invalid');
    } else if (event.key > this.no_of_applicants) {
      console.log('no of signatories is more than no of applicants');
      this.flag = true;
    } else if (event.key == 0) {
      this.flag1 = true;
    } else {
      this.flag = false;
      this.flag1 = false;
    }
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }

}
