import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { TasksummaryService } from '../../tasksummary.service';
@Component({
  selector: 'app-approval-details-update',
  templateUrl: './approval-details-update.component.html',
  styleUrls: ['./approval-details-update.component.scss']
})
export class ApprovalDetailsUpdateComponent implements OnInit {

  approvalUpdateForm: FormGroup;
  arrayAccountType: any[] = [];
  arrayAccountCurrency: any[] = [];
  arrayUserRecomendation: any[] = [];
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<ApprovalDetailsUpdateComponent>,
    private api: ApiService,
    private formBuilder: FormBuilder,
    private ls: LocalStoreService,
    public snack: MatSnackBar,
    private tasksummaryService: TasksummaryService

  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.buildApprovalForm({})
    this.getAccountCurrency("currency");
    this.getAccountType("account_type");
    this.getUserRecomendation("user_recomendation");
    if(this.data){
    this.buildApprovalForm(this.data)
    }
  }
  buildApprovalForm(item) {
    this.approvalUpdateForm = this.formBuilder.group({
      accountId: [this.ls.getItem('accountId')],
      accountType: [item ? item.accountType : '',],
      businessProductName: [item ? item.businessProductName : '',],
      productCode: [item ? item.businessProductName : '',],
      description: [item ? item.description : '',],
      accountBranch: [item ? item.accountBranch : '',],
      accountCurrency: [item ? item.accountCurrency : '',],
      applicationDate: [item ? item.applicationDate : '',],
      fundTheAccount: [item ? item.fundTheAccount : '',],
      fundBy: [item ? item.fundBy : '',],
      amount: [item ? item.amount : '',],
      overDraftRequested: [item ? item.overDraftRequested : '',],
      productDescription: [item ? item.productDescription : '',],
      userRecommendation: [item ? item.userRecommendation : '',],
      createdBy: [this.ls.getItem("userName")],
    });
  }
  
  onSubmit(form) {
form.applicationDate= moment(form.applicationDate).format('DD-MMM-YYYY')
    this.api.approvalUpdate(form).subscribe(resp => {
      this.snack.open('Approval Details Updated!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
        this.dialogRef.close({
          status: 'BACKLOG'
        })
    })
  }
  getAccountCurrency(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountCurrency = data;
    });
  }
  getAccountType(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountType = data;
    });
  }
  getUserRecomendation(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayUserRecomendation = data;
    });
  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }
}

