import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';

@Component({
  selector: 'app-card-credit-score-update',
  templateUrl: './card-credit-score-update.component.html',
  styleUrls: ['./card-credit-score-update.component.scss']
})
export class CardCreditScoreUpdateComponent implements OnInit {
  cardCreditScoreUpdateForm: FormGroup;
  customerNames:String[];
  disableAddAgency: boolean;
  disableFastTrack: boolean;
  disableUjjivan: boolean;
  disableTest: boolean;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<CardCreditScoreUpdateComponent>,
    private formBuilder: FormBuilder,
    private appApiService: ApplicationEntryStageService,
    private api: ApiService,
    public snack: MatSnackBar
  ) { }

  ngOnInit(): void {
this.getCustomerInformationDetails();
    if(!this.data.creditScore.cardId){
       this.buildCreditForm()       
    }
    else{
        this.buildCreditForm(this.data.creditScore);
    }
  
   
  }

  getCustomerInformationDetails() {
    this.customerNames = []
    this.appApiService
      .fetchCustomersByCardAccId(this.data.cardId, 'cardOrigin')
      .subscribe(
        (resp: any) => {
          console.log('resp for customer information names ', resp.data);
          for (let index = 0; index < resp.data.length; index++) {
            if (resp.data[index].middleName == null) {
              this.customerNames.push(resp.data[index].firstName + ' ' + resp.data[index].lastName)
            } else {
              this.customerNames.push( resp.data[index].firstName +
                ' ' +
                resp.data[index].middleName +
                ' ' +
                resp.data[index].lastName)
               
            }
          }
          console.log('customer name ', this.customerNames);
          if (this.customerNames) {
            console.log('If condtion');
            this.cardCreditScoreUpdateForm
              .get('customerName')
              .setValue(this.customerNames[0]);
          }
        },
        (err: any) => {}
      );
  }

  get f() {
    return this.cardCreditScoreUpdateForm.controls;
  }
  get c() {
    return this.f.agencyRating as FormArray;
  }

  buildCreditForm(data?, isResponse?: boolean) {
    console.log(data);

    this.cardCreditScoreUpdateForm = this.formBuilder.group({
      cardId: this.data.cardId,
      customerName: data.customerName ? data.customerName : this.customerNames[0],
      agencyRating: this.formBuilder.array([this.addModFormGroup(data)]),
    });

    console.log(this.cardCreditScoreUpdateForm);
  }

  addModFormGroup(data): FormGroup {
    console.log(data, 'inside addModeFormGrp');
    return this.formBuilder.group({
      cardId: [this.data.cardId],
      agencyId: [data ? data.agencyId : 0],
      agencyName: [data ? data.agencyRating[0].agencyName : '',Validators.required,],
      ratings: [data ? data.agencyRating[0].ratings : '', Validators.required],
      remarks: [data ? data.agencyRating[0].remarks : ''],
    });
  }

  addFormGroup() {
    this.c.push(
      this.formBuilder.group({
        agencyId: [0],
        agencyName: ['', Validators.required],
        ratings: ['', Validators.required],
      })
    );
  }
  submit(val) {
    console.log('val', val);
    val.accountType = 'cardOrigin';
    this.api.saveCreditDetails(val).subscribe(
      (data: any) => {
        console.log(data);
        if (data === 200 || data !== null) {
          this.snack.open(' Credit Details Updated!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right',
          });
          let statusModel = {
            cardId: data.cardId,
            status: "BACKLOG"
          };
          this.api.updateCardStatus(statusModel).subscribe(data => {
            this.dialogRef.close({
              status: data.status
             });
            });
         
        } else if (data === 500) {
          this.snack.open(
            'Not able to connect to server!',
            'INTERNAL SERVER ERROR',
            {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right',
            }
          );
        }
      },
      (err) => {
        console.log(err);
      }
    );
  }

  onClick() {
    for(let i=0; i<3; i++) {     
      if(this.cardCreditScoreUpdateForm.value.agencyRating[i].agencyName=="Test Agency") {
        this.disableTest=true;
       }    
      if(this.cardCreditScoreUpdateForm.value.agencyRating[i].agencyName=="Ujjivan Agency") {
        this.disableUjjivan=true;
       }    
      if(this.cardCreditScoreUpdateForm.value.agencyRating[i].agencyName=="Fast Track Agency") {
        this.disableFastTrack=true;
       }
     }
     if(this.cardCreditScoreUpdateForm.value.agencyRating.length==3) {
       this.disableAddAgency=true;
     }
   } 
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  onSubmit(){
    
  }

  close(){
    this.dialogRef.close({
      status:"ONGOING"
    })
  }
}
