import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-loan-mandate-details-update',
  templateUrl: './loan-mandate-details-update.component.html',
  styleUrls: ['./loan-mandate-details-update.component.scss']
})
export class LoanMandateDetailsUpdateComponent implements OnInit {
  loanMandateUpdate: FormGroup;
  loanaccountID: any;
  loanAccountId: any;
  mandateDetails: any;

  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;
  
  constructor(private fb: FormBuilder,private router: Router,   @Inject(MAT_DIALOG_DATA) public data: any, private snack: MatSnackBar,
  private dataService: DataService, private ls: LocalStoreService,private taskSumService: TasksummaryService,
    private apiService: ApiService, public dialogRef: MatDialogRef<LoanMandateDetailsUpdateComponent>,
     private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    
    this.loanaccountID = this.data.loanAccountId;
    console.log(this.loanaccountID);
  
    console.log("this.loanaccountID", this.loanaccountID);
    this.getMandateDetails();
    this.buildMandateForm();
  }
  

  buildMandateForm(data?) {
    this.loanMandateUpdate = this.fb.group({
      applicantName: [data ? data?.applicantName: ''],
      registered: [data ? data?.registered : ''],
      numberOfApplicant: [data ? data?.numberOfApplicant : ''],
      collateralPercentage: [data ? data?.collateralPercentage : ''],
      repaymentSharePercentage: [data ? data?.repaymentSharePercentage: '']
      // businessProductName:[data ? data?.info?.businessProductName:''],
      // accountBranch:[data ? data?.info?.accountBranch:''],
    })
  }
  submitForm1(){
    
  

    let payload={
      loanAccountId: this.data.loanAccountId,
      applicantName:this.loanMandateUpdate.value.applicantName,
      collateralShare:this.loanMandateUpdate.value.collateralShare,
      repaymentShare: this.loanMandateUpdate.value.repaymentShare,
  
     
      
  
    }
    console.log("payload",payload)
    this.apiService.upsertMandateDetails(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
    
      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      

      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName);

        
    this.loanaccountID = this.data.loanAccountId;
    console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  getMandateDetails() {
   
    this.apiService.getMandateDetailsByLoanAccId(this.loanaccountID).subscribe((mandateDetails) => {
  
      this.mandateDetails = mandateDetails;
      console.log(this.mandateDetails);
      this.buildMandateForm(this.mandateDetails);
      // if(mandateDetails){
      //   for (let index = 0; index < mandateDetails.mandateDetails.length; index++) {
      //     this.addMandate(mandateDetails.mandateDetails[index]);
      //   }
      // }
    }, (err: any) => {
      // this.loader.close();
    });
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }
  next(){
    // this.ls.setItem('LOAN_DETAIL_DONE', true);
   
     this.router.navigate([`/tasks/loan-account-task-summary`]);
     console.log("this.ls.accounttype", this.ls.getItem('accountType'))
     this.taskSumService.getLoanAccountDetails(this.loanaccountID).subscribe((data) => {
       console.log("data", data)
     
 
       
    
     });
 
   
  }
  
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
  }

  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
  }
  
  goToClose(): void {
    this.dialogRef.close();
  }
 
}
