import { Component, OnInit, Inject, ChangeDetectorRef} from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TasksummaryService } from '../../tasksummary.service';



@Component({
  selector: 'app-loan-disbursement-details-update',
  templateUrl: './loan-disbursement-details-update.component.html',
  styleUrls: ['./loan-disbursement-details-update.component.scss']
})
export class LoanDisbursementDetailsUpdateComponent implements OnInit {
  loanAccountId: any;
  loanaccountID: any;
  loanDisbursementForm: FormGroup;
  disbursementDetails: any;

  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;
  

  constructor(private apiService: ApiService, @Inject(MAT_DIALOG_DATA) public data: any, private fb: FormBuilder,
    private loader: AppLoaderService, private taskSumService: TasksummaryService, private cdr: ChangeDetectorRef,
    private ls: LocalStoreService, public dialogRef: MatDialogRef<LoanDisbursementDetailsUpdateComponent>, private snack: MatSnackBar,
  ) { }

  ngOnInit(): void {
    this.loanaccountID = this.data.loanAccountId;
    console.log('loanaccountID', this.loanaccountID)
    this.buildDisbursementForm(this.data)
   // this.getDisbursementDetails();
  }
  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
   buildDisbursementForm(data?) {
    console.log('item',data)
   
     
    this.loanDisbursementForm= this.fb.group({
        loanAccountId: [this.loanaccountID],
        disbursementMode:[data ? data?.disbursementInfo?.disbursementMode:''],
        totalDisbursement: [data ? data?.disbursementInfo?.totalDisbursement : ''],
        numberOfDisbursement: [data ? data?.disbursementInfo?.numberOfDisbursement : ''],
        multipleDisbursementRequired:[data ? data?.disbursementInfo?.multipleDisbursementRequired:true],
        branchCode: [data ? data?.disbursementInfo?.branchCode : ''],
        customerAccount:[data ? data?.disbursementInfo?.customerAccount:''],
        loanAmount:[data ? data?.disbursementInfo?.loanAmount:''],
    
        marginIn: [data ? data?.disbursementInfo?.marginIn: '']
  
 
    })
  }
  getDisbursementDetails() {
 
    this.apiService.loanDisbursementFetchById(this.loanaccountID).subscribe((disbursementDetails) => {
 
      this.disbursementDetails = disbursementDetails;
      console.log("this.disbursementDetails::", this.disbursementDetails);
    

      this.buildDisbursementForm(this.disbursementDetails);
      this.loanDisbursementForm.value.numberOfDisbursement = this.disbursementDetails.numberOfDisbursement;
      this.loanDisbursementForm.value.totalDisbursement = this.disbursementDetails.totalDisbursement;
      this.loanDisbursementForm.value.loanAmount = this.disbursementDetails.loanAmount;
      this.loanDisbursementForm.value.branchCode = this.disbursementDetails.branchCode;
      this.loanDisbursementForm.value.customerAccount = this.disbursementDetails.customerAccount;
      this.loanDisbursementForm.value.marginIn = this.disbursementDetails.marginIn;
      this.loanDisbursementForm.value.multipleDisbursementRequired = this.disbursementDetails.multipleDisbursementRequired;
      // this.disbursementDetails.address2 = this.loanDisbursementForm.value.street;
    }, (err: any) => {
      this.loader.close();
    });
  }

  goToClose(): void {
    this.dialogRef.close();

    this.apiService.loanDisbursementFetchById(this.loanaccountID).subscribe((disbursementDetails: any) => {
      console.log("All Account Service", disbursementDetails);
      this.disbursementDetails = disbursementDetails;
      //this.buildformGroup(this.loanAccountDetails);
      console.log("this.accountDetails", this.disbursementDetails);
      console.log('form',this.loanDisbursementForm.value)
   });
   
  }
  submitForm1(){
    
   

    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      disbursementMode:this.loanDisbursementForm.value.disbursementMode,
      totalDisbursement:this.loanDisbursementForm.value.totalDisbursement,
      numberOfDisbursement: this.loanDisbursementForm.value.numberOfDisbursement,  
      multipleDisbursementRequired:this.loanDisbursementForm.value.multipleDisbursementRequired,
      branchCode:this.loanDisbursementForm.value.branchCode,
      customerAccount: this.loanDisbursementForm.value.customerAccount,
      loanAmount: this.loanDisbursementForm.value.loanAmount,
      marginIn: this.loanDisbursementForm.value.marginIn,
      
  
  
    }
    console.log("payload",payload)
    this.apiService.loanDisbursementSave(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName);    
        this.loanaccountID = this.data.loanAccountId;
        console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }

  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
}

}
