import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CorporateOnboardingService } from 'app/views/others/onboarding-corporate/corporate-onboarding.service';

@Component({
  selector: 'app-update-corporate-contact-info',
  templateUrl: './update-corporate-contact-info.component.html',
  styleUrls: ['./update-corporate-contact-info.component.scss']
})
export class UpdateCorporateContactInfoComponent implements OnInit {
  updateContactInfoForm: FormGroup;
  arrayState: any[] = [];
  arrayCountry: any[] = [];
  arrayCity: any[] = [];
  isAllSameDigitVal: boolean = false;
  zipIndex: number;
  corporateId: any;
  corporateAddress: FormArray = new FormArray([]);
  constructor(
    @Inject(MAT_DIALOG_DATA) private data,
    private dialogRef: MatDialogRef<UpdateCorporateContactInfoComponent>,
    private formBuilder: FormBuilder,
    private corporateService: CorporateOnboardingService,
    private ls: LocalStoreService,
    private snack: MatSnackBar,
    private apiService: ApiService,
  ) { }

  ngOnInit(): void {
    this.corporateId = this.ls.getItem('corporateId')
    console.log(this.data)
    this.getCountryName('country');
    if (this.data) {
      this.buildContactForm(this.data[0]);
      this.corporateAddress = this.updateContactInfoForm.get('corporateAddress') as FormArray;
      this.corporateAddress.push(this.createUserAddress(this.data[0]))
      this.getStateName(this.corporateAddress.get('country').value)
      this.getStateName(this.corporateAddress.get('state').value)
    }
    else {
      this.buildContactForm();
      this.corporateAddress = this.updateContactInfoForm.get('corporateAddress') as FormArray;
      this.corporateAddress.push(this.createUserAddress())
    }
  }
  buildContactForm(data?) {
    this.updateContactInfoForm = this.formBuilder.group({
      corporateId: [this.corporateId],
      primaryEmail: [data ? data.primaryEmail : '', [Validators.required]],
      secondaryEmail: [data ? data.secondaryEmail : ''],
      mobileNumber: [data ? data.mobileNumber : '', [Validators.minLength(10), Validators.maxLength(10)]],
      landLine: [data ? data.landlineNumber : '', [Validators.maxLength, Validators.minLength]],
      isMailingAddressSame: ['YES'],
      corporateAddress: this.formBuilder.array([])
    })
  }
  createUserAddress(item?): FormGroup {
    return this.formBuilder.group({
      addressType: ["Communication", [Validators.required]],
      address1: [item ? item.address1 : [''], [Validators.pattern(".*[a-zA-Z].*")]],
      address2: [item ? item.address2 : [''], [Validators.pattern(".*[a-zA-Z].*")]],
      city: item ? item.city : [''],
      state: item ? item.state : [''],
      zipCode: item ? item.zipCode : [''],
      country: item ? item.country : [''],
      residenceType: item ? item.residenceType : ['']
    })
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  letterOnly(event) {
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }
  isAllSameDigit(event, index) {
    this.zipIndex = index;
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      if (count == number.length) {
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    } else {
      this.isAllSameDigitVal = false;
    }
  }

  getCountryName(key?: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].authStatus === 'A') {
          this.arrayCountry.push(data[i].countryName);
        }
      }
    });
  }

  getStateName(country) {
    this.arrayState = [];
    this.arrayCity = [];
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].countryName == country) {
          this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
            this.arrayState = resp
          })
          break;
        }
      }
    })
  }
  getCityName(state: any) {
    this.apiService.getDropDownValues1("city", state).subscribe((data) => {
      this.arrayCity = data;
    });
  }
  onSubmit(updateContactInfoForm) {
    if (updateContactInfoForm.isMailingAddressSame == 'NO') {
      updateContactInfoForm.corporateAddress[0].addressType = 'Company';
      updateContactInfoForm.corporateAddress[1].addressType = 'Mailing';
    }
    if (updateContactInfoForm.isMailingAddressSame == 'YES') {
      updateContactInfoForm.corporateAddress[0].addressType = 'Company';
    }
    this.corporateService.saveOrUpdateCorporateAccountInfo(updateContactInfoForm)
      .subscribe(data => {
        if (data != null) {
          this.corporateService.sendCorporateAccountId(data.corporateId)
          this.snack.open('Contact Information Updated' + ' !', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          this.dialogRef.close({
            status: "INITIATED",
            resp: this.updateContactInfoForm.value
          })
        }
      })
  }
  close() {
    this.dialogRef.close({
      status: "UNDER_PROCESS"
    })
  }
}
