import { Component, OnInit,Inject, ChangeDetectorRef } from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-loan-approval-details-update',
  templateUrl: './loan-approval-details-update.component.html',
  styleUrls: ['./loan-approval-details-update.component.scss']
})
export class LoanApprovalDetailsUpdateComponent implements OnInit {
  loanAccountId: any;
  loanaccountID: any;
  loanApprovalForm: FormGroup;
  selectedDay: number = 1;
  day: number;
  days: any[] = [];
  months: any[] = [];
  selectedYear: number;
  year: number;
  years: any[] = [];
  accountType: any;
  arrayAccountBranch: any;
  screenArray: any;
  screenArrayData: any;
  branchName: any;
  currencyData: any;
  arraySystemRecommendation:any[]=['Approved','Rejected'];
  arrayUserRecommendation:any[]=['Approved','Rejected'];
  loanAccountDetails: any;

  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;

  constructor(private loader: AppLoaderService, public apiService: ApiService, @Inject(MAT_DIALOG_DATA) public data: any,
    private fb: FormBuilder, private ls: LocalStoreService, public dialogRef: MatDialogRef<LoanApprovalDetailsUpdateComponent>,
  private cdr: ChangeDetectorRef, private taskSumService: TasksummaryService,
  private snack: MatSnackBar) { }

  ngOnInit(): void {
    this.loanaccountID = this.data.loanAccountId;
    this.buildLoanApprovalForm(this.data)
    this.getLoanDetails();
    this.getBusinessProductDetails("business_product_name");
    this.getAccountBranchDetails();
    this.selectedYear = new Date().getFullYear();
  
    for (let year = 0; year <= 50; year++) {
      this.years.push(year+ " year");
    }

    for (let month = 0; month <= 11; month++) {
      this.months.push(month+ " month");
    }

    for (let day = 0; day <= 30; day++) {
      this.days.push(day+ " day");
    }
    this.loanaccountID = this.data.loanAccountId;
    console.log('loanaccountID', this.loanaccountID)

  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }
  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
  }
  
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)

      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }

    this.cdr.markForCheck();
}
  
   buildLoanApprovalForm(data?) {
    console.log('item',data)
   
    this.getBusinessProductDetails("business_product_name");
     this.getAccountBranchDetails();
     
    this.loanApprovalForm= this.fb.group({
        loanAccountId: [this.loanaccountID],
        productName:[data ? data?.loanApprovalInfo?.productName:''],
        accountBranch: [data ? data?.loanApprovalInfo?.accountBranch : ''],
        accountType:[data ? data?.loanApprovalInfo?.accountType:''],
        applicantName: [data ? data?.loanApprovalInfo?.applicantName : ''],
        approvedLoanAccount: [data ? data?.loanApprovalInfo?.approvedLoanAccount : ''],
        userRecommendation:[data ? data?.loanApprovalInfo?.userRecommendation:''],
        rateOfInterest:[data ? data?.loanApprovalInfo?.rateOfInterest:''],
        margin: [data ? data?.loanApprovalInfo?.margin : ''],
        loanTenure: [data ? data?.loanApprovalInfo?.loanTenure : ''],
        effectiveRate: [data ? data?.loanApprovalInfo?.effectiveRate: ''],
        day: [data ? (data?.loanApprovalInfo?.loanTenure.split("-")[2]) : ""],
        month: [data ? data?.loanApprovalInfo?.loanTenure.split("-")[1] : ""],
        year: [data ? (data?.loanApprovalInfo?.loanTenure.split("-")[0]) : ""],
  
 
    });

    // console.log('form before',this.loanAssetForm)
    // console.log('form aftr',this.loanAssetForm.value)
   }
   getAccountType(productName) {
    console.log("State -- ", productName)
    this.apiService.getAccount(productName).subscribe((data) => {
      console.log("data.accountType",data);
      this.accountType = data.accountTypeName;
      console.log("this.accountType",this.accountType);
      this.loanApprovalForm.get('accountType').setValue(data.accountTypeName);

     
    });
  }
  getBusinessProductDetails(key: string){
    this.apiService.getBusinessProductName(key).subscribe((data) => {
       console.log("data:: AccountBranch ", data)
       this.arrayAccountBranch = data;
      
     });
   }
   getAccountBranchDetails() {
    //debugger
    this.apiService.getAccountBranch().subscribe((data) => {
      console.log("data:: AccountBranch ", data)
      this.screenArray = data;
      this.screenArrayData=data;
      if (this.screenArray.length == 0) {
        this.screenArray.push(this.branchName);
      }
      console.log("this.branchName::", this.branchName, this.screenArray)
      this.currencyData = this.screenArray.find(x => x.branchName == this.branchName);
      console.log("Currency", this.currencyData)

    

    });
  }
   onChangeBranch(value) {
  //  this.ifEdit = true;
    this.currencyData = this.screenArray.find(x => x.branchName == value.value);
    if (this.currencyData != null || this.currencyData != undefined) {
      this.loanApprovalForm.get('accountCurrency').setValue(this.currencyData.branchCcy);
    }
   }


  submitForm1(){
  

    const renewData =
    this.loanApprovalForm.value.year +
    "-" +
    this.loanApprovalForm.value.month +
    "-" +
    this.loanApprovalForm.value.day;
  this.loanApprovalForm.get("loanTenure").setValue(renewData);
  delete this.loanApprovalForm.value.year;
  delete this.loanApprovalForm.value.month;
  delete this.loanApprovalForm.value.day;
    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      productName:this.loanApprovalForm.value.productName,
      accountBranch:this.loanApprovalForm.value.accountBranch,
      accountType:this.loanApprovalForm.value.accountType,
      applicantName: this.loanApprovalForm.value.applicantName,
      margin:this.loanApprovalForm.value.margin,
      rateOfInterest: this.loanApprovalForm.value.rateOfInterest,
      userRecommendation:this.loanApprovalForm.value.userRecommendation,
      effectiveRate:this.loanApprovalForm.value.effectiveRate,
      loanTenure:this.loanApprovalForm.value.loanTenure,
      accountCurrency: this.loanApprovalForm.value.accountCurrency,
      approvedLoanAccount: this.loanApprovalForm.value.approvedLoanAccount
    }
    console.log("payload",payload)
    this.apiService.saveLoanDetails(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.productName);
  
        
    this.loanaccountID = this.data.loanAccountId;
    console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  getLoanDetails() {
    console.log('get loan details');
    
    this.apiService.getLoanDetails(this.loanaccountID).subscribe((loanAccountDetails: any) => {
      console.log("All Account Service", loanAccountDetails);
      this.buildLoanApprovalForm(this.loanAccountDetails);
      this.loanAccountDetails = loanAccountDetails;
      console.log("this.accountDetails", this.loanAccountDetails);
      console.log('form',this.loanApprovalForm.value)
      this.loanApprovalForm.controls.productName.setValue(this.loanAccountDetails.productName);
      this.loanApprovalForm.value.accountType = this.loanAccountDetails.accountType;
     console.log('accountType',   this.ls.setItem('accountType', this.loanApprovalForm.value.accountType))
      this.ls.setItem('accountType', this.loanApprovalForm.value.accountType);


     this.loanApprovalForm.value.accountBranch = this.loanAccountDetails.accountBranch;
   this.loanApprovalForm.value.userRecommendation = this.loanAccountDetails.userRecommendation;
   this.loanApprovalForm.value.loanTenure = this.loanAccountDetails.loanTenure;
   this.loanApprovalForm.value.applicantName = this.loanAccountDetails.applicantName;
   this.loanApprovalForm.value.margin = this.loanAccountDetails.margin;
   this.loanApprovalForm.value.approvedLoanAccount = this.loanAccountDetails.approvedLoanAccount;
   this.loanApprovalForm.value.effectiveRate = this.loanAccountDetails.effectiveRate;
   this.loanApprovalForm.value.accountType = this.loanAccountDetails.accountType;
   this.loanApprovalForm.value.productName = this.loanAccountDetails.productName;
   this.loanApprovalForm.value.rateOfInterest = this.loanAccountDetails.rateOfInterest;
    });
  }
goToClose(): void {
  this.dialogRef.close();
 
  
  this.apiService.getLoanDetails(this.loanaccountID).subscribe((loanAccountDetails: any) => {
    console.log("All Account Service", loanAccountDetails);
    this.buildLoanApprovalForm(this.loanAccountDetails);
    this.loanAccountDetails = loanAccountDetails;
    //this.buildformGroup(this.loanAccountDetails);
    console.log("this.accountDetails", this.loanAccountDetails);
    console.log('form',this.loanApprovalForm.value)
  //  this.loanUpdateForm.value.businessProductName = this.loanAccountDetails.businessProductName;
    this.loanApprovalForm.controls.productName.setValue(this.loanAccountDetails.productName);
    this.loanApprovalForm.value.accountType = this.loanAccountDetails.accountType;
   // this.ls.setItem('accountType', data.accountType);
   console.log('accountType',   this.ls.setItem('accountType', this.loanApprovalForm.value.accountType))
    this.ls.setItem('accountType', this.loanApprovalForm.value.accountType);


   this.loanApprovalForm.value.accountBranch = this.loanAccountDetails.accountBranch;
 this.loanApprovalForm.value.userRecommendation = this.loanAccountDetails.userRecommendation;
 this.loanApprovalForm.value.loanTenure = this.loanAccountDetails.loanTenure;
 this.loanApprovalForm.value.applicantName = this.loanAccountDetails.applicantName;
 this.loanApprovalForm.value.margin = this.loanAccountDetails.margin;
 this.loanApprovalForm.value.approvedLoanAccount = this.loanAccountDetails.approvedLoanAccount;
 this.loanApprovalForm.value.effectiveRate = this.loanAccountDetails.effectiveRate;
 this.loanApprovalForm.value.accountType = this.loanAccountDetails.accountType;
 this.loanApprovalForm.value.productName = this.loanAccountDetails.productName;
 this.loanApprovalForm.value.rateOfInterest = this.loanAccountDetails.rateOfInterest;
  });
  
 
}

}




 
 










