import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';

@Component({
  selector: 'app-account-details-update',
  templateUrl: './account-details-update.component.html',
  styleUrls: ['./account-details-update.component.scss']
})
export class AccountDetailsUpdateComponent implements OnInit {

  accountUpdateForm: FormGroup
  listOfCustByCif: any[];
  timer: NodeJS.Timeout;
  disableFundTheAccount: boolean;
  flag: boolean;
  arrayAccountBranch: any;
  arrayAccountType: any;
  onbackSpace: boolean;
  isCifValid: boolean;
  cifNumberPrimary: any;
  showSpinn: boolean;
  currencyData: any;
  branchName: any;
  minDate = new Date();
  maxDate = new Date();
  validDate = new Date();
  accountId: any;
  invalidDate: boolean =false;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    private dialogRef: MatDialogRef<AccountDetailsUpdateComponent>,
    private appApiService: ApplicationEntryStageService,
    private formBuilder: FormBuilder,
    private api: ApiService,
    private cdr: ChangeDetectorRef,
    public snack: MatSnackBar,
    private apiService: ApplicationEntryStageService,
    private ls: LocalStoreService
  ) {  this.validDate.setDate(this.validDate.getDate());}

  ngOnInit(): void {
    console.log(this.data);
    this.accountId = this.ls.getItem('accountId')
    this.getAccountBranchDetails();
    this.getAccountType("account_type");
    if (this.data && this.data.accountId) {
      this.buildAccountUpdateForm(this.data)
    }
    else {
      this.buildAccountUpdateForm()
    }


  }
  buildAccountUpdateForm(data?) {
    console.log(data);
    let applicationDate = data ? moment(data.applicationDate).format() : '';
    this.flag = (data && data.swiftCodeEnable) ? true : false;
    if (data) {
      if (data.fundTheAccount != null) {
        this.disableFundTheAccount = true;
      }
    }
    if(data.applicationDate <  moment(new Date()).format('YYYY-MM-DD')){
   // if(moment(data.applicationDate, moment.defaultFormat).toDate() < new Date()){
      this.invalidDate=true;
     }

    this.accountUpdateForm = this.formBuilder.group({
      cifNumber: [data && data.customerId ? data.customerId : '', [Validators.required, Validators.minLength(5)]],
      customerName: [data ? data.customerName : ''],
      accountId: [this.accountId],
      accountType: [data ? data.accountType : '', Validators.required],
      businessProductName: [data ? data.businessProductName : '', Validators.required],
      productImage: [data ? data.productImage : ''],
      productDescription: [data ? data.productDescription : '', Validators.required],
      accountBranch: [data ? data.accountBranch : '', Validators.required],
      accountCurrency: [data ? data.accountCurrency : '', Validators.required],
      applicationDate: [data ? applicationDate : '', Validators.required],
      fundTheAccount: [data ? data.fundTheAccount : '0'],
      fundBy: [data ? data.fundBy : false],
      amount: [data ? data.amount : '0'],
      overDraftRequested: [data ? data.overDraftRequested : false],
      status: [data ? data.status : ''],
      swiftCodeEnable: [data ? data.swiftCodeEnable : false],
      swiftCode: [data ? data.swiftCode : '', Validators.pattern("(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+){8,8}$")],
    });

  }

  onSubmit() {
    let payload = {
      "accountId": this.accountId,
      "accountType": this.accountUpdateForm.value.accountType,
      "businessProductName": this.accountUpdateForm.value.businessProductName,
      "customerId": this.accountUpdateForm.value.customerId,
      "productImage": this.accountUpdateForm.value.productImage,
      "productDescription": this.accountUpdateForm.value.productDescription,
      "accountBranch": this.accountUpdateForm.value.accountBranch,
      "accountCurrency": this.accountUpdateForm.value.accountCurrency,
      "applicationDate": this.convertDate(this.accountUpdateForm.value.applicationDate),
      "fundTheAccount": this.accountUpdateForm.value.fundTheAccount,
      "fundBy": this.accountUpdateForm.value.fundBy,
      "amount": this.accountUpdateForm.value.amount,
      "overDraftRequested": this.accountUpdateForm.value.overDraftRequested,
      "status": this.accountUpdateForm.value.status,
      "swiftCode": this.accountUpdateForm.value.swiftCode,
      "swiftCodeEnable": this.accountUpdateForm.value.swiftCodeEnable,
      "customerName": this.accountUpdateForm.value.customerName
    };
    if (this.isCifValid == false) {
      this.snack.open('Kindly Enter Valid CifNumber to proceed!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      return;
    }
    this.apiService.saveProductAccountInfo(payload).subscribe(response => {
      if (response === 200 || response !== null) {
        this.snack.open('Account Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
            this.dialogRef.close({
              status: 'BACKLOG'
            })
      }
      else if (response === 500) {
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
    });
  }

  fetchByCif(cifNumber) {
    this.showSpinn = true;
    this.apiService.fetchByCif(cifNumber).subscribe((res: any) => {
      if (res.customerInfoList[0].custStatus != "APPROVED") {
        this.isCifValid = false;
        this.snack.open("CIF Number is not Approved :" + cifNumber, "", {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }

      this.accountUpdateForm.controls.cifNumber.setValue(cifNumber);
      this.currencyData = this.arrayAccountBranch.find(x => x.branchName == this.branchName);
      this.accountUpdateForm.get('accountBranch').setValue(this.branchName);
      if (this.currencyData != null || this.currencyData != undefined) {
        this.accountUpdateForm.get('accountCurrency').setValue(this.currencyData.branchCcy);
      }
      this.accountUpdateForm.get('amount').setValue(0);
      this.accountUpdateForm.get('swiftCode').setValue(null);
      this.showSpinn = false;
      if (!res) {
        this.isCifValid = false;
        this.snack.open('Invalid CIF Number', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        return;
      } else {
        if (res.customerInfoList[0].custStatus == "APPROVED") {
          this.isCifValid = true;
        }
        if (this.accountUpdateForm.value.swiftCode == null || this.accountUpdateForm.value.swiftCodeEnable == null) {
          console.log(this.accountUpdateForm.value.swiftCode);
          this.flag = false;
        }
        if (res.customerInfoList[0].custStatus == "APPROVED") {
          if (res.customerInfoList[0].middleName) {
            this.accountUpdateForm.get('customerName').setValue(res.customerInfoList[0].firstName + ' ' + res.customerInfoList[0].middleName + ' ' + res.customerInfoList[0].lastName);
          }
          else {
            this.accountUpdateForm.get('customerName').setValue(res.customerInfoList[0].firstName + ' ' + res.customerInfoList[0].lastName);
          }
          this.cifNumberPrimary = cifNumber;
        }
        this.cdr.markForCheck();
      }
    });

  }

  onToggle(event: any) {
    if (event.checked == true) {
      this.flag = true;
    }
    else {
      this.flag = false;
      this.accountUpdateForm.controls.swiftCode.reset();
      this.cdr.detectChanges();
      this.cdr.markForCheck();
    }
  }
  onKeyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    console.log(event.target.value);
    clearTimeout(this.timer)
    if (!event.target.value) {
      this.listOfCustByCif = []
    } else {
      this.timer = setTimeout(() => {
        this.appApiService.fetchByCif(event.target.value).subscribe((res: any) => {
          console.log(res);
          if (res) {
            this.listOfCustByCif = res.customerInfoList
          }
        })
      }, 1000);
    }
  }
  _keyPress(event: any) {
    console.log("gghhhhhhhhhhhhhhhh")
    const pattern = /^[A-Za-z\s]*$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  _keyPress1(event: any) {
    const pattern = /[a-zA-Z0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }

  }

  keyUpevent(event) {
    console.log("event", event);
    if (event.keyCode === 8) {
      this.onbackSpace = true;
      this.isCifValid = false;
    }
    else {
      this.onbackSpace = false;
    }
  }


  getAccountType(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountType = data;
    });
  }

  getAccountBranchDetails() {
    this.api.getAccountBranch().subscribe((data) => {
      this.arrayAccountBranch = data;
      this.currencyData = this.arrayAccountBranch.find(x => x.branchName == this.branchName);
      console.log("Currency", this.currencyData)

      if (this.currencyData != null || this.currencyData != undefined) {
        this.accountUpdateForm.get('accountCurrency').setValue(this.currencyData.branchCcy);
      }

    });
  }


  onChangeBranch(value) {
    this.currencyData = this.arrayAccountBranch.find(x => x.branchName == value.value);
    if (this.currencyData != null || this.currencyData != undefined) {
      this.accountUpdateForm.get('accountCurrency').setValue(this.currencyData.branchCcy);
    }

  }
  convertDate(value) {
    let date = moment(value).format('YYYY-MMM-DD');
    if (date)
      return date;
  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }
  addevent(event){
    this.invalidDate = false 
  }
}

