import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { inject } from '@angular/core/testing';
import { FormArray, FormBuilder, FormGroup, FormGroupDirective, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CorporateOnboardingService } from 'app/views/others/onboarding-corporate/corporate-onboarding.service';

@Component({
  selector: 'app-update-director-details',
  templateUrl: './update-director-details.component.html',
  styleUrls: ['./update-director-details.component.scss']
})
export class UpdateDirectorDetailsComponent implements OnInit {
  directorUpdateForm: FormGroup;
  arrayCountry: any[] = [];
  Currency: any;
  arrayState: any[] = [];
  arrayCity: string[] = [];
  arrayResidence: string[];
  customerId: any;
  panelOpenState: boolean;
  showAutocomplete: boolean;
  autocomplete: any;
  customerInfoList: any;
  cifNumberForPayload: any;
  customerIdForPayload: any;
  genderList: string[];
  prefixList: string[];
  idTypeList: string[];
  addressTypeList: string[];
  isAllSameDigitVal: boolean;
  corporateId: any;
  saveStatus: any;
  noOfDir: number;
  cifNumber: any;
  item: any;
  items: any;
  number: number;
  prefixFlag: boolean;
  currencyCode: any;
  timer: NodeJS.Timeout;
  isDirectorSaved: boolean = false;
  directors:FormArray = new FormArray([]);
  constructor(
    @Inject(MAT_DIALOG_DATA) private data,
    private dialogRef: MatDialogRef<UpdateDirectorDetailsComponent>,
    private formBuilder: FormBuilder,
    private ls: LocalStoreService,
    private apiService: ApiService,
    private corporateService: CorporateOnboardingService,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private appApiService: ApplicationEntryStageService,
  ) { }

  ngOnInit(): void {
    this.corporateId = this.ls.getItem('corporateId')
    console.log(this.data);
    this.getDoc();
    this.getCountryName('country');
    if (this.data != null) {
      this.buildDirectorForm(this.data);
      this.directors = this.directorUpdateForm.get('directors') as FormArray;
      for(let i = 0; i < this.data.length ; i++){
        this.directors.push(this.addDirectors(this.data[i]))
        for(let j = 0; j < this.data[i].userAddress.length; j++){
          this.getStateName(this.data[i].userAddress[j].country)
          this.getCityName(this.data[i].userAddress[j].state)
          this.addAddress(i,this.data[i].userAddress[j])
        }
        this.addIdProof(i,this.data[i].custIdProofs[0])
        this.addCustTaxInfo(i,this.data[i].custTaxInfo[0])
      }
    }
    else{
      this.buildDirectorForm();
      this.directors = this.directorUpdateForm.get('directors') as FormArray;
this.directors.push(this.addDirectors())
this.addAddress(0);
this.addIdProof(0)
this.addCustTaxInfo(0);
    }
  }
  buildDirectorForm(data?) {
    this.directorUpdateForm = this.formBuilder.group({
      noOfDirector:[data? this.data.length : '1'],
      directors: this.formBuilder.array([])
    })
  }

  addDirectors(data?){
    return this.formBuilder.group({
      customerId: this.customerId,
      citizenship: [data ? data.citizenship : ''],
      cifNumber: [data ? data.cifNumber : ''],
      corporateId: [data ? data.corporateId : ''],
      countryOfResidence: [data ? data.countryOfResidence : ''],
      dateOfBirth: [data ? data.dateOfBirth : ''],
      existingCustomer: [data ? data.existingCustomer : ''],
      firstName: [data ? data.firstName : ''],
      gender: [data ? data.gender : ''],
      landlineNumber: [data ? data.landlineNumber : '', [Validators.minLength(10), Validators.maxLength(10)]],
      lastName: [data ? data.lastName : ''],
      middleName: [data ? data.middleName : ''],
      phoneNumber: [data ? data.phoneNumber : '', [Validators.minLength(10), Validators.maxLength(10)]],
      placeOfBirth: [data ? data.placeOfBirth : ''],
      prefix: [data ? data.prefix : ''],
      primaryCustomer: [false],
      primaryEmailAdress: [data ? data.primaryEmailAdress : ''],
      residenceStatus: [data ? data.residenceStatus : ''],
      verificationType: "onboarding",
      userAddress: this.formBuilder.array([]),
      custIdProofs: this.formBuilder.array([]),
      custTaxInfo: this.formBuilder.array([])
    })
  }

  addAddress(i,data?) {
    (this.directors.controls[i].get('userAddress') as FormArray).push(this.formBuilder.group({
      addressType: [data ? data.addressType : ''],
      address1: [[data ? data.address1 :''], [Validators.pattern(".*[a-zA-Z].*")]],
      address2: [[data ? data.address2 :''], [Validators.pattern(".*[a-zA-Z].*")]],
      city: [data ? data.city :''],
      state: [data ? data.state :''],
      country: [data ? data.country :''],
      zipCode: [data ? data.zipCode :'', [Validators.required, Validators.pattern("^((\\+91-?)|0)?[0-9]{5,10}$")]]
    }));
  }

  addIdProof(i,data?) {
    (this.directors.controls[i].get('custIdProofs') as FormArray).push(this.formBuilder.group({
      idNumber: [data ? data.idNumber :''],
      idType: [data ? data.idType :''],
      expiryDate: [data ? data.expiryDate :'']
    }));
  }

  addCustTaxInfo(i,data?) {
    (this.directors.controls[i].get('custTaxInfo') as FormArray) .push(this.formBuilder.group({
      taxId: [data ? data.taxId :'', [Validators.required, Validators.pattern("^[0-9]*$")]],
      countryOfIssue: [data ? data.countryOfIssue :'']
    }));
  }

  getCountryName(key: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].authStatus === 'A') {
          this.arrayCountry.push(data[i].countryName);
        }
      }
    });
  }
  getStateName(country) {
    this.arrayState = [];
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].countryName == country) {
          this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
            for(let j = 0; j < resp.length; j++){
              if(resp[i].authStatus == 'A'){
                this.arrayState.push(resp[i].stateName)
              }
            }
          })
          break;
        }
      }
    })
  }
  getCityName(state: any) {
    this.apiService.getDropDownValues1("city", state).subscribe((data) => {
      this.arrayCity = data;
    });
  }
  getResidenceDetails(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayResidence = data;
    });
  }
  getDoc() {
    this.apiService.getDropDownValues("gender").subscribe((responseDatas) => {
      this.genderList = responseDatas;
    });
    this.apiService.getDropDownValues("prefix").subscribe((re) => {
      this.prefixList = re;
    });
    this.apiService.getDropDownValues("document_name").subscribe((re) => {
      this.idTypeList = re;
    });
    this.apiService.getDropDownValues("address_type").subscribe((re) => {
      this.addressTypeList = re;
    });
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  letterOnly(event) {
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }
  editDirector(index) {
    this.panelOpenState = !this.panelOpenState;

  }
  updatedVal(e) {
    if (e && e.length >= 3) {
      this.showAutocomplete = true;
    } else {
      this.showAutocomplete = false;
    }
  }
  selectCif(data) {
    this.getStateName(data.userAddress[0].country);
    this.getCityName(data.userAddress[0].state);
    if(data.citizenship!=null){
      this.onSelectionCitizionship(data.citizenship);
    }
    if(data.countryOfResidence!=null){
      this.onSelectionResidance(data.countryOfResidence);
    }
    this.customerInfoList = data.customerInfoList;
    this.cifNumberForPayload = data.cifNumber;
    this.customerIdForPayload=data.customerId;
    delete data.cifNumber;
    this.autocomplete.closePanel();
    this.directorUpdateForm.patchValue(data);
    this.directorUpdateForm.get('existingCustomer').setValue(true);
  //   if(data.custIdProofs[0].expiryDate < this.datepipe.transform(new Date(), 'yyyy-MM-dd')){
  //     this.snack.open('Kindly Enter future Valid till Date', 'OK', {
  //       duration: 4000,
  //       verticalPosition: 'top',
  //       horizontalPosition: 'right'
  //     });    }
  }
  fetchByCif(cifNumber) {
    console.log(cifNumber?.length);
    if (cifNumber?.length == 0) {
      this.autocomplete.closePanel();
    }
    this.directorUpdateForm.reset({
      cifNumber: this.directorUpdateForm.get('cifNumber').value,
      existingCustomer: this.directorUpdateForm.get('existingCustomer').value
    });
    this.showAutocomplete = false;
    if (cifNumber) {
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.appApiService.fetchByCif(cifNumber).subscribe((res: any) => {
          console.log("settimeout")
          console.log("res::", res);
          if (res) {
            this.showAutocomplete = true;
            this.customerInfoList = res.customerInfoList;
          } 
          if (this.customerInfoList[0].citizenship != null) {
            this.onSelectionCitizionship(this.customerInfoList[0].citizenship);
          }
          if (this.customerInfoList[0].countryOfResidence != null) {
            this.onSelectionResidance(this.customerInfoList[0].countryOfResidence);
          }
          else if (res?.customerInfoList.length > 1) {
            this.customerInfoList = res.customerInfoList;
          }
          else if (res?.customerInfoList.length == 0 || !res) {
            this.snack.open('Kindly Enter Valid CifNumber to proceed', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });

          }
        }, (err: any) => {
          console.error(err);
        })
      }, 1000);
    }
  }
  toggleChanges(event: any, formDirective: FormGroupDirective) {

    console.log(event)
    if (event === true) {
      this.directorUpdateForm.get('cifNumber').enable();
    }
    else if (event === false) {
      this.directorUpdateForm.get('cifNumber').disable();
      this.customerId = null;
    }
  }
  onSelectionResidance(data: any) {
    console.log(data);

    this.apiService.countryFlaglists(data).subscribe((value) => {
      console.log(value.currencyCode);
      this.Currency = value.currencyCode.toLowerCase();
      console.log(this.Currency);
    })

  }
  onSelectionCitizionship(data: any) {
    console.log(data);
    this.apiService.countryFlaglists(data).subscribe((value) => {
      console.log(value.currencyCode);
      this.currencyCode = value.currencyCode.toLowerCase();
      console.log(this.currencyCode);
    })
  }
  onChangeGender(event) {
    let prefix = this.directorUpdateForm.value.prefix;
    if ((prefix == "Mr" && event.value == "Male") || ((prefix == "Ms" || prefix == "Mrs") && event.value == "Female")) {
      this.prefixFlag = true;
    } else {
      this.prefixFlag = false;
      this.snack.open("Prefix and Gender does not match!", "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }
  noOfDirector() {
    let noOfDir = Number(this.directorUpdateForm.get('noOfDirector').value);
    if (this.directors.length < noOfDir) {
      for (let i = this.directors.length; i < noOfDir; i++) {
        this.directors.push(this.addDirectors())
        this.addAddress(i);
        this.addIdProof(i);
        this.addCustTaxInfo(i);
      }
    }
    else if (noOfDir < this.directors.length) {
      for (let i = this.directors.length - 1; i >= noOfDir ; i--) {
        this.directors.removeAt(i);
      }
    }
  }
  omit_special_char(event) {
    var k;
    k = event.charCode;  //         k = event.keyCode;  (Both can be used)
    return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32 || (k >= 48 && k <= 57));
  }
  isAllSameDigit(event) {
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      if (count == number.length) {
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    } else {
      this.isAllSameDigitVal = false;
    }
  }
  saveDirector(directorFormGroup, item) {
    directorFormGroup.cifNumber = this.cifNumberForPayload;
    directorFormGroup.customerId=this.customerIdForPayload;
    directorFormGroup.corporateId=this.corporateId;
    let coporateObjModel = {
      "corporateId": this.corporateId,
      "customerInfo": [
        directorFormGroup
      ]
    }
    if (this.noOfDir > 0) {
      this.saveStatus = [];
      for (let i = 0; i < this.noOfDir; i++) {
        this.saveStatus.push(true);
      }
      this.corporateService.setfinishSave(this.saveStatus);
    }
    this.corporateService.saveOrUpdateCorporateAccountInfo(coporateObjModel)
      .subscribe(data => {
        if (data != null) {
          if(data.customerInfo!=null && data.customerInfo[0].cifNumber!=null){
              this.cifNumber = data.customerInfo[0].cifNumber;
          }
          this.snack.open(`Director ${this.item} profile created` + ' !', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          this.cdr.markForCheck();
          if (this.noOfDir <= 1) {
            this.dialogRef.close({
              status:"INITIATED",
              resp:this.data
            })
          }
        } else {
          this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
      }, error => {
        console.log('error resp :: ', error);
        this.snack.open(`${error.error.message} `, 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
      this.isDirectorSaved=true;

  }
  onSubmit(){
    this.dialogRef.close({
      status:"INITIATED"
    })
  }
  close() {
    this.dialogRef.close({
      status: 'UNDER_PROCESS'
    })
  }
}
