import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import Swal from 'sweetalert2';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-unsecured-overdraft-details-update',
  templateUrl: './unsecured-overdraft-details-update.component.html',
  styleUrls: ['./unsecured-overdraft-details-update.component.scss']
})
export class UnsecuredOverdraftDetailsUpdateComponent implements OnInit {

  unsecuredUpdateForm: FormGroup
  accountId: any;
  flag: boolean;
  selectedDay: any;
  selectedYear: any;
  currency: any;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<UnsecuredOverdraftDetailsUpdateComponent>,
    private ls: LocalStoreService,
    private formBuilder: FormBuilder,
    private cdr: ChangeDetectorRef,
    private api: ApiService,
    private snack: MatSnackBar,
    private tasksummaryService: TasksummaryService
  ) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    console.log(this.data, this.accountId);
    this.getCurrencyDetails();
    if (this.data) {
      this.buildUnsecuredForm(this.data);
    }
    else {
      this.buildUnsecuredForm();
    }

  }

  buildUnsecuredForm(data?, isResponse?: boolean) {
    this.unsecuredUpdateForm = this.formBuilder.group({
      limitId: [data ? data.limitId : '', Validators.required],
      accountId: this.accountId != null ? this.accountId : '',
      overdraftCurrency: [data ? data.overdraftCurrency : '', Validators.required],
      overdraftLimitAmount: [data ? data.overdraftLimitAmount : '', Validators.required],
      limitStartDate: [data ? new Date(data.limitStartDate) : '', Validators.required],
      limitEndDate: [data ? new Date(data.limitEndDate) : '', Validators.required],
      unsecuredLimit: [''],
      renewPeriod: [data ? data.renewPeriod : ''],
      renewalAmount: [data ? data.renewalAmount : ''],
      year: [data ? data.year : ''],
      month: [data ? data.month : ''],
      day: [data ? data.day : ''],
      overdraftLimitRenewed: [data ? data.overdraftLimitRenewed : ''],
    });
    if (data) {
      console.log("values:", data.renewPeriod);
      if (data.renewPeriod) {
        this.selectedYear = data.renewPeriod.substring(0, 4);
        let selectedMonth = data.renewPeriod.substring(5, 8);
        this.selectedDay = data.renewPeriod.substring(9, 11);

        this.unsecuredUpdateForm.get('year').setValue(Number(this.selectedYear));
        this.unsecuredUpdateForm.get('month').setValue(selectedMonth);
        this.unsecuredUpdateForm.get('day').setValue(Number(this.selectedDay));
        this.cdr.markForCheck();
      }
    }
  }

  onSubmit() {
    if (this.unsecuredUpdateForm.value.overdraftLimitRenewed) {
      if (this.unsecuredUpdateForm.controls.day.value == null || this.unsecuredUpdateForm.controls.year.value == null || this.unsecuredUpdateForm.controls.month.value == null) {
        this.snack.open('Please give Renew Period', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        return;
      }
    }
    let startDate = this.convertDate(this.unsecuredUpdateForm.controls.limitStartDate.value);
    this.unsecuredUpdateForm.value['limitStartDate'] = startDate;
    let endDate = this.convertDate(this.unsecuredUpdateForm.controls.limitEndDate.value);
    this.unsecuredUpdateForm.value['limitEndDate'] = endDate;
    let renewData;
    if (this.unsecuredUpdateForm.value.overdraftLimitRenewed) {
      renewData = this.unsecuredUpdateForm.value.year + '-' + this.unsecuredUpdateForm.value.month + '-' + this.unsecuredUpdateForm.value.day;
    } else {
      renewData = '';
    }

    let limitData = this.unsecuredUpdateForm.value.limitId;
    let currencyData = this.unsecuredUpdateForm.value.overdraftCurrency;
    let overdraftLAData = this.unsecuredUpdateForm.value.overdraftLimitAmount;
    let limitStartDateData = this.unsecuredUpdateForm.value.limitStartDate;
    let limitEndDateData = this.unsecuredUpdateForm.value.limitEndDate;
    let overdraftLimitRenewed = this.unsecuredUpdateForm.value.overdraftLimitRenewed;
    let renewalAmountData = this.unsecuredUpdateForm.value.renewalAmount;
    let finalPayload = {
      accountId: this.accountId,
      limitId: limitData,
      overdraftCurrency: currencyData,
      overdraftLimitAmount: overdraftLAData,
      limitStartDate: limitStartDateData,
      limitEndDate: limitEndDateData,
      renewPeriod: renewData,
      renewalAmount: renewalAmountData,
      overdraftLimitRenewed: overdraftLimitRenewed
    };
    if (finalPayload.limitId == null || finalPayload.overdraftLimitAmount == null) {
      Swal.fire('Please Enter The Mandatory Fields !');
      return false;
    }

    if (this.unsecuredUpdateForm.value.limitId != '') {
      this.api.unsecuredDraft(finalPayload).subscribe(resp => {
        this.snack.open('UnSecured Overdraft Details Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
            this.dialogRef.close({
              status: 'BACKLOG'
        })
      }, err => {
        this.snack.open(err['message'], 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      })
    }

  }

  getCurrencyDetails() {
    this.api.fetchCurrencyMaintAuthRecs().subscribe(resp => {
      this.currency = resp
    })
  }
  onToggle(event) {
    if (event.checked === true) {
      this.flag = true;
    }
    else {
      this.flag = false;
      this.unsecuredUpdateForm.get('renewalAmount').setValue('');
    }
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  convertDate(value) {
    let date = moment(value).format('YYYY-MMM-DD');
    console.log("date", date);
    if (date)
      return date;
  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }
}