@Component({
  selector: 'app-loan-account-details-edit',
  templateUrl: './loan-account-details-edit.component.html',
  styleUrls: ['./loan-account-details-edit.component.scss']
})
export class LoanAccountDetailsEditComponent implements OnInit {
  loanDetailsUpdateForm: FormGroup;
  isEdit:boolean=false;
  resultOfScreen: string[];
  screenArray: any;
  screenArrayData: any;
  productName: any;
  arrayAccountBranch: any;
  entityName: string;
  accountType: any;
  accountDetails: any;
  businessProductName: any;
  accountBranch: any;
  applicationDate: any;
  estimatedCost: any;
  customerContribution: any;
  minDate = new Date();
  maxDate = new Date();
  loanAmount: any;
  purposeOfLoan: any;
   branchName: any;
  currencyData: any;
  showEditOption: boolean = false;
  loanaccountID: any;
  @Input() public loanDetailsInfo: any;
  appEntrySummaryView: boolean = false;
  loanInfo: any;

  loanAssetForm: FormGroup;
  arrayCountry: any[] = [];
  arrayState: any[] = [];
  arrayCity: any[] = [];
 // entityName: string;
  assetDetails: any
  loanAccountId: any;
  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;

  
  constructor(private router: Router,   @Inject(MAT_DIALOG_DATA) public data: any, private snack: MatSnackBar,
    private dataService: DataService, private ls: LocalStoreService, private fb:FormBuilder,private taskSumService: TasksummaryService,
    private apiService: ApiService, public dialogRef: MatDialogRef<LoanAccountDetailsEditComponent>,
    private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {

    this.loanaccountID = this.data.loanAccountId;
    console.log(this.loanaccountID);
  
    console.log("this.loanaccountID", this.loanaccountID);

 //   this.getAssetDetails();
    this.buildAssetForm(this.data);
    this.getCountryName("country");
    this.getStateName("state");
    this.getCityName("city");

  }

  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }

  getAssetDetails(){
    //this.loader.open();
    //this.apiService.getAssetValuation(this.loacnAccountId).subscribe((accountDetails) => {
    this.apiService.getAssetails(this.loanaccountID).subscribe((assetDetails) => {
   //   this.loader.close();
      this.assetDetails = assetDetails;
      this.buildAssetForm(this.assetDetails);
      this.getStateName(this.assetDetails.country);
      this.getCityName(this.assetDetails.state);
      console.log(this.assetDetails);
 
    }, (err: any) => {
   //   this.loader.close();
    });
  
    
  }


  submitForm1(){
    
  

    let payload={
      // loanAccountId: this.showEditOption ? this.loanaccountID : null,
      loanAccountId: this.data.loanAccountId,
      country:this.loanAssetForm.value.country,
      state:this.loanAssetForm.value.state,
      city: this.loanAssetForm.value.city,
      assetStatus:this.loanAssetForm.value.assetStatus,
      estimatedCost: this.loanAssetForm.value.estimatedCost,
      dimensions: this.loanAssetForm.value.dimensions,
      street:this.loanAssetForm.value.street,
      locality:this.loanAssetForm.value.locality,
      marketValue:this.loanAssetForm.value.marketValue,
      mortgagedBranch:this.loanAssetForm.value.marketValue,
      building:this.loanAssetForm.value.building,
      homeType:this.loanAssetForm.value.homeType,
      zipCode: this.loanAssetForm.value.zipCode
      
  
    }
    console.log("payload",payload)
    this.apiService.upsertAssetDetails(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
      // this.loanAccountId=data.loanAccountId;
      // console.log("loanAccountid",this.loanAccountId);

      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)
      
     
      // this.assign(data.loanAccountId)
      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };
       

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
       // this.ls.setItem('loanAccountId',this.data.loanAccountId);
        this.ls.setItem('productName',data.businessProductName);
    //    this.loanaccountID = this.ls.getItem('loanAccountid');
        
    this.loanaccountID = this.data.loanAccountId;
        console.log("this.productaccountID", this.loanaccountID);
        this.cdr.markForCheck();
        this.cdr.detectChanges();
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
       
      this.goToClose();
    })
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }

 
   goBack(){
    this.dialogRef.close();
  }
  
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
  

    this.cdr.markForCheck();
}
  

  getBusinessProductDetails(key: string){
    this.apiService.getBusinessProductName(key).subscribe((data) => {
       console.log("data:: AccountBranch ", data)
       this.arrayAccountBranch = data;
      
     });
   }
   getAccountBranchDetails() {
    //debugger
    this.apiService.getAccountBranch().subscribe((data) => {
      console.log("data:: AccountBranch ", data)
      this.screenArray = data;
      this.screenArrayData=data;
      if (this.screenArray.length == 0) {
        this.screenArray.push(this.branchName);
      }
      console.log("this.branchName::", this.branchName, this.screenArray)
      this.currencyData = this.screenArray.find(x => x.branchName == this.branchName);
      console.log("Currency", this.currencyData)


    });
  }
  buildAssetForm(data?) {
    console.log('item',data)
    this.getBusinessProductDetails("business_product_name");
    this.getAccountBranchDetails();
    this.getCountryName("country");
    this.getStateName("state");
    this.getCityName("city");
    
    this.loanAssetForm = this.fb.group({
        loanAccountId: [this.loanaccountID],
        street: [ data ? data?.assetInfo?.street: ''],
        country:[data ? data?.assetInfo?.country:''],
        state:[data ? data?.assetInfo?.state:''],
        city: [data ? data?.assetInfo?.city : ''],
        assetStatus:[data ? data?.assetInfo?.assetStatus:''],
        estimatedCost:[data ?data?.assetInfo?.estimatedCost:''],
        dimensions:[data ? data?.assetInfo?.dimensions:''],
        locality:[data ? data?.assetInfo?.locality:''],
        marketValue:[data ? data?.assetInfo?.marketValue:''],
        building:[data ? data?.assetInfo?.building:''],
        zipCode: [data ? data?.assetInfo?.zipCode : ''],
        homeType:[data ? data?.assetInfo?.homeType:''],
        mortgagedBranch: [data ? data?.assetInfo?.mortgagedBranch:'']
     
  
  
 
    
    });
    console.log('form before',this.loanAssetForm)
    console.log('form aftr',this.loanAssetForm.value)
  }

  getCountryName(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayCountry = data;
      console.log("-->", this.arrayCountry)
    });
  }
  getStateName(country) {
    this.entityName = "state";
    console.log("State -- ", country)
    this.apiService.getDropDownValues1(this.entityName, country).subscribe((data) => {
      this.arrayState = data;
      console.log("-->", this.arrayState)
    });
  }
  getCityName(state: any) {
    this.entityName = "city";
    console.log(state)
    this.apiService.getDropDownValues1(this.entityName, state).subscribe((data) => {
      this.arrayCity = data;
      console.log("-->", this.arrayCity)
     
  
    });
  }

  goToEdit() {
    this.dialogRef.close();
    this.dataService.sendNavParam(this.accountDetails);
    this.router.navigate([`loan/account/loan-details`]);
  }
  goToClose(): void {
    this.dialogRef.close();
    this.getDataOnLoad();
  }
 
}


      






     








       









