import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-interest-details-update',
  templateUrl: './interest-details-update.component.html',
  styleUrls: ['./interest-details-update.component.scss']
})
export class InterestDetailsUpdateComponent implements OnInit {

  interestUpdateForm: FormGroup
  accountId: any;
  accountType: any;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<InterestDetailsUpdateComponent>,
    private api: ApiService,
    private ls: LocalStoreService,
    private formBuilder: FormBuilder,
    public snack: MatSnackBar,
    private tasksummaryService: TasksummaryService
  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.accountId = this.ls.getItem('accountId');
    this.accountType = this.ls.getItem('accountType');
    this.interestUpdateForm = this.formBuilder.group({
      interest: new FormArray([]),
    });
    if (this.data != null) {
      this.buildInterestForm(this.data.length);
      this.in.patchValue(this.data)
    }
    this.api.getFlexcubeDetails("intrestDetails", this.accountType).subscribe(response => {
      this.buildInterestForm(response.length)
      this.in.patchValue(response)
    })
  }
  buildInterestForm(e) {
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            intrestRateApplicable: [""],
            marginIn: ["", [Validators.required, Validators.max(100), Validators.min(0)]],
            effectiveRate: ["", [Validators.required, Validators.max(100), Validators.min(0)]],
            accountId: [""],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
            id: ['']
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }
  get f() {
    return this.interestUpdateForm.controls;
  }
  get in() {
    return this.f.interest as FormArray;
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  onSubmit(form) {
    form.interest = form.interest.map(interest => ({
      ...interest,
      accountId: this.accountId,
    }))

    this.api.intrestDetailsSave(form.interest).subscribe(resp => {
      this.snack.open('Interest Details Updated!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
          this.dialogRef.close({
            status: 'BACKLOG'
          })
    }, err => {
      this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }
}
