import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-card-approval-details-update',
  templateUrl: './card-approval-details-update.component.html',
  styleUrls: ['./card-approval-details-update.component.scss']
})
export class CardApprovalDetailsUpdateComponent implements OnInit {
  cardApprovalUpdateForm: any;
  arrayAccountType: string[];
  arrayAccountCurrency: string[];
  arrayUserRecomendation: string[];
  arrayAccountBranch: any;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private apiService: ApiService,
    public dialogRef: MatDialogRef<CardApprovalDetailsUpdateComponent>,
    public snack: MatSnackBar
  ) { }

  ngOnInit(): void {
    this.getUserRecomendation("user_recomendation");
    this.getAccountType("account_type")
    this.getAccountBranchDetails();
    if(this.data.approvalInfo != null){
      this.buildformGroup(this.data.approvalInfo);
    }

else{
  this.buildformGroup();
}
  }
  buildformGroup(data?){
    this.cardApprovalUpdateForm = this.fb.group({
      accountType:[data ? data.accountType : '',Validators.required],
      productCode:[data ? data.productCode :'',Validators.required],
      userRecommendation:[data ? data.userRecommendation :'',Validators.required],
      accountBranch:[data ? data.accountBranch :'',Validators.required],
      accountCurrency:[data ? data.accountCurrency :'',Validators.required],
      productName:[data ? data.productName :'',Validators.required],
      existingValues:[data ? data.existingValues :'',Validators.required],
      remarks:[data ? data.remarks :'']
    })
  }

  onSubmit(){
    console.log("data", this.cardApprovalUpdateForm.value);
    
    let payload={
      cardId: this.data.cardId,
      accountBranch: this.cardApprovalUpdateForm.value.accountBranch,
      accountCurrency: this.cardApprovalUpdateForm.value.accountCurrency,
      accountType: this.cardApprovalUpdateForm.value.accountType,
      existingValues: this.cardApprovalUpdateForm.value.existingValues,
      productCode: this.cardApprovalUpdateForm.value.productCode,
      productName: this.cardApprovalUpdateForm.value.productName,
      remarks: this.cardApprovalUpdateForm.value.remarks,
      userRecommendation: this.cardApprovalUpdateForm.value.userRecommendation,

    }
    this.apiService.upsertApprovalDetails(payload)
    .subscribe((data: any) => {

      this.snack.open('Data Updated!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      let statusModel = {
        cardId: data.cardId,
        status: "BACKLOG"
      };
      this.apiService.updateCardStatus(statusModel).subscribe(data => {
        this.dialogRef.close({
          status: data.status
         });
      });
    }, err => {
      this.snack.open('Error Unable to Update Data!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      
    });
  }

  onKeyPress(event: any) {
    const number = /[0-9\+\-\ ]/;
    let inputCharacter = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !number.test(inputCharacter)) {
      event.preventDefault();
      console.log("event:", event)
    }
  }

  getAccountCurrency(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountCurrency = data;
    });
  }

  getAccountType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountType = data;
    });
  }
  
  getUserRecomendation(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayUserRecomendation = data;
    });
  }

  getAccountBranchDetails() {
    //debugger
    this.apiService.getAccountBranch().subscribe((data) => {
      console.log("data:: AccountBranch ", data)
      this.arrayAccountBranch = data;
    });
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  close(){
    this.dialogRef.close({
      status:"ONGOING"
    })
  }
}
