import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-charge-details-update',
  templateUrl: './charge-details-update.component.html',
  styleUrls: ['./charge-details-update.component.scss']
})
export class ChargeDetailsUpdateComponent implements OnInit {

  chargeUpdateForm: FormGroup
  accountId: any;
  accountType: any;
  flag: any;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<ChargeDetailsUpdateComponent>,
    private api: ApiService,
    private formBuilder: FormBuilder,
    private ls: LocalStoreService,
    public snack: MatSnackBar,
    private tasksummaryService: TasksummaryService
  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.accountId = this.ls.getItem('accountId');
    this.accountType = this.ls.getItem('accountType');
    this.chargeUpdateForm = this.formBuilder.group({
      charges: new FormArray([])
    });
    if (this.data != null) {
      this.buildChargeForm(this.data.length)
      this.c.patchValue(this.data)
    }
    this.api.getFlexcubeDetails("chargeDetails", this.accountType).subscribe(response => {
      this.buildChargeForm(response.length)
      this.c.patchValue(response)
    })

  }
  buildChargeForm(e) {
    const numberOfItems = e || 0;
    if (this.c.length < numberOfItems) {
      for (let i = this.c.length; i < numberOfItems; i++) {
        this.c.push(this.formBuilder.group({
          intrestType: [''],
          amount: ['', Validators.required],
          waiver: [false],
          accountId: [this.accountId],
          createdDate: [''],
          craetedBy: [''],
          lastModifiedDate: [''],
          lastModifiedBy: [''],
          id: ['']

        }));
      }
    } else {
      for (let i = this.c.length; i >= numberOfItems; i--) {
        this.c.removeAt(i);
      }
    }
  }
  get f() { return this.chargeUpdateForm.controls; }
  get c() { return this.f.charges as FormArray; }
  onSubmit(form) {
    form.charges = form.charges.map(charges => ({
      ...charges,
      accountId: this.accountId,
    }))
    form.accountId = this.accountId
    this.api.chargeDetailsSave(form.charges).subscribe(resp => {
      this.snack.open('Account Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
          this.dialogRef.close({
            status: 'BACKLOG'
          })
    }, err => {
      this.snack.open('Server Error!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })

  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  checkboxChanged(event) {
    console.log(event.checked);
    this.flag = event.checked;
  }
  close() {
    this.dialogRef.close({
      status: "ONGOING"
    })
  }
}