import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CorporateOnboardingService } from 'app/views/others/onboarding-corporate/corporate-onboarding.service';
@Component({
  selector: 'app-update-corporate-addititonal-info',
  templateUrl: './update-corporate-addititonal-info.component.html',
  styleUrls: ['./update-corporate-addititonal-info.component.scss']
})
export class UpdateCorporateAddititonalInfoComponent implements OnInit {
  otherInfoUpdateForm: FormGroup;
  organizationTypeList: any = ['Public', 'Private'];
  typeOfOwnershipList: any = ['Independent', 'Dependent'];
  kycStatusList: any = ['APPROVED', 'BACKLOG', 'INITIATED', 'REJECTED'];
  otherDocumentArray: any[] = [];
  corporateId: any;
  documentTypeList: string[];
  selectedOptions: any[] = [];
  selectedDocumentTypes: any[] = [];
  corporateDocs: any[] = [];

  constructor(
    private dialogRef: MatDialogRef<UpdateCorporateAddititonalInfoComponent>,
    private formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data,
    private ls: LocalStoreService,
    private apiService: ApiService,
    private corporateService: CorporateOnboardingService,
    private snack: MatSnackBar
  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.corporateId = this.ls.getItem('corporateId')
    this.apiService.getDropDownValues("document_type").subscribe((data) => {
      this.documentTypeList = data;
    });
    if (this.data) {
      this.data.corporateDocs = this.data.corporateDocs.filter(x => x.documentName != 'Profile')
      this.buildOtherFormGroup(this.data);
      for (let i = 0; i < this.data.corporateDocs.length; i++) {
        this.addDocument()
        this.otherInfoUpdateForm.controls.corporateDocs['controls'][i].controls.documentType.setValue(this.data.corporateDocs[i].documentName);
        this.otherInfoUpdateForm.controls.corporateDocs['controls'][i].controls.fileData.setValue(this.data.corporateDocs[i].fileUrl); 
        this.otherInfoUpdateForm.controls.corporateDocs['controls'][i].controls.documentName.setValue(this.data.corporateDocs[i].fileName);
      }
    }
  }

  buildOtherFormGroup(data?) {
    this.otherInfoUpdateForm = this.formBuilder.group({
      corporateId: [this.corporateId],
      organizationType: [data ? data.organizationType : '',Validators.required],
      typeOfOwnership: [data ? data.typeOfOwnership : '',Validators.required],
      kycReference: [data ? data.kycReference : ''],
      KycStatus: [data ? data.kycStatus : ''],
      corporateDocs: this.formBuilder.array([])
    })
  }
  get getDocumentList() {
    return this.otherInfoUpdateForm.get('corporateDocs') as FormArray;
  }
  addDocument() {
    this.getDocumentList.push(this.formBuilder.group({
      documentType: ['',[Validators.required]],
      documentName: ['', [Validators.required]],
      fileData: ['']
    }));
  }
  deleteDocument(item, i) {
    this.getDocumentList.removeAt(i);
  }
  isDocumentOptionDisabled = (value) => {
    return this.selectedOptions.includes(value);
  }
  changeValue(value: any, index) {

    console.log(index);
    if (this.selectedDocumentTypes[index]) {
      this.selectedOptions = this.selectedOptions.filter(val => val !== this.selectedDocumentTypes[index])
    }
    this.selectedDocumentTypes[index] = value;

    console.log(value)
    this.selectedOptions.push(value);
  }
  onOtherDocSelect(files: FileList, idx: any, fileName: String) {
    this.otherDocumentArray.push(files.item(0));
    this.otherInfoUpdateForm.get('corporateDocs')['controls'][idx].controls.documentName.setValue(fileName);
    let reader = new FileReader();
    reader.readAsDataURL(files.item(0));
    reader.onload = (event2) => {
      this.otherInfoUpdateForm.get('corporateDocs')['controls'][idx].controls.fileData.setValue(reader.result);
    };
  }
  onSubmit(otherInfoUpdateForm) {
    let obj = {}
    Object.keys(otherInfoUpdateForm).map(function (key, index) {
      if (key != 'corporateDocs') {
        obj[key] = otherInfoUpdateForm[key]
      }
    });
    this.corporateService.saveOrUpdateCorporateAccountInfo(obj)
      .subscribe(data => {
        if (data != null) {
          for (let i = 0; i < this.otherInfoUpdateForm.get('corporateDocs').value.length; i++) {
            if (this.otherDocumentArray.length > 0) {
              let documentToBeUploaded: any = {};
              documentToBeUploaded.documentName = this.otherInfoUpdateForm.get('corporateDocs').value[i].documentType;
              documentToBeUploaded.documentType = 2;
              documentToBeUploaded.fileType = this.otherDocumentArray[i].type;
              documentToBeUploaded.fileName = this.otherDocumentArray[i].name;
              documentToBeUploaded.corporateId = data.corporateId;
              documentToBeUploaded.id = this.otherDocumentArray[i].id;
              const uploadData = new FormData();
              uploadData.append('data', JSON.stringify(documentToBeUploaded));
              uploadData.append('file', this.otherDocumentArray[i]);
              this.apiService.uploadCorporateDocument(uploadData).subscribe((resp) => {
                console.log(resp);
                this.corporateDocs.push(resp)
              });
            }
          }
        } else {
          this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
        this.dialogRef.close({
          status:'INITIATED',
          resp:{typeOfOwnership:data.typeOfOwnership, organizationType:data.organizationType, corporateDocs:this.corporateDocs}
        })
      }, error => {
        console.log('error resp :: ', error);
        this.snack.open(`${error.error.message} `, 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  close() {
    this.dialogRef.close({
      status: 'UNDER_PROCESS'
    })
  }
}
