import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DocumentOcrService } from 'app/views/others/genric-document-upload/document-ocr.service';

@Component({
  selector: 'app-update-proof-of-identity',
  templateUrl: './update-proof-of-identity.component.html',
  styleUrls: ['./update-proof-of-identity.component.scss']
})
export class UpdateProofOfIdentityComponent implements OnInit {
  customerId: any
  screenInfo={
    screenName:'onboarding',
    data:'',
    customerId: '',
    isEdit: true
  }
  disableNextAtIndex1: boolean = false;
  constructor(
    private ls: LocalStoreService,
    @Inject(MAT_DIALOG_DATA) public data,
    private formBuilder: FormBuilder,
    private dialogRef: MatDialogRef<UpdateProofOfIdentityComponent>,
    private doOcrService:DocumentOcrService,

  ) { }

  ngOnInit(): void {
    this.customerId = this.ls.getItem('customerId')
    this.ls.setItem('customer_id',this.customerId)
    this.screenInfo={
      screenName:'onboarding',
      data:this.data,
      customerId: this.customerId,
      isEdit:true
    }
    this.doOcrService.getenableNext()
    .subscribe(data=>{
      console.log("data disableNextAtIndex1 :: ",data);
      this.disableNextAtIndex1=data;
    });
    
  }
  createDocItemOthers(item): FormGroup {
    return this.formBuilder.group({
      id: item ? item.id : [''],
      documentType: item ? item.documentName : [''],
      documentName: item ? item.fileName : [''],
      fileData: item ? item.fileData : [''],
      fileUrl: item ? item.fileUrl : [''],
      fileUrl2: item ? item.fileUrl2 : [''],
      documentSide: item ? item.documentSide : ['']
    })
  }
onSubmit(){
  this.dialogRef.close({
    status:'INITIATED',
    screen:'documentUpload'
  })
}
close(){
  this.dialogRef.close({
    status:'ONGOING'    
  })
}
}
