import { Component, OnInit, Inject, ChangeDetectorRef } from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TasksummaryService } from '../../tasksummary.service';

@Component({
  selector: 'app-loan-valuation-of-asset-update',
  templateUrl: './loan-valuation-of-asset-update.component.html',
  styleUrls: ['./loan-valuation-of-asset-update.component.scss']
})
export class LoanValuationOfAssetUpdateComponent implements OnInit {
  loanAccountId: any;
  loanaccountID: any;
  loanValuationForm: FormGroup;
  creditDetails: any;
  valuationOfAssetDetails: any;

  
  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;

  constructor(
    private apiService: ApiService, @Inject(MAT_DIALOG_DATA) public data: any, private fb: FormBuilder,
  private loader: AppLoaderService, private taskSumService: TasksummaryService, private cdr: ChangeDetectorRef,
  private ls: LocalStoreService, public dialogRef: MatDialogRef<LoanValuationOfAssetUpdateComponent>, private snack: MatSnackBar
  ) { }

  ngOnInit(): void {
    this.loanaccountID = this.data.loanAccountId;
    console.log('loanaccountID', this.loanaccountID)
    this.buildLoanValuationForm(this.data)
  }

  _keyPress(event: any) {
   
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    console.log("inputChar",inputChar);
    inputChar=inputChar.toString();
    console.log("inputChar1",inputChar);
    inputChar=inputChar
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
   buildLoanValuationForm(data?) {
    console.log('item',data)
   
     
    this.loanValuationForm= this.fb.group({
        loanAccountId: [this.loanaccountID],
      valuationDate: [data ? data?.loanValuationOfAssetInfo?.valuationDate : ''],
      forcedSaleValue:[data ? data?.loanValuationOfAssetInfo?.forcedSaleValue:''],
      propertyAreaSize: [data ? data?.loanValuationOfAssetInfo?.propertyAreaSize : ''],
        bankValuation: [data ? data?.loanValuationOfAssetInfo?.bankValuation : ''],
        assetType:[data ? data?.loanValuationOfAssetInfo?.assetType: ''],
        actualPropertyAreaSize: [data ? data?.loanValuationOfAssetInfo?.actualPropertyAreaSize : ''],
        borrowersMktValOfAsset: [data ? data?.loanValuationOfAssetInfo?.borrowersMktValOfAsset : ''],
        faceValOfAsset: [data ? data?.loanValuationOfAssetInfo?.faceValOfAsset : ''],
      mktValOfAsset: [data ? data?.loanValuationOfAssetInfo?.mktValOfAsset : ''],
      propertyAreaInUnits: [data ? data?.loanValuationOfAssetInfo?.propertyAreaInUnits : ''],
   
      

    });


  }
  getLoanCreditDetails() {
 
    this.apiService.gettingValuationDetails(this.loanaccountID).subscribe((valuationOfAssetDetails) => {
 
      this.valuationOfAssetDetails = valuationOfAssetDetails;
      console.log("this.creditDetails::", this.valuationOfAssetDetails);
    

      this.buildLoanValuationForm(this.valuationOfAssetDetails);
      this.loanValuationForm.value.faceValOfAsset = this.valuationOfAssetDetails.faceValOfAsset;
      this.loanValuationForm.value.forcedSaleValue = this.valuationOfAssetDetails.forcedSaleValue;
      this.loanValuationForm.value.bankValuation = this.valuationOfAssetDetails.bankValuation;
      this.loanValuationForm.value.mktValOfAsset = this.valuationOfAssetDetails.mktValOfAsset;
      this.loanValuationForm.value.borrowersMktValOfAsset = this.valuationOfAssetDetails.borrowersMktValOfAsset;
      this.loanValuationForm.value.propertyAreaSize = this.valuationOfAssetDetails.propertyAreaSize;
      this.loanValuationForm.value.propertyAreaInUnits = this.valuationOfAssetDetails.propertyAreaInUnits;
      this.loanValuationForm.value.assetType = this.valuationOfAssetDetails.assetType;
      this.loanValuationForm.value.actualPropertyAreaSize = this.valuationOfAssetDetails.actualPropertyAreaSize;

      
      
     
    }, (err: any) => {
      this.loader.close();
    });
  }

  goToClose(): void {
    this.dialogRef.close();
   
  
    this.apiService.gettingValuationDetails(this.loanaccountID).subscribe((valuationOfAssetDetails: any) => {
      console.log("All Account Service", valuationOfAssetDetails);
      this.valuationOfAssetDetails = valuationOfAssetDetails;

      console.log("this.accountDetails", this.valuationOfAssetDetails);
      console.log('form',this.loanValuationForm.value)
   
    

   });
   
  }
  submitForm1(){
    

    let payload={
 
      loanAccountId: this.data.loanAccountId,
      bankValuation: this.loanValuationForm.value.bankValuation,
      assetType: this.loanValuationForm.value.assetType,
      propertyAreaSize: this.loanValuationForm.value.propertyAreaSize,
      actualPropertySizeArea: this.loanValuationForm.value.actualPropertySizeArea,
      borrowersMktValOfAsset: this.loanValuationForm.value.borrowersMktValOfAsset,
      faceValOfAsset: this.loanValuationForm.value.faceValOfAsset,
      mktValOfAsset: this.loanValuationForm.value.mktValOfAsset,
      propertyAreaInSize: this.loanValuationForm.value.propertyAreaInSize,
      propertyAreaInUnits: this.loanValuationForm.value.propertyAreaInUnits,
      valuationDate: this.loanValuationForm.value.valuationDate,
      forcedSaleValue: this.loanValuationForm.value.forcedSaleValue,
      actualPropertyAreaSize: this.loanValuationForm.value.actualPropertyAreaSize,
    }
    console.log("payload",payload)
    this.apiService.saveValuationDetails(payload).subscribe(data => {
      console.log("data",data.loanAccountId);
      this.loanaccountID = this.data.loanAccountId;
      console.log('loanaccountID', this.loanaccountID)

      this.assign(this.data.loanAccountId)
      if (data === 200 || data !== null) {
        let statusModel = {
          loanAccountId: this.data.loanAccountId,
          status: "ONGOING"
        };
        this.apiService.updateLoanDetailsStatus(statusModel).subscribe(data => {
        });
      //  sessionStorage.setItem('loanAccountId', data.loanAccountid);
        let fundStatus = {
          loanAccountid: this.data.loanAccountId
          
        };

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('accountType', data.accountType);
        this.ls.setItem('productName',data.businessProductName);
  
        
    this.loanaccountID = this.data.loanAccountId;
    console.log("this.productaccountID",this.loanaccountID);
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.getDataOnLoad();
      }
      if( data === 500){
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
      
      this.goToClose();
    })
  }
  assign(valueForaccId) {
    this.apiService.setAccountId({
      loanAccountId: valueForaccId
    });
    console.log("loan", this.loanAccountId)
  }
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }

  }

   fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
      if (status == 'ONGOING') {
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }
 

    this.cdr.markForCheck();
  }

}













