import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-update-additional-info',
  templateUrl: './update-additional-info.component.html',
  styleUrls: ['./update-additional-info.component.scss']
})
export class UpdateAdditionalInfoComponent implements OnInit {
  additionalInfoUpdateForm: FormGroup
  arrayEmploymentType: any;
  arrayEmployerName: any;
  kycList: string[] = ['INITIATED', 'UNDER_PROCESS', 'APPROVED', 'REJECTED', 'VERIFIED',];
  othersFlag: boolean;
  customerId: any;
  constructor(
    @Inject(MAT_DIALOG_DATA) public additionalInfo: any,
    private dialogRef: MatDialogRef<UpdateAdditionalInfoComponent>,
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private ls: LocalStoreService,
  ) { }

  ngOnInit(): void {
    console.log(this.additionalInfo);
    this.customerId = this.ls.getItem('customerId')
    this.getEmploymentType("employment_type");
    this.getEmployerName("employer_name");
    if (this.additionalInfo) {
      this.buildAdditionalInfoForm(this.additionalInfo)
      this.OnInput(this.additionalInfoUpdateForm.get('salary').value)
    }
    else {
      this.buildAdditionalInfoForm()
    }
  }
  buildAdditionalInfoForm(data?) {
    this.additionalInfoUpdateForm = this.formBuilder.group({
      employmentType: [data ? data.employmentType : '', [Validators.required]],
      employerName: [data ? data.employerName : '', [Validators.required]],
      otherEmployerName: [data ? data.otherEmployerName : ''],
      salary: [data ? data.salary : '', [Validators.required]],
      kycReference: [data ? data.kycReference : '', [Validators.required]],
      kycStatus: [data ? data.kycStatus : '', [Validators.required]]
    })

  }
  getEmploymentType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayEmploymentType = data;
      console.log(this.arrayEmploymentType);

    });
  }
  getEmployerName(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayEmployerName = data;
    });
  }
  OnInput(val) {
    let salary = val.toString().replace(/,/g, '').split(".");
    salary[0] = salary[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    let final = salary.join(".");
    console.log("final", final);
    this.additionalInfoUpdateForm.controls.salary.setValue(final);
  }

  letterOnly(event) {
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }
  _keyPress1(event: any) {
    const pattern = /^[0-9]*\.?[0-9]*$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    const input = event.target.value;
    if (input.length === 0 && event.which === 48) {
      event.preventDefault();
    }
  }
  onSubmit() {
    let obj: any = {};
    obj.customerId = this.customerId
    obj.customerAdditionalInfo = this.additionalInfoUpdateForm.value
    obj.customerAdditionalInfo.salary = obj.customerAdditionalInfo.salary.replaceAll(',', '');
    this.apiService.insertNewUserOnboarding(obj).subscribe((result) => {
      console.log(result);
      this.dialogRef.close({
        status: 'INITIATED',
        resp: this.additionalInfoUpdateForm.value
      })
    });
  }
  close() {
    this.dialogRef.close({
      status: "UNDER_PROCESS"
    })
  }
}
