import { Component, OnInit, ChangeDetectorRef, EventEmitter, Output, HostListener } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DomSanitizer } from '@angular/platform-browser';
import { SortCriteria } from 'app/shared/pipes/custom-task-summary-sorting.pipe';
import { ApiService } from 'app/shared/services/api.service';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { ApproverComponent } from '../approver/approver.component';
import { CardAccountApproverComponent } from '../card-account-approver/card-account-approver.component';
import { TasksummaryService } from '../tasksummary.service';
import * as _ from 'lodash';
import { ActivatedRoute } from '@angular/router';



@Component({
  selector: 'app-card-origination-task-summary',
  templateUrl: './card-origination-task-summary.component.html',
  styleUrls: ['./card-origination-task-summary.component.scss']
})

export class CardOriginationTaskSummaryComponent implements OnInit {
  data: any;
  radioButtonSelected: any;
  searchText;
  statusArr: any = ['ONGOING', 'BACKLOG', 'APPROVED', 'REJECTED'];

  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];

  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;

  loading: boolean;

  image: Object;
  searchable: boolean = false;
  isScroll: boolean = true;
  //variable for sorting data
  public criteria: SortCriteria;


  @Output() setValue: EventEmitter<string> = new EventEmitter();
  private _searchSubject: Subject<string> = new Subject();
  noDataFound: boolean = false;
  redirectedFromTellerSearch: boolean = false;

  constructor(public dialog: MatDialog, private sanitizer: DomSanitizer, public tasksummaryService: TasksummaryService,
    private apiService: ApiService, public cdr: ChangeDetectorRef, private route: ActivatedRoute) {
    this._setSearchSubscription();
  }
  @HostListener('scroll', ['$event'])
  onScroll(event: any) {
    if(this.isScroll){
      this.ONGOINGPage++;
      this.getDataOnScroll("ONGOING",this.ONGOINGPage,this.ONGOINGTotalPages);
      this.BACKLOGPage++;
      this.getDataOnScroll("BACKLOG",this.BACKLOGPage, this.BACKLOGTotalPages);
      this.APPROVEDPage++;
      this.getDataOnScroll("APPROVED", this.APPROVEDPage, this.APPROVEDTotalPages);
      this.REJECTEDPage++;
      this.getDataOnScroll("REJECTED", this.REJECTEDPage, this.REJECTEDTotalPages);
    }
  }

  ngOnInit(): void {
    var task:boolean=true;
    this.apiService.setTask(task);
    this.route.queryParamMap.subscribe((params: any) => {
      if(params.get('searchTerm')){
        this.redirectedFromTellerSearch = true
        this.searchText =params.get('searchTerm')
        this.updateSearch(this.searchText);
        setTimeout(() => {
          this.openSearchedApprover()
        }, 3000); 
      }
      else{
    /* @called :: on screen load */
    this.getDataOnLoad();
      }
    })

    /* @called :: to refresh data for every 1min interval */
    // setInterval(() => { this.getDataOnLoad() }, 60000);

  }

  /* @method :: on keyup used to search the data */
  public updateSearch(searchTextValue: string) {
    this._searchSubject.next(searchTextValue);
  }

  /* @method :: on keyup to avoid api calling for each time , using subject from rxjs library and debounce concept */
  private _setSearchSubscription() {
    this._searchSubject.pipe(
      debounceTime(1000)
    ).subscribe((searchValue: string) => {
      this.searchKycid(searchValue);
    });
  }

  /* @method :: to load all data on screen load */
  getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.getAllAccDetails(statusItem, 0, 0);
    }
  }

  getAllAccDetails(status: string, page: number, size: number) {
    this.tasksummaryService.getCardAccDetails(status)
      .subscribe((data: any) => {
        console.log("Response", data);
        this.updateStatusResponse(status,data);
      });
  }

  updateStatusResponse(status, data){
    if(status == 'ONGOING'){
      this.ONGOINGArr = data.cardList;
      this.ONGOINGTotalPages = data.totalPages;
      this.ONGOINGTotalRecords = data.totalNoOfElements;
    }
    else if(status == 'BACKLOG'){
      this.BACKLOGArr = data.cardList;
      this.BACKLOGTotalPages = data.totalPages;
      this.BACKLOGTotalRecords = data.totalNoOfElements;
    }
    else if(status == 'APPROVED'){
      this.APPROVEDArr = data.cardList;
      this.APPROVEDTotalPages = data.totalPages;
      this.APPROVEDTotalRecords = data.totalNoOfElements;
    }
    else if(status == 'REJECTED'){ 
      this.REJECTEDArr = data.cardList;
      this.REJECTEDTotalPages = data.totalPages;
      this.REJECTEDTotalRecords = data.totalNoOfElements;
      console.log("this.REJECTEDArr",this.REJECTEDArr);
    }

    this.cdr.markForCheck();
}

  /* @method :: to load data on scroll of data*/
  onScrollingFinished(e, status) {
    if(status == 'ONGOING'){
      this.ONGOINGPage++;
      this.getDataOnScroll(status,this.ONGOINGPage,this.ONGOINGTotalPages);
     }else if(status == 'BACKLOG'){
     this.BACKLOGPage++;
     this.getDataOnScroll(status,this.BACKLOGPage, this.BACKLOGTotalPages);
     }else if(status == 'APPROVED'){
       this.APPROVEDPage++;
       this.getDataOnScroll(status, this.APPROVEDPage, this.APPROVEDTotalPages);
     }else if(status == 'REJECTED'){ 
       this.REJECTEDPage++;
       this.getDataOnScroll(status, this.REJECTEDPage, this.REJECTEDTotalPages);
     }
  }

  /* @method :: to call service ,when scroll method called */
  getDataOnScroll(status,page,totalPage) {
    console.log('---------scrolled----------')
    if (page === totalPage) {
      console.log('No scroll requied')
      return;
    }
    this.tasksummaryService.getAllCardAccDetailOnScroll(status, page)
        .subscribe((data:any) => {
          this.updateDataOnScroll(status, data.cardList);
          this.cdr.markForCheck();
        });
  }
  
  gotoapproverpage(data, status, colCode?) {
    // console.log('data:: ', data);
    this.tasksummaryService.cardStatusData(status);
    localStorage.setItem('CustomerColorCode', colCode);
    const dialogRef = this.dialog.open(CardAccountApproverComponent,
      {
        panelClass: 'full-width-dialog',
        data: data,
        width: '80%',
        disableClose: true
      });
    dialogRef.afterClosed().subscribe((submit) => {
      // console.log('submit :: ', submit);
      if (submit.data === 'REJECTED' || submit.data === 'APPROVED' || submit.data === 'BACKLOG' || submit.data === 'ONGOING') {
        if(this.redirectedFromTellerSearch == true){
          this.updateSearch(this.searchText)
        }
        else{
          this.getDataOnLoad();
        }
      }
    })
  }
  /* @method :: search the data locally on the loaded data array and if no data found in array call api */
  searchKycid(searchTerm) {
    let text:any;
      let params:any;
      if(searchTerm===''){
        console.log('empty')
        this.getDataOnLoad();
        this.isScroll = true;
        return;
      }
      if(this.isNum(searchTerm)){
        if(searchTerm.length == 10){
          console.log("contact");
          text=this.searchText.trim();
          params = 'phoneNumber='+text;
        }
        else{
          console.log("cardId");
          text=this.searchText.trim();
          this.tasksummaryService.getCardAccSearchData1(text)
          .subscribe(data => {
            console.log(data)
            if(data != null){
              params = 'cardId='+text;
              console.log('loan',params);
            }
            else{
              console.log('not a loan')
              params = 'customerId='+text;
              console.log('customer',params);
            }
          })
        }
      }
      else if(this.isString(searchTerm)){
        if( /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(searchTerm)){
          console.log("email");
          text=this.searchText.trim();
          params = 'primaryEmailAdress='+text;
        }
        else{
        console.log("name");
        text=this.searchText.trim();
        params = 'customerName='+text;
        }
      }
      setTimeout(() => {
        this.getAccountData(params);
      }, 100);
      
      this.isScroll = false;
  }

  // getAccountData(params) {
  //   this.loading = true;
  //   console.log("params :: ",params);
  //   this.tasksummaryService.getCardSeracData(params)
  //   .subscribe(data => {
  //     this.loading = false;
  //     console.log('resp data :: ', data);
  //    this.resetSearchData();
  //     for (let index = 0; index < data?.cardList.length; index++) {
  //       const element = data?.cardList[index];
  //       console.log('element ::',element);
  //       this.searchResponseUpdate(element.status,data?.cardList[index]);
        
  //     }
  //   })
  // }

  getAccountData(params) {
    this.loading = true;
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
     // this.fetchCustomerByStatus(statusItem,params,0);
     if(statusItem == 'BACKLOG'){
    this.tasksummaryService.getCardSeracData(params,statusItem)
    .subscribe(data => {
      this.BACKLOGTotalPages = data?.totalPages;
      this.loading = false;
      console.log('resp data :: ', data);
      this.BACKLOGArr = [];
      for (let index = 0; index < data?.cardList.length; index++) {
        const element = data?.cardList[index];
        this.BACKLOGArr.push(element);
        
      }
      
    })
  }
  else  if(statusItem == 'ONGOING'){
    this.tasksummaryService.getCardSeracData(params,statusItem)
    .subscribe(data => {
      this.ONGOINGTotalPages = data?.totalPages;
      this.loading = false;
      console.log('resp data :: ', data);
      this.ONGOINGArr = [];
      for (let index = 0; index < data?.cardList.length; index++) {
        const element = data.cardList[index];
        this.ONGOINGArr.push(element);
        
      }
       
    })
  }
  else  if(statusItem == 'APPROVED'){
    this.tasksummaryService.getCardSeracData(params,statusItem)
    .subscribe(data => {
      this.APPROVEDTotalPages = data?.totalPages;
      this.loading = false;
      console.log('resp data :: ', data);
      this.APPROVEDArr = [];
      for (let index = 0; index < data?.cardList.length; index++) {
        const element = data.cardList[index];
        this.APPROVEDArr.push(element);
        
      }
       
    })
  }
  else  if(statusItem == 'REJECTED'){
    this.tasksummaryService.getCardSeracData(params,statusItem)
    .subscribe(data => {
      this.REJECTEDTotalPages = data?.totalPages;
      this.loading = false;
      console.log('resp data :: ', data);
      this.REJECTEDArr = [];
      for (let index = 0; index < data?.cardList.length; index++) {
        const element = data.cardList[index];
        this.REJECTEDArr.push(element);
        
      }
    })
  }
  }
  }

  resetSearchData(){
    this.ONGOINGArr = [];
    this.BACKLOGArr = [];
    this.APPROVEDArr = [];
    this.REJECTEDArr = [];
    this.cdr.markForCheck();
  }

  clear(){
    this.searchText = "";
    this.isScroll = true;
    this.getDataOnLoad();
  }

  isNum(val){
    return !isNaN(val)
  }

  isString(val){
    return val;
  }

  searchResponseUpdate(status:string, data: any) {
    console.log("status::",status)
    if(status == 'ONGOING'){
      // this.ONGOINGArr = [];
      this.ONGOINGArr.push(data);
     }else if(status == 'BACKLOG'){
      // this.BACKLOGArr = [];
      this.BACKLOGArr.push(data);
     }else if(status == 'APPROVED'){
      // this.APPROVEDArr = [];
      this.APPROVEDArr.push(data);
     }else if(status == 'REJECTED'){ 
      // this.REJECTEDArr = [];
      this.REJECTEDArr.push(data);
     }
    this.cdr.markForCheck();
  }

  updateDataOnScroll(status:string, data: any) {
    if(status == 'ONGOING'){
      const result = _.unionBy(this.ONGOINGArr, data, 'cardId');
      this.ONGOINGArr = result;
    }else if(status == 'BACKLOG'){
      const result = _.unionBy(this.BACKLOGArr, data, 'cardId');
      this.BACKLOGArr = result;
    }else if(status == 'APPROVED'){
      const result = _.unionBy(this.APPROVEDArr, data, 'cardId');
      this.APPROVEDArr = result;
    }else if(status == 'REJECTED'){ 
      const result = _.unionBy(this.REJECTEDArr, data, 'cardId');
      this.REJECTEDArr = result;
    }

    this.cdr.markForCheck();
  }
  openSearchedApprover(){
    let redirectArr = [this.REJECTEDArr,this.APPROVEDArr,this.ONGOINGArr,this.BACKLOGArr].filter(arr => arr.length == 1);
    console.log(redirectArr);
    if(redirectArr.length == 1)
    this.gotoapproverpage(redirectArr[0][0],redirectArr[0][0].custStatus);
  }


  ngOnDestroy() {
    this._searchSubject.unsubscribe();
    this.BACKLOGArr = [];
    this.APPROVEDArr = [];
    this.REJECTEDArr = [];
    this.ONGOINGArr = [];

    this.ONGOINGPage = 0;
    this.REJECTEDPage = 0;
    this.APPROVEDPage = 0;
    this.BACKLOGPage = 0;

    this.ONGOINGTotalPages = 0;
    this.REJECTEDTotalPages = 0;
    this.APPROVEDTotalPages = 0;
    this.BACKLOGTotalPages = 0;
  }

  getRandomColor(idx) {
    var col0 = '#0d856b';
    var col1 = '#d66f1b';
    var col2 = '#9f52e7';
    var col3 = '#e9318d';
    var col4 = '#1175a3';
    var col5 = '#e93131';
    var col6 = '#2316d3';
    var col7 = '#f557f5';
    var col8 = '#d6c31b';
    var col9 = '#40d61b';

    if ((idx % 10) == 0) return col0;
    if ((idx % 10) == 1) return col1;
    if ((idx % 10) == 2) return col2;
    if ((idx % 10) == 3) return col3;
    if ((idx % 10) == 4) return col4;
    if ((idx % 10) == 5) return col5;
    if ((idx % 10) == 6) return col6;
    if ((idx % 10) == 7) return col7;
    if ((idx % 10) == 8) return col8;
    if ((idx % 10) == 9) return col9;
    return '#d86315';

  }

}

